<style scoped>
  .ll-p-m-w {
    display: flex;
    flex-direction: row;
    flex-wrap: nowrap;
  }
  .ll-img-w {
    width: 60px;
    height: 60px;
  }
  .ll-img-w>img {
    width: 60px;
  }
  .ll-p-m-c-r {
    padding: 0 10px;
  }
  .ll-erp-p-no {
    color: #666;
    font-size: 12px;
  }
  .c-g-sfw {
    padding-top: 0 !important;
    padding-bottom: 0 !important;
  }
</style>

<template>
  <div>
    <el-dialog 
      :close-on-click-modal = "false"
      :close-on-press-escape = "false"
      :title="title" 
      :visible.sync="isShow" 
      width='720px'>
      <div class="search-form-wraper c-g-sfw">
        <el-form :model="searchContent" label-position="top" @submit.native.prevent>
          <el-form-item>
            <el-cascader
              placeholder=""
              class="search-input"
              :options="categoryList"
              change-on-select
              size="small"
              clearable
              filterable
              @change="doSearchHandel(true)"
              :props="cateDefaultProps"
              v-model="searchContent.categoryIdList">
            </el-cascader>
          </el-form-item>
          <el-form-item>
            <el-input class="search-input" v-model.trim="searchContent.searchValue" placeholder="SKU/" size="small" @keyup.enter.native="doSearchHandel(true)" clearable></el-input>
          </el-form-item>
          <el-form-item>
            <el-button type="primary" @click="doSearchHandel(true)" size="mini"></el-button>
          </el-form-item>
        </el-form>
      </div>
      
      <div>
        <el-table
          class="fixed-height"
          ref="multipleTable"
          v-loading="loading"
          :data="tableData"
          empty-text=""
          style="width:100%;"
          height="350"
          @select="handleSelect"
          @select-all="handleSelectAll"
          size="small">
          <el-table-column
            type="selection"
            align="center"
            :selectable = "selectableHandel">
          </el-table-column>
          <el-table-column min-width="226px" label="">
            <template slot-scope="scope">
              <div class="ll-p-m-w">
                <div class="ll-img-w">
                  <img v-if="scope.row.imgUrl && scope.row.imgUrl != ''" :src="scope.row.imgUrl" alt="" onerror="onerror=null;src='/shop/static/img_error.png'">
                  <img v-else src="/shop/static/img_error.png" alt="">
                </div>
                <div class="ll-p-m-c-r">
                  <div>{{scope.row.goodsName || '--'}}</div>
                  <div class="ll-erp-p-no">SKU{{scope.row.goodsNo || '--'}}</div>
                  <div class="ll-erp-p-no">ID{{scope.row.platformCityGoodsId || '--'}}</div>
                </div>
              </div>
            </template> 
          </el-table-column>
          <el-table-column min-width="120px" prop="spec" label=""></el-table-column>
          <el-table-column min-width="120px" prop="categoryName" label=""></el-table-column>
        </el-table>
        <!--  -->
        <el-pagination
          v-show="tableData.length>0"
          class="tc mt20"
          background
          @size-change="changeSize"
          @current-change="changeCurrent"
          :current-page.sync="pageNum"
          :page-sizes="pageSizes"
          layout="total, sizes, prev, pager, next, jumper"
          :pager-count="5"
          :total="total">
        </el-pagination>
      </div>
      <div slot="footer" class="dialog-footer">
        <el-button @click="isShow = false" size="mini"> </el-button>
        <el-button type="primary" @click="confirmClose" size="mini" :disabled="isDisabled || checkedList.length === 0"> </el-button>
      </div>
    </el-dialog>
  </div>
</template>

<script>
  import GoodsSelect from "@/components/Common/GoodsSelectByGid"
  import config from "@/utils/config"
  const requestUrl = {
    // 
    getCategoryTree: '/plateform/a/category/getCategoryTree',
    // 
    selectCityGoods: '/plateform/a/baseCommon/city/selectPreSaleCityGoodsList'
  }
  export default {
    data() {
      return {
        pageSizes: config.pageSizes10,
        isDisabled: false,
        isShow: false,
        title: '', // title
        loading: false,
        pageNum: 1,
        pageSize: 10,
        total: 0,
        searchContent: {
          saleType: 4,
          vendorType: 2,
          queryString: '', // 
          goodsIds: [], // ID 
          categoryIdList: [],
          searchValue: '', // sku
          categoryId: '' // ID
        },
        tableData: [],
        // 
        checkedList: [],
        // 
        cateDefaultProps: {
          children: 'categoryVoList',
          label: 'categoryName',
          value: 'categoryId'
        },
        regionCode: '',
        categoryList: [] // id

      }
    },
    components: {
      "goods-select": GoodsSelect
    },
    methods: {
      /**
       * [setDefault ]
       * @param {[type]} _val [description]
       */
      setDefault(val) {
        if(val) {
          this.title = val.title
          this.regionCode = val.regionCode
          this.searchContent.vendorType = val.vendorType || 2
          this.checkedList = val.checkedList && val.checkedList.length ? JSON.parse(JSON.stringify(val.checkedList)) : []
        }
        this.isShow = true
        // 
        this.getCategoryId()
        this.doSearchHandel(true)
      },
      /**
       * [getCategoryId ]
       * @return {[type]} [description]
       */
      getCategoryId() {
        this.$http.post(requestUrl.getCategoryTree, {DIFFCITY: true}).then(res => {
          if(res.code === 0) {
            if(res.data && res.data.length) {
              for(let item of res.data) {
                if(item.categoryVoList && item.categoryVoList.length === 0) {
                  item.categoryVoList = null
                }
              }
              this.categoryList = res.data
            }else {
              this.categoryList = []
            }
            return 
          }
          throw new Error(res.msg)
        }).catch(e => {
          console.log("():", e.message)
        })
      },
      /**
       * [changeSize description]
       * @param    {[type]}                 value [description]
       * @return   {[type]}                       [description]
       */
      changeSize (value) {
        this.pageSize = value
        this.doSearchHandel()
      },
      /**
       * [changeCurrent ]
       * @param    {[type]}                 value []
       * @return   {[type]}                       [description]
       */
      changeCurrent (value) {
        this.pageNum = value
        this.doSearchHandel()
      },
      /**
       * [doSearchHandel ]
       * @param  {[type]} _param [description]
       * @return {[type]}         [description]
       */
      doSearchHandel(_param) {
        let spliceLen = this.searchContent.categoryIdList.length - 1
        this.searchContent.categoryId = this.searchContent.categoryIdList[spliceLen]
        this.loading = true
        if(_param === true) {
          this.pageNum = 1
        }
        this.$http.post(requestUrl.selectCityGoods, { ...this.searchContent, pageNum: this.pageNum, pageSize: this.pageSize, regionCode: this.regionCode, DIFFCITY: true }).then(res => {
          this.loading = false
          if(res.code === 0) {
            // 
            if(res.data && res.data.result && res.data.result.length) {
              for(let item of res.data.result) {
                item.goodsId = item.goodsId
                item.goodsName = item.platformCityGoodsName || ''
                item.activityGoodsName = item.platformCityGoodsName
                item.price = item.price || ''
                item.preSalePrice = item.preSalePrice || ''
                item.stock = item.stock || ''
                item.platformCurStock = item.platformCurStock || ''
                item.limitCount = item.limitCount || ''
                item.sortNum = item.sortNum || ''
                item.preSaleImageUrl = item.bigImageUrl || ''
                item.imgUrl = item.platformImageUrl
                item.sfInStock = item.sfInStock || true
              }
              this.tableData = res.data.result
            }else {
              this.tableData = []
            }
            this.total = res.data.total
            this.defaultSelectFun()
            return 
          }
          throw new Error(res.msg)
        }).catch(e => {
          this.loading = false
          this.$notify.error({
            title: '',
            message: e.message
          })
        })
      },
      /**
       * [handleSelect ]
       * @return {[type]} [description]
       */
      handleSelect(checkedRows, row) {
        if(checkedRows.includes(row)) {
          this.checkedList.push(row)
        }else {
          const index = this.checkedList.findIndex(item => {
            return item.storeNo === row.storeNo
          })
          this.checkedList.splice(index, 1)
        }
      },
      /**
       * [handleSelectAll ]
       * @return {[type]} [description]
       */
      handleSelectAll(arr) {
        if(arr.length) {
          // 
          this.tableData.forEach(i => {
            const index = this.checkedList.findIndex(j => {
              return j.platformCityGoodsId === i.platformCityGoodsId
            })
            if(index === -1) {
              this.checkedList.push(i)
            }
          })
        }else {
          // 
          this.tableData.forEach(i => {
            const index = this.checkedList.findIndex(j => {
              return j.platformCityGoodsId === i.platformCityGoodsId
            })
            if(index !== -1) {
              this.checkedList.splice(index, 1)
            }
          })
        }
      },
      /**
       * [defaultSelectFun ]
       * @param    {[type]}                 row [description]
       * @return   {[type]}                     [description]
       */
      defaultSelectFun() {
        let _this = this
        let rows = []
        for(let i of this.tableData) {
          for(let j of this.checkedList) {
            if(i.platformCityGoodsId === j.platformCityGoodsId) {
              i.goodsId = j.goodsId
              i.goodsNo = j.goodsNo
              i.goodsName = j.goodsName
              i.preSalePrice = j.preSalePrice
              i.price = j.price
              i.stock = j.stock
              i.limitCount = j.limitCount
              i.sortNum = j.sortNum
              i.platformCurStock = j.platformCurStock
              i.preSaleImageUrl = j.preSaleImageUrl ? j.preSaleImageUrl : j.bigImageUrl
              i.disabled = j.disabled
              i.sfInStock = j.sfInStock
              rows.push(i)
            }
          }
        }
        if (rows.length > 0) {
          this.$refs.multipleTable.clearSelection()
          setTimeout(() => {
            rows.forEach(rowItem => {
              _this.$refs.multipleTable.toggleRowSelection(rowItem)
            });
          }, 100)
        } else {
          setTimeout(() => {
            this.$refs.multipleTable.clearSelection()
          }, 100)
        }
      },
      /**
       * [confirmClose ]
       * @return {[type]} [description]
       */
      confirmClose() {
        let _params = {
          checkedList: this.checkedList
        }
        this.$emit("confirmChooseGoods", _params)
        this.isShow = false
        this.isDisabled = false
      },
      selectableHandel(row, index) {
        return !row.disabled
      },
      closeDialog() {
        this.isShow = false
      },
      closeDiasbled() {
        this.isDisabled = false
      }
    }
  }
</script>


