<style>
  .mh-address {
    color: #666;
    font-size: 12px;
  }
  .pic-wrap {
    width: 100%;
    text-align: center;
    padding-top: 10px;
    min-height: 250px;
  }
</style>
<template>
  <div>
    <div class="batch-handel-w">
      <el-button class="sup-mini" size="mini" icon="el-icon-time" @click="batchOpenHandel" :disabled="checkedList.length === 0"></el-button>
      <el-button class="sup-mini" size="mini" icon="el-icon-bell" @click="batchCloseHandel" :disabled="checkedList.length === 0"></el-button>
      <el-button class="sup-mini" size="mini" icon="el-icon-check" @click="batchEableHandel" :disabled="checkedList.length === 0"></el-button>
      <el-button class="sup-mini" size="mini" icon="el-icon-close" @click="batchDisableHandel" :disabled="checkedList.length === 0"></el-button>
      <el-button class="sup-mini" size="mini" icon="el-icon-sort" @click="batchEditCheck" :disabled="checkedList.length === 0" v-if="platformCode===5 || platformCode===6"></el-button>
      <el-popover
        style="float:right;"
        placement="left"
        title=""
        width="300"
        trigger="hover">
        <div v-if="platformCode===5||platformCode===6">
          <p>1;</p>
          <p>2;</p>
          <p>3</p>
        </div>
        <div v-else>ID</div>
        <el-button icon="el-icon-question" circle size="mini" slot="reference"></el-button>
      </el-popover>
    </div>
    <el-table
      :key="platformCode"
      :data="tableData"
      empty-text=""
      style="width:100%;"
      @select="handleSelect"
      @select-all="handleSelectAll"
      size="small">
      <el-table-column
        type="selection"
        align="center">
      </el-table-column>
      <el-table-column label="" min-width="200px">
        <template slot-scope="scope">
          <div>
            <p>
              {{scope.row.storeNo}}-{{scope.row.storeName}}
              <span v-if="scope.row.storeStatus === 1" style="color:#67c23a;">()</span>
              <span v-else-if="scope.row.storeStatus === 2" style="color:#e6a23c;">()</span>
            </p>
            <p class="mh-address">{{scope.row.address}}</p>
          </div>
        </template>  
      </el-table-column>
      <el-table-column prop="phone" label="" min-width="130px"></el-table-column>
      <el-table-column prop="saleType" label="" min-width="80px" :formatter="formatter"></el-table-column>
      <el-table-column label="" min-width="120px">
        <template slot-scope="scope">
          <div>
            {{scope.row.openTime || '-'}}-{{scope.row.closeTime || '-'}}
          </div>
        </template>  
      </el-table-column>
      <el-table-column label="" min-width="120px" v-if="platformCode === 5">
        <template slot-scope="scope">
          <div>
            {{scope.row.pickupStartTime || '-'}}-{{scope.row.pickupEndTime || '-'}}
          </div>
        </template>  
      </el-table-column>
      <el-table-column label="" min-width="120px" prop="pickTypeStr" v-if="platformCode === 5 || platformCode === 6"></el-table-column>
      <el-table-column prop="shopId" label="ID" min-width="120px"  v-if="platformCode != 5 && platformCode != 6" :formatter="shopIdFormater"></el-table-column>
      
      <el-table-column label="" min-width="80px">
        <template slot-scope="scope">
          <div>
            <span style="color:#888888;" v-show="scope.row.openStatus == '0'"></span>
            <span style="color:#67c23a;" v-show="scope.row.openStatus == '1'"></span>
            <span style="color:#e6a23c;" v-show="scope.row.openStatus == '2'"></span>
          </div>
        </template>  
      </el-table-column>

      <el-table-column label="" min-width="80px">
        <template slot-scope="scope">
          <div>
            <span style="color:#888888;" v-if="scope.row.openOnline == '0'"></span>
            <span style="color:#67c23a;" v-else-if="scope.row.openOnline == '1'"></span>
            <span v-else>--</span>
          </div>
        </template>  
      </el-table-column>
      <el-table-column
        label=""
        :width="platformCode === 5 ? 90 : 90"
        fixed="right"
        size="small">
        <template slot-scope="scope">
          <div v-if="platformCode === 5">
            <p>
              <el-button type="text" size="mini" @click="unPlatform(scope.row)" icon="el-icon-edit"></el-button>
            </p>
            <p>
              <el-button type="text" size="mini" @click="showScanCodeDialog(scope.row)" icon="el-icon-picture"></el-button>
            </p>
          </div>
          <div v-else>
            <el-button size="mini" @click="unPlatform(scope.row)" icon="el-icon-edit"></el-button>
          </div>
        </template>
      </el-table-column>
    </el-table>

    <!--  -->
    <el-dialog
      title=""
      :visible.sync="dialogVisible"
      width="400px">
      <span></span>
      <span slot="footer" class="dialog-footer">
        <el-button @click="dialogVisible = false" size="mini"> </el-button>
        <el-button type="primary" @click="sureCategorical" size="mini"> </el-button>
      </span>
    </el-dialog>
    <!--  -->
    <el-dialog
      title=""
      :visible.sync="pickupTimeDialog"
      width="400px">
      <span style="color: red;">*</span><span></span>
      <el-time-picker
        class="search-date-picker"
        size="small"
        is-range
        v-model="pickupTimeRange"
        value-format="HH:mm"
        format="HH:mm"
        range-separator="-"
        start-placeholder=""
        end-placeholder=""
        placeholder="">
      </el-time-picker>
      <p style="padding-left: 96px; padding-top: 6px;"></p>
      <span slot="footer" class="dialog-footer">
        <el-button @click="pickupTimeDialog = false" size="mini"> </el-button>
        <el-button type="primary" @click="sureChoosePickupTime" size="mini"> </el-button>
      </span>
    </el-dialog>
    <!--  -->
    <el-dialog
      :title="storeNameStr"
      :visible.sync="scanCodeDialog"
      :close-on-click-modal="false"
      :close-on-press-escape ="false"
      width="400px">
      <span>{{storeAddress}}</span>
      <div class="pic-wrap" v-loading="loading">
        <img width="240" :src="scanCodeUrl" alt="">
      </div>
      <span slot="footer" class="dialog-footer">
        <el-button @click="scanCodeDialog = false" size="mini"></el-button>
        <el-button type="primary" @click="downloadImage(scanCodeUrl)" :disabled="!scanCodeUrl" size="mini"></el-button>
      </span>
    </el-dialog>
  </div>
</template>
<script>
import * as utils from '@/utils/utils'
const requestUrl = {
  generateQRCode: '/member/a/qrScanCode/generateQRCode'
}
export default {
  data() {
    return {
      pageIndex: 1,
      pageSize: 10,
      platformCode: '',
      tableData: [],
      dialogVisible: false,
      store: {},
      // 
      checkedList: [],
      // 
      pickupTimeDialog: false,
      pickupTimeRange: ['10:00', '20:00'],
      scanCodeDialog: false,
      storeNameStr: '',
      storeAddress: '',
      loading: false,
      scanCodeUrl: '' 
    }
  },
  methods: {
    /**
     * [setDefaultMesg ]
     * @param {[type]} _val [description]
     */
    setDefaultMesg(_val) {
      this.platformCode = _val.platformCode
      this.tableData = _val.tableData

      this.checkedList = []
    },
    formatter(row, column, cellValue, index) {
      if(cellValue == 1) {
        return ''
      }else if(cellValue == 2) {
        return ''
      }else if(cellValue == 3) {
        return ''
      }else if(cellValue == 4) {
        return ''
      }else {
        return cellValue
      }
    },
    /**
     * [unPlatform ]
     * @return {[type]} [description]
     */
    unPlatform(_val) {
      // if(_val.platformId === 5) {
      //   this.$emit('openOnlineJob', _val)
      // }else {
      //   this.$emit('openOnlineJobThirdP', _val)
      // }
      this.$emit('openOnlineJobThirdP', _val)
    },
    /**
     * [unCategorical ]
     * @return {[type]} [description]
     */
    unCategorical(_val) {
      this.dialogVisible = true
      this.store = {
        storeNo: _val.storeNo,
        storeId: _val.storeId
      }
    },
    /**
     * [sureCategorical ]
     * @return {[type]} [description]
     */
    sureCategorical(){
      this.dialogVisible = false
      this.$emit('sureCategorical', this.store)
    },
    /**
     * [shopIdFormater ]
     * @return {[type]} [description]
     */
    shopIdFormater(row, column, cellValue, index) {
      if(cellValue == '') {
        return '--'
      }else {
        return cellValue
      }
    },
    /**
     * [handleSelect ]
     * @return {[type]} [description]
     */
    handleSelect(val, row) {
      let hash = {}
      let checkedListTemp = []

      if(utils.isKeyInArray(row.uniqueId, this.checkedList)) {
        this.checkedList = utils.removeItemByKeyVal('uniqueId', row.uniqueId, this.checkedList)
        return
      }else {
        checkedListTemp.push(row)
      }

      let checkedListTempArr = [...checkedListTemp, ...this.checkedList]
      this.checkedList = checkedListTempArr.reduceRight((item, next) => {
        hash[next.uniqueId] ? '' : hash[next.uniqueId] = true && item.push(next)
        return item
      }, [])
    },
    /**
     * [handleSelectAll ]
     * @return {[type]} [description]
     */
    handleSelectAll(val) {
      let hash = {}
      if(val.length > 0) {        
        // 
        for(let item of val) {
          this.checkedList.push(item)
        }
        // 
        this.checkedList = this.checkedList.reduceRight((item, next) => {
          hash[next.uniqueId] ? '' : hash[next.uniqueId] = true && item.push(next)
          return item
        }, [])
      }else { 
        //  val.length<=0
        // 1.tableid
        let checkedListTemp = []
        for(let item of this.tableData) {
          checkedListTemp.push(item)
        }
        // 2.
        let checkedListArr = utils.getSubtract(this.checkedList, checkedListTemp, 'uniqueId')
        this.checkedList = checkedListArr && checkedListArr.length > 0 ? checkedListArr : [] 
      }
    },
    /**
     * [batchOpenHandel ]
     * @return {[type]} [description]
     */
    batchOpenHandel() {
      this.$confirm('', '', {
        confirmButtonText: '',
        cancelButtonText: '',
        type: 'warning'
      }).then(() => {
        let _value = {
          checkedList: this.checkedList
        }
        this.$emit('batchOpenHandel', _value)
      }).catch(() => {});
    },
    /**
     * [batchCloseHandel ]
     * @return {[type]} [description]
     */
    batchCloseHandel() {
      this.$confirm('', '', {
        confirmButtonText: '',
        cancelButtonText: '',
        type: 'warning'
      }).then(() => {
        let _value = {
          checkedList: this.checkedList
        }
        this.$emit('batchCloseHandel', _value)
      }).catch(() => {});
    },
    /**
     * [batchEableHandel ]
     * @return {[type]} [description]
     */
    batchEableHandel() {
      if(this.platformCode === 5) {
        this.pickupTimeDialog = true
      }else {
        this.$confirm('', '', {
          confirmButtonText: '',
          cancelButtonText: '',
          type: 'warning'
        }).then(() => {
          let _value = {
            checkedList: this.checkedList
          }
          this.$emit('batchEableHandel', _value)
        }).catch(() => {});
      }
    },
    /**
     * [batchDisableHandel ]
     * @return {[type]} [description]
     */
    batchDisableHandel() {
      this.$confirm('', '', {
        confirmButtonText: '',
        cancelButtonText: '',
        type: 'warning'
      }).then(() => {
        let _value = {
          checkedList: this.checkedList
        }
        this.$emit('batchDisableHandel', _value)
      }).catch(() => {});
    },
    /**
     * [batchEditCheck ]
     * @return {[type]} [description]
     */
    batchEditCheck() {
      let _value = {
        checkedList: this.checkedList
      }
      this.$emit('batchEditCheck', _value)
    },
    sureChoosePickupTime() {
      if(!this.pickupTimeRange || !this.pickupTimeRange.length) {
        this.$message("")
        return
      }
      this.pickupTimeDialog = false
      let _value = {
        checkedList: this.checkedList,
        pickupStartTime: this.pickupTimeRange[0] || '',
        pickupEndTime: this.pickupTimeRange[1] || ''
      }
      this.$emit('batchEableHandel', _value)
    },
    showScanCodeDialog(row) {
      this.scanCodeUrl = ""
      this.scanCodeDialog = true
      this.storeNameStr = `${row.storeNo || ''} ${row.storeName || ''}`
      this.storeAddress = `${row.address || ''}`
      let posterUrl = row.storeLeaderId ? `pages/shopping/newindex?storeNo=${row.storeNo}&storeLeaderId=${row.storeLeaderId}` : `pages/shopping/newindex?storeNo=${row.storeNo}`
      this.loading = true
      this.$http.post(requestUrl.generateQRCode, {posterUrl: posterUrl, width: 800}).then(res => {
        this.loading = false
        if(res.code === 0) {
          this.scanCodeUrl = res.data || ''
          return
        } 
        throw new Error(res.msg)
      }).catch(e => {
        this.loading = false
        this.$notify.error({
          title: '',
          message: e.message
        })
      })
    },
    downloadImage (imgsrc, name) { 
      // 
      let image = new Image();
      //  Canvas 
      image.setAttribute("crossOrigin", "anonymous")
      image.onload = function() {
        let canvas = document.createElement("canvas")
        canvas.width = image.width
        canvas.height = image.height
        let context = canvas.getContext("2d")
        context.drawImage(image, 0, 0, image.width, image.height)
        let url = canvas.toDataURL("image/png") //base64
        let a = document.createElement("a") // a
        let event = new MouseEvent("click") // 
        a.download = `${Date.parse(new Date())}${name || "photo"}` // 
        a.href = url // URLa.href
        a.dispatchEvent(event) // a
      }
      image.src = imgsrc
      setTimeout(() => {
        this.$message.success("")
        this.scanCodeDialog = false
      }, 400);
    }

  }
}
</script>



