<style>
  .c-g-sfw {
    padding-top: 0 !important;
    padding-bottom: 0 !important;
  }
  .switch-cp{
    margin: 0 10px 10px;
  }
</style>

<template>
  <div>
    <el-dialog :title="title" :visible.sync="isShow" width='760px'>
      <div class="switch-cp" v-if="!showData && noExclude && !chooseGoods">
        <el-switch
          v-model="chooseOfflineGoodsTOF"
          active-color="#409EFF"
          inactive-color="#F56C6C"
          active-text=""
          inactive-text="">
        </el-switch>
      </div>
      <div class="search-form-wraper c-g-sfw" v-if="!showData && !chooseGoods">
        <el-form :model="searchContent" label-position="top">
          <el-form-item>
            <el-cascader
              placeholder=""
              class="search-input"
              :options="categoryList"
              change-on-select
              size="small"
              clearable
              filterable
              @change="doSearchHandel(true)"
              :props="cateDefaultProps"
              v-model="searchContent.categoryNoList">
            </el-cascader>
          </el-form-item>
          <el-form-item>
            <el-input
              class="search-input"
              placeholder=""
              v-model="searchContent.goodsNo"
              @keyup.enter.native="doSearchHandel(true)"
              clearable
              size="small">
            </el-input>
          </el-form-item>
          <el-form-item>
            <el-input
              class="search-input"
              placeholder=""
              v-model="searchContent.goodsName"
              @keyup.enter.native="doSearchHandel(true)"
              clearable
              size="small">
            </el-input>
          </el-form-item>
          <el-form-item>
            <el-button type="primary" @click="doSearchHandel(true)" size="mini"></el-button>
          </el-form-item>
        </el-form>
      </div>
      
      <div>
        <div class="goods-table tab-wraper">
          <el-table
            class="fixed-height"
            ref="multipleTableTimely"
            v-loading="loading"
            :data="tableData"
            empty-text=""
            style="width:100%;"
            height="300"
            size="small"
            @select="handleSelect"
            @select-all="handleSelectAll">
            <el-table-column
              v-if="!showData"
              type="selection"
              width="50">
            </el-table-column>
            <el-table-column prop="name" label=""></el-table-column>
            <el-table-column prop="goodsNo" label=""></el-table-column>
            <el-table-column prop="categoryNoName" label=""></el-table-column>
            <el-table-column prop="saleUnitName" label=""></el-table-column>
          </el-table>
        </div>

        <div class="tc mt20" v-show="tableData.length > 0 && !showData && !chooseGoods">
          <el-pagination
            background
            @size-change="changeSize"
            @current-change="changeCurrent"
            :current-page.sync="pageNum"
            :page-sizes="[10, 50, 100, 150, 200]"
            layout="total, sizes, prev, pager, next, jumper"
            :pager-count="5"
            :total="total">
          </el-pagination>
        </div>
      </div>
      <div slot="footer" class="dialog-footer" v-if="!showData">
        <el-button @click="isShow = false" size="mini"> </el-button>
        <el-button type="primary" @click="doConfirmHandel" size="mini" :disabled="checkedList.length === 0"> </el-button>
      </div>
    </el-dialog>
  </div>
</template>

<script>
  import * as utils from '@/utils/utils'
  const reqUrl = {
    // 
    categoryList: '/foundation/category/list',
    // 
    productList: '/foundation/goods/info/query'
  }
  export default {
    data() {
      return {
        isShow: false,
        title: '', // title
        loading: false,
        chooseOfflineGoodsTOF: true,
        pageNum: 1,
        pageSize: 10,
        total: 0,
        searchContent: {
          goodsNo: '',
          goodsName: '',
          categoryNoList: [],
          categoryNo: '' // No
        },
        tableData: [],
        // 
        cateDefaultProps: {
          children: 'childrenList',
          label: 'categoryName',
          value: 'categoryNo'
        },
        categoryList: [], // No
        // loading
        queryGoodsAsyncLoading: false,
        // 
        queryGoodsSearchAsyncList: [],
        // 
        checkedList: [],
        showData: false,
        noExclude: true,
        chooseGoods: false,
        chooseGoodsIndex: 0
      }
    },
    methods: {
      /**
       * [setDefault ]
       * @param {[type]} _val [description]
       */
      setDefault(_val, _param, noExclude) {
        this.chooseGoods = false
        if(noExclude === 'noExclude') {
          this.noExclude = false
        }else{
          this.noExclude = true
        }
        if(_param === 'show') {
          this.title = _val.title
          this.showData = true
          this.tableData = _val.checkedList
          this.isShow = true
        }else{
          this.showData = false
          if(_val) {
            this.title = _val.title
            if(_val.checkedList && _val.checkedList.length) {
              this.checkedList = _val.checkedList
            }else {
              this.checkedList = []
            }
          }

          this.isShow = true
          // 
          this.getCategoryList()
          this.doSearchHandel(true)
        }
      },
      setDefaultChoose(_params, index) {
        this.chooseGoods = true
        this.chooseGoodsIndex = index
        this.showData = false
        if(_params) {
          this.title = _params.title
          if(_params.checkedElseList && _params.checkedElseList.length) {
            this.tableData = _params.checkedElseList
            this.checkedList = []
            let checkedList = _params.checkedList
            setTimeout(() => {
              this.$refs.multipleTableTimely.clearSelection()
              setTimeout(() => {
                if(checkedList.length) {
                  this.tableData.forEach(item => {
                    checkedList.forEach(item_ => {
                      if(item_ === item.goodsNo) {
                        this.$refs.multipleTableTimely.toggleRowSelection(item)
                        this.checkedList.push(item)
                      }
                    })
                  })
                }
              }, 100)
            }, 100)

          }else {
            this.tableData = []
          }
        }
        this.isShow = true
      },
      /**
       * [getCategoryList ]
       * @return {[type]} [description]
       */
      getCategoryList() {
        this.$http.get(reqUrl.categoryList).then(res => {
          if(res.code === 0) {
            if(res.data && res.data[0].childrenList && res.data[0].childrenList.length) {
              this.categoryList = res.data[0].childrenList
            }else {
              this.categoryList = []
            }
            return 
          }
          throw new Error(res.msg)
        }).catch(e => {
          console.log("():", e.message)
        })
      },
      /**
       * [changeSize description]
       * @param    {[type]}                 value [description]
       * @return   {[type]}                       [description]
       */
      changeSize (value) {
        this.pageSize = value
        this.doSearchHandel()
      },
      /**
       * [changeCurrent ]
       * @param    {[type]}                 value []
       * @return   {[type]}                       [description]
       */
      changeCurrent (value) {
        this.pageNum = value
        this.doSearchHandel()
      },
      /**
       * [doSearchHandel ]
       * @param  {[type]} _param [description]
       * @return {[type]}         [description]
       */
      doSearchHandel(_param) {
        let spliceLen = this.searchContent.categoryNoList.length - 1
        this.searchContent.categoryNo = this.searchContent.categoryNoList[spliceLen]
        this.loading = true
        if(_param === true) {
          this.pageNum = 1
        }
        this.$http.post(reqUrl.productList, { ...this.searchContent, pageNum: this.pageNum, pageSize: this.pageSize }).then(res => {
          this.loading = false
          if(res.code === 0) {
            // 
            if(res.data && res.data.result && res.data.result.length) {
              this.tableData = res.data.result            
            }else {
              this.tableData = []
            }
            if(this.pageNum == 1) {
              this.total = res.data.total
            }

            this.defauleSelectFun()
            return 
          }
          throw new Error(res.msg)
        }).catch(e => {
          this.loading = false
          this.$notify.error({
            title: '',
            message: e.message
          })
        })
      },
      /**
       * [handleSelect ]
       * @return {[type]} [description]
       */
      handleSelect(val, row) {
        let hash = {}
        let checkedListTemp = []

        if(utils.isKeyInArrayBykey(row.goodsNo, 'goodsNo', this.checkedList)) {
          this.checkedList = utils.removeItemByKeyVal('goodsNo', row.goodsNo, this.checkedList)
          return
        }else {
          checkedListTemp.push(row)
        }

        let checkedListTempArr = [...checkedListTemp, ...this.checkedList]
        this.checkedList = checkedListTempArr.reduceRight((item, next) => {
          hash[next.goodsNo] ? '' : hash[next.goodsNo] = true && item.push(next)
          return item
        }, [])
      },
      /**
       * [handleSelectAll ]
       * @return {[type]} [description]
       */
      handleSelectAll(val) {
        let hash = {}
        if(val.length > 0) {        
          // 
          for(let item of val) {
            this.checkedList.push(item)
          }
          // 
          this.checkedList = this.checkedList.reduceRight((item, next) => {
            hash[next.goodsNo] ? '' : hash[next.goodsNo] = true && item.push(next)
            return item
          }, [])
        }else { 
          //  val.length<=0
          // 1.tablecategoryNo
          let checkedListTemp = []
          for(let item of this.tableData) {
            checkedListTemp.push(item)
          }
          // 2.
          let checkedListArr = utils.getSubtract(this.checkedList, checkedListTemp, 'goodsNo')
          this.checkedList = checkedListArr && checkedListArr.length > 0 ? checkedListArr : [] 
        }
      },
      /**
       * [defauleSelectFun ]
       * @param    {[type]}                 row [description]
       * @return   {[type]}                     [description]
       */
      defauleSelectFun() {
        let _this = this
        let rows = []
        //   
        for(let i of this.tableData) {
          for(let j of this.checkedList) {
            if(i.goodsNo === j.goodsNo) {
              rows.push(i)
            }
          } 
        }
        if(rows.length > 0) {
          this.$refs.multipleTableTimely.clearSelection()
          setTimeout(function() {
            rows.forEach(rowItem => {
              _this.$refs.multipleTableTimely.toggleRowSelection(rowItem)
            });
          }, 100)
        } else {
          this.$refs.multipleTableTimely.clearSelection()
        }
      },
      /**
       * [doConfirmHandel ]
       * @return {[type]} [description]
       */
      doConfirmHandel() {
        if(this.chooseGoods) {
          let goodsNo = []
          let goodsName = []
          let saleUnitName = this.checkedList[0].saleUnitName
          let res
          this.checkedList.forEach(item => {
            goodsNo.push(item.goodsNo)
            goodsName.push(item.name)
            if(item.saleUnitName !== saleUnitName) {
              res = false
            }
          })
          if(res === false) {
            this.$message.warning("")
            return
          }
          let _params = {
            index: this.chooseGoodsIndex,
            goodsNo: goodsNo.join(';'),
            goodsName: goodsName.join(';'),
            saleUnitName: saleUnitName
          }
          this.$emit("chooseOfflineGoods", _params)
          this.isShow = false
        }else{
          let _params = {}
          if(this.noExclude === true) {
            _params = {
              chooseOfflineGoodsTOF: this.chooseOfflineGoodsTOF,
              checkedList: this.checkedList
            }
          }else{
            _params = {
              checkedList: this.checkedList
            }
          }
          this.$emit("confirmChooseOfflineGoods", _params)
          this.isShow = false
        }
      }
    }
  }
</script>




