<style scoped>
  .el-wrap {
    display: inline;
  }
</style>
<template>
  <div class="el-wrap">
    <el-form-item label="">
      <el-cascader
        ref="cascaderCategory"
        placeholder=""
        :class="className"
        :options="storeGroupTypeNoOptions"
        size="small"
        filterable
        clearable
        :props="defaultProps"
        @change="storeGroupTypeNoChange"
        v-model="storeGroupTypeNoList">
      </el-cascader>
    </el-form-item>
    <el-form-item label="">
      <el-select 
        :class="className" 
        v-model="storeNo" 
        filterable 
        placeholder="" 
        clearable 
        size="small" 
        @change="storeChange">
        <el-option
          v-for="item in storeOptions"
          :key="item.value"
          :label="item.label"
          :value="item.value">
        </el-option>
      </el-select>
    </el-form-item>
  </div>
</template>
<script>
  const requestUrl = {
    // 
    getStoreGroupType: '/backstage/storeGroup/storeGroupTypeList',
    // 
    search: '/plateform/a/store/condition/search'
  }
  export default {
    props: {
      className: {
        type: String,
        default: 'search-input'
      },
    },
    data() {
      return {
        storeGroupTypeNoList: [],
        storeNo: [],
        // 
        storeGroupTypeNoOptions: [],
        defaultProps: {
          children: 'storeGroupList',
          label: 'labelName',
          value: 'valueName'
        },
        // 
        storeOptions: [] 
      }
    },
    created() {
      // 
      this.getStoreGroupType()
      // 
      this.getStoreList()
    },
    methods: {
      /**
       * @description: 
       * @param {type} 
       * @return: 
       */
       getStoreGroupType() {
        this.$http.get(requestUrl.getStoreGroupType).then(res => {
          if(res.code === 0) {
            let dataList = res.data && res.data.length ? res.data: []
            this.modifyStoreGroupeTree(dataList)
            this.storeGroupTypeNoOptions = dataList
            return 
          }
          throw new Error(res.msg)
        }).catch(e => {
          console.log("" + e.message)
        })
      },
      modifyStoreGroupeTree(storeTree, typeNo) {
        storeTree.forEach(v => {
          if(v.storeGroupList && v.storeGroupTypeNo) {
            // 
            v.valueName = v.storeGroupTypeNo
            v.labelName = v.storeGroupTypeName ? v.storeGroupTypeNo + '.' + v.storeGroupTypeName : ''
            if(v.storeGroupList.length) {
              this.modifyStoreGroupeTree(v.storeGroupList, v.storeGroupTypeNo)
            }
          }else if(v.storeGroupNo) {
            // 
            v.valueName = v.storeGroupNo
            v.labelName = v.storeGroupName ? v.storeGroupNo + '.' + v.storeGroupName : ''
            if(v.childrenList && v.childrenList.length) {
              v.storeGroupList = JSON.parse(JSON.stringify(v.childrenList))
              this.modifyStoreGroupeTree(v.storeGroupList)
            }
          }
        })
      },
      /**
       * [getStoreList ]
       * @return {[type]} [description]
       */
      getStoreList(typeNo, groupNo) {
        this.$http.post(requestUrl.search, {storeGroupTypeNo: typeNo || null, storeGroupNo: groupNo || null}).then(res => {
          if (res.code === 0) {
            let optionsArr = res.data && res.data.length ? res.data : []
            this.storeOptions = optionsArr.map(item => {
              return {
                value: item.storeNo,
                label: item.storeNo + '-' + item.storeName
              }
            })
            return
          }
          throw new Error(res.msg)
        }).catch(e => {
          console.log("():", e.message)
        })
      },
      /**
       * @description: 
       * @param {type} 
       * @return: 
       */
      storeGroupTypeNoChange(val) {
        this.storeNo = ''
        this.getStoreList(val[0], val[1])
        let params = {
          storeNo: this.storeNo,
          storeGroupTypeNoList: this.storeGroupTypeNoList
        }
        this.$emit("StoreGroupChange", params)
      },
      /**
       * @description: 
       * @param {type} 
       * @return: 
       */
      storeChange(val) {
        let params = {
          storeNo: this.storeNo,
          storeGroupTypeNoList: this.storeGroupTypeNoList
        }
        this.$emit("StoreGroupChange", params)
      }
    }
  }
</script>


