<style>
.prom-type .el-tabs__nav{
  width: 100%;
}
.prom-type .el-tabs__item{
  width: 50%;
  text-align: center;
}
</style>
<template>
  <div>
    <el-form-item label="" required>
      <el-tabs v-model="promType" class="add-input prom-type" @tab-click="promTypeChange">
        <el-tab-pane label="" name="2"></el-tab-pane>
        <el-tab-pane label="" name="5"></el-tab-pane>
      </el-tabs>
    </el-form-item>
    <!--  -->
    <div v-if="promType === '2'">
      <el-form-item label="" required>
        <el-select size="small" class="add-input" v-model.trim="discountCoupon.ruleSource" placeholder=""  @change="promTypeChange">
          <el-option :label="item.label" :value="item.value" :key="item.value" v-for="item in ruleSourceList"></el-option>
        </el-select>
      </el-form-item>
      <!--  -->
      <online-template v-if="discountCoupon.ruleSource === 0 || discountCoupon.ruleSource === 2" ref="templateOnline" @changeOnline="changeOnline"></online-template>
      <!--  -->
      <offline-template v-if="discountCoupon.ruleSource === 0 || discountCoupon.ruleSource === 1" ref="templateOffline" @changeOffline="changeOffline"></offline-template>
      <!--  -->
      <dis-coupon-list ref="disCouponList" :triggerType="triggerType" @changeDisCoupon="changeDisCoupon"></dis-coupon-list>
    </div>
    <!--  -->
    <div v-if="promType === '5'">
      <el-form-item label="" required>
        <el-select size="small" class="add-input" v-model.trim="goodCoupon.ruleSource" placeholder="" @change="changeRuleSource">
          <el-option :label="item.label" :value="item.value" :key="item.value" v-for="item in ruleSourceList"></el-option>
        </el-select>
      </el-form-item>
      <!--  -->
      <online-template v-if="goodCoupon.ruleSource === 0 || goodCoupon.ruleSource === 2" ref="templateOnline" @changeOnline="changeOnline"></online-template>
      <!--  -->
      <offline-template v-if="goodCoupon.ruleSource === 0 || goodCoupon.ruleSource === 1" ref="templateOffline" @changeOffline="changeOffline"></offline-template>
      <!--  -->
      <dis-coupon-list ref="disCouponList" :triggerType="triggerType" @changeDisCoupon="changeDisCoupon"></dis-coupon-list>
    </div>
  </div>
</template>
<script>
  import * as utils from '@/utils/utils'
  import TemplateOnline from './TemplateOnline'
  import TemplateOffline from './TemplateOffline'
  import DisCouponList from './DisCouponList'

  export default {
    props: ['triggerType'],
    data() {
      return {
        promType: '2',
        discountCoupon: { // 
          promType: '2',
          ruleSource: 0
        },
        goodCoupon: { // 
          promType: '5',
          ruleSource: 0
        },
        disPromDiscounts: [],
        goodPromDiscounts: [],
        // 
        ruleSourceList: [
          {
            value: 0, 
            label: '+'
          },
          {
            value: 2, 
            label: ''
          },
          {
            value: 1, 
            label: ''
          }
        ]
      }
    },
    created() {

    },
    mounted() {
    },
    components: {
      "online-template": TemplateOnline,
      "offline-template": TemplateOffline,
      "dis-coupon-list": DisCouponList
    },
    watch: {
      discountCoupon: {
        handler(newVal) {
          this.changePromType()
        },
        deep: true,
        immediate: true
      },
      goodCoupon: {
        handler(newVal) {
          this.changePromType()
        },
        deep: true,
        immediate: true
      },
      disPromDiscounts: {
        handler(newVal) {
          this.changePromType()
        },
        deep: true,
        immediate: true
      },
      goodPromDiscounts: {
        handler(newVal) {
          this.changePromType()
        },
        deep: true,
        immediate: true
      }
    },
    methods: {
      /**
       * [promTypeChange ]
       * @return {[type]} [description]
       */
      promTypeChange() {
        // , 
        if(this.promType === '2') {
          let ruleSource = this.discountCoupon.ruleSource
          if(ruleSource === 0 || ruleSource === 2) {
            setTimeout(() => {
              this.$refs.templateOnline.promTypeChange(this.promType, this.discountCoupon)
            },100)
          }
          if(ruleSource === 0 || ruleSource === 1) {
            setTimeout(() => {
              this.$refs.templateOffline.promTypeChange(this.promType, this.discountCoupon)
            },100)
          }
          this.$refs.disCouponList.promTypeChange(this.promType)
        }else if(this.promType === '5') {
          let ruleSource = this.goodCoupon.ruleSource
          if(ruleSource === 0 || ruleSource === 2) {
            setTimeout(() => {
              this.$refs.templateOnline.promTypeChange(this.promType, this.goodCoupon)
            },100)
          }
          if(ruleSource === 0 || ruleSource === 1) {
            setTimeout(() => {
              this.$refs.templateOffline.promTypeChange(this.promType, this.goodCoupon)
            },100)
          }
          this.$refs.disCouponList.promTypeChange(this.promType)
        }
      },
      changeRuleSource() {
        this.$refs.disCouponList.changeRuleSource(this.goodCoupon.ruleSource)
        this.promTypeChange()
      },
      changeOnline(val) {
        if(this.promType === '2') {
          this.discountCoupon = {...this.discountCoupon, ...val}
        }else if(this.promType === '5') {
          this.goodCoupon = {...this.goodCoupon, ...val}
          this.$refs.disCouponList.goodsChange(this.goodCoupon)
        }
      },
      changeOffline(val) {
        if(this.promType === '2') {
          this.discountCoupon = {...this.discountCoupon, ...val}
        }else if(this.promType === '5') {
          this.goodCoupon = {...this.goodCoupon, ...val}
          this.$refs.disCouponList.goodsChange(this.goodCoupon)
        }
      },
      // 
      changeDisCoupon(val) {
        if(this.promType === '2' && val[0] && val[0].meetAmount != undefined) {
          this.disPromDiscounts = JSON.parse(JSON.stringify(val))
        }else if(this.promType === '5') {
          this.goodPromDiscounts = JSON.parse(JSON.stringify(val))
        }
      },
      changePromType() {
        let promDiscounts = [this.formattDisCoupon(), this.formattGoodCoupon()]
        this.$emit("changePromType", promDiscounts)
      },
      formattDisCoupon() {
        // 
        let obj = this.discountCoupon
        let promReqsDis = {}
        // 
        if(obj.chooseOnlineType === 1) {
          // 
          let goodsNo = []
          obj.onlineGoodsCheckedList.forEach(item => {
            goodsNo.push(item.goodsId)
          })
          delete promReqsDis.onlineCategoryNo
          delete promReqsDis.onlineExcludeCategoryNo
          if(obj.chooseOnlineGoodsTOF === true) {
            promReqsDis.onlineGoodsNo = goodsNo.join(';')
            delete promReqsDis.onlineExcludeGoodsNo
          }else if(obj.chooseOnlineGoodsTOF === false) {
            promReqsDis.onlineExcludeGoodsNo = goodsNo.join(';')
            delete promReqsDis.onlineGoodsNo
          }
        }else if(obj.chooseOnlineType === 2) {
          // 
          let categoryNo = []
          obj.onlineCategoryCheckedList.forEach(item => {
            categoryNo.push(item.categoryNo)
          })
          delete promReqsDis.onlineGoodsNo
          delete promReqsDis.onlineExcludeGoodsNo
          if(obj.chooseNCateTOF === true) {
            promReqsDis.onlineCategoryNo = categoryNo.join(';')
            delete promReqsDis.onlineExcludeCategoryNo
          }else if(obj.chooseNCateTOF === false) {
            promReqsDis.onlineExcludeCategoryNo = categoryNo.join(';')
            delete promReqsDis.onlineCategoryNo
          }
        }else if(obj.chooseOnlineType === 3) {
          // 
          let goodsNo = []
          obj.uploadNGoodsNo.forEach(item => {
            goodsNo.push(item.goodsId)
          })
          delete promReqsDis.onlineCategoryNo
          delete promReqsDis.onlineExcludeCategoryNo
          promReqsDis.onlineGoodsNo = goodsNo.join(';')
          delete promReqsDis.onlineExcludeGoodsNo
        }
        if(obj.onlineActivityTypeList) {
          promReqsDis.onlineActivityType = obj.onlineActivityTypeList.join(';')
        }

        // 
        if(obj.chooseOfflineType === 1) {
          // 
          let goodsNo = []
          obj.offlineGoodsCheckedList.forEach(item => {
            goodsNo.push(item.goodsNo)
          })
          delete promReqsDis.categoryNo
          delete promReqsDis.excludeCategoryNo
          if(obj.chooseOfflineGoodsTOF === true) {
            promReqsDis.goodsNo = goodsNo.join(';')
            delete promReqsDis.excludeGoodsNo
          }else if(obj.chooseOfflineGoodsTOF === false) {
            promReqsDis.excludeGoodsNo = goodsNo.join(';')
            delete promReqsDis.goodsNo
          }
        }else if(obj.chooseOfflineType === 2) {
          // 
          let categoryNo = []
          obj.offlineCategoryCheckedList.forEach(item => {
            categoryNo.push(item.categoryNo)
          })
          delete promReqsDis.goodsNo
          delete promReqsDis.excludeGoodsNo
          if(obj.chooseFCateTOF === true) {
            promReqsDis.categoryNo = categoryNo.join(';')
            delete promReqsDis.excludeCategoryNo
          }else if(obj.chooseFCateTOF === false) {
            promReqsDis.excludeCategoryNo = categoryNo.join(';')
            delete promReqsDis.categoryNo
          }
        }else if(obj.chooseOfflineType === 3) {
          // 
          let goodsNo = []
          obj.uploadFGoodsNo.forEach(item => {
            goodsNo.push(item.goodsNo)
          })
          delete promReqsDis.categoryNo
          delete promReqsDis.excludeCategoryNo
          promReqsDis.goodsNo = goodsNo.join(';')
          delete promReqsDis.excludeGoodsNo
        }
        if(obj.activityTypeList) {
          promReqsDis.activityType = obj.activityTypeList.join(';')
        }

        promReqsDis.promType = 2
        promReqsDis.ruleSource = obj.ruleSource
        promReqsDis.promDiscounts = this.disPromDiscounts
        return promReqsDis
      },
      formattGoodCoupon() {
        // 
        let obj = this.goodCoupon
        let promReqsGood = {}
        // 
        if(obj.chooseOnlineType === 1) {
          // 
          let goodsNo = []
          obj.onlineGoodsCheckedList.forEach(item => {
            goodsNo.push(item.goodsId)
          })
          delete promReqsGood.onlineCategoryNo
          delete promReqsGood.onlineExcludeCategoryNo
          if(obj.chooseOnlineGoodsTOF === true) {
            promReqsGood.onlineGoodsNo = goodsNo.join(';')
            delete promReqsGood.onlineExcludeGoodsNo
          }else if(obj.chooseOnlineGoodsTOF === false) {
            promReqsGood.onlineExcludeGoodsNo = goodsNo.join(';')
            delete promReqsGood.onlineGoodsNo
          }
        }else if(obj.chooseOnlineType === 3) {
          // 
          let goodsNo = []
          obj.uploadNGoodsNo.forEach(item => {
            goodsNo.push(item.goodsId)
          })
          delete promReqsGood.onlineCategoryNo
          delete promReqsGood.onlineExcludeCategoryNo
          promReqsGood.onlineGoodsNo = goodsNo.join(';')
          delete promReqsGood.onlineExcludeGoodsNo
        }
        if(obj.onlineActivityTypeList) {
          promReqsGood.onlineActivityType = obj.onlineActivityTypeList.join(';')
        }

        // 
        if(obj.chooseOfflineType === 1) {
          // 
          let goodsNo = []
          obj.offlineGoodsCheckedList.forEach(item => {
            goodsNo.push(item.goodsNo)
          })
          delete promReqsGood.categoryNo
          delete promReqsGood.excludeCategoryNo
          if(obj.chooseOfflineGoodsTOF === true) {
            promReqsGood.goodsNo = goodsNo.join(';')
            delete promReqsGood.excludeGoodsNo
          }else if(obj.chooseOfflineGoodsTOF === false) {
            promReqsGood.excludeGoodsNo = goodsNo.join(';')
            delete promReqsGood.goodsNo
          }
        }else if(obj.chooseOfflineType === 3) {
          // 
          let goodsNo = []
          obj.uploadFGoodsNo.forEach(item => {
            goodsNo.push(item.goodsNo)
          })
          delete promReqsGood.categoryNo
          delete promReqsGood.excludeCategoryNo
          promReqsGood.goodsNo = goodsNo.join(';')
          delete promReqsGood.excludeGoodsNo
        }
        if(obj.activityTypeList) {
          promReqsGood.activityType = obj.activityTypeList.join(';')
        }
        
        promReqsGood.promType = 5
        promReqsGood.ruleSource = obj.ruleSource
        promReqsGood.promDiscounts = this.goodPromDiscounts
        return promReqsGood
      }
    }
  }

</script>


