<style>
  .p-t {
    margin-bottom: 10px;
  }
  
  .u-btn-wrap {
    display: flex;
    flex-direction: row;
    flex-wrap: nowrap;
  }
  
  .file-name-wrap {
    width: 200px;
    height: 32px;
    line-height: 32px;
    overflow: hidden;
    border: 1px solid #ececec;
    border-radius: 4px;
    padding: 0 10px;
    position: relative;
    top: 5px;
  }
  
  .label-wrap {
    border: 1px solid #ececec;
    border-radius: 4px;
    position: relative;
    top: 5px;
    height: 32px;
    line-height: 32px;
  }
  
  .label-wrap > .excel-file-label {
    line-height: 32px;
    cursor: pointer;
    padding: 7px 10px;
  }
  
  .label-wrap > .excel-file-label:hover {
    color: #fff;
    background-color: #66b1ff;
    border-radius: 4px;
  }
</style>
  
<template>
  <div>
    <el-dialog 
      :title="title" 
      :visible.sync="isShow"
      :close-on-click-modal="false"
      :close-on-press-escape="false" 
      width='450px'>
      <el-form ref="form" label-width="80px">
        <el-form-item label="">
          <div class="excel-u-d-wrap">
            <div class="u-btn-wrap">
              <div class="file-name-wrap">{{ file.name }}</div>
              <div class="label-wrap">
                <label :for="elId" class="excel-file-label">
                  <input type="file" :id="elId" hidden @change="handleFileSelect"/>
                  <span class="u-txt"></span>
                </label>
              </div>
            </div>
            <div class="d-btn-wrap">
              <span>EXCEL</span>
              <el-button type="text" @click="downloadModel" size="mini" :disabled="isDisabled"></el-button>
            </div>
          </div>
        </el-form-item>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button @click="closeDialog" size="mini"></el-button>
        <el-button type="primary" @click="doConfirmHandel" size="mini" :disabled="!file.name || conDisabled" :loading="saveLoading"></el-button>
      </div>
    </el-dialog>
  </div>
</template>
  
<script>
  import * as utils from '@/utils/utils'
  import exportSheet from '@/utils/exportExcel'
  import Storage from '@/utils/localStorage'
  export default {
    data () {
      return {
        elId: 'addByExcel', // inputexcelid
        conDisabled: false, // 
        isDisabled: false, // 
        saveLoading: false,
        title: '',
        isShow: false,
        formLabelWidth: '70px',
        file: {name: ''}, // 
        // 
        exportGoods: [],
        tempConfig: {},
        // 
        list: []
  
      }
    },
    methods: {
      /**
       * [setDefaultMsg ]
       * @param {[type]} _params []
       */
      setDefaultMsg (_params) {
        this.isShow = true
        this.title = _params.title || ''
        this.tempConfig = _params.tempConfig || {}
        this.elId = _params.elId || 'addByExcel'
        // 
        this.resetExcelFile()
      },
      /**
       * [downloadModel ]
       * @return {[type]} [description]
       */
      downloadModel () {
        this.isDisabled = true
        let tableData = []
        let tableDataItem = {}
        for(let key in this.tempConfig) {
          tableDataItem[key] = ''
        }
        tableData.push(tableDataItem)
        const tHeader = utils.objectToArray(this.tempConfig, false)
        const filterVal = utils.objectToArray(this.tempConfig, true)
        exportSheet.ExportJsonToExcel({
          header: tHeader,
          filterVal,
          data: tableData,
          filename: `${this.title}`
        })
        setTimeout(() => {
          this.$notify.success({
            title: '',
            message: `${this.title}`
          })
          this.isDisabled = false
        }, 500)
      },
      /**
       * [handleFileSelect ]
       * @param  {[type]} e [description]
       * @return {[type]}   [description]
       */
      handleFileSelect (e) {
        // 
        if (!e.target.files.length) {
          return
        }
        this.file = e.target.files[0] || {name: ''}
        let idx = this.file.name.lastIndexOf(".")
        let strLength = this.file.name.length
        let postf = this.file.name.substring(idx, strLength) // 
  
        if (!this.file.name || postf != '.xlsx') {
          this.$notify.warning({
            title: '',
            message: 'excel'
          })
          // 
          this.resetExcelFile()
          return
        }
  
        let items = []
        let parseConfig = {}
        for (let key in this.tempConfig) {
          parseConfig[this.tempConfig[key]] = key
        }
        items = exportSheet.parseToJSON(this.file, parseConfig)
        items.then((result) => {
          result.forEach(item => {
            // sfInStockStr  true
            if(item.sfInStockStr === '') {
              item.sfInStock = true
            }else if(item.sfInStockStr === '') {
              item.sfInStock = false
            }else {
              item.sfInStock = true
            }
          })
          this.list = result
        }).catch()
      },
      /**
       * [doConfirmHandel ]
       * @return {[type]} [description]
       */
      doConfirmHandel() {
        this.conDisabled = true
        let params = {
          dataList: this.list
        }
        this.$emit("doConfirmHandel", params)
      },
      /**
       * @description: 
       * @param {type} 
       * @return: 
       */
      closeDisabled() {
        this.conDisabled = false
        // 
        this.resetExcelFile()
      },
      /**
       * @description: 
       * @param {type} 
       * @return: 
       */
      closeDialog() {
        this.isShow = false
        // 
        this.resetExcelFile()
      },
      resetExcelFile() {
        this.file = {name: ''}
        let excelFileDom = document.getElementById(this.elId)
        if(excelFileDom) excelFileDom.value = ""
      }

    }
  }
</script>
  


