<template>
  <el-date-picker
    class="search-date-picker-range"
    v-model="datePickerRange"
    size="small"
    :format="format" 
    :value-format="format"
    :type="type"
    :range-separator="rangeSeparator"
    :start-placeholder="startPlace"
    :end-placeholder="endPlace"
    :clearable="clearable"
    :editable="editable"
    :picker-options="pickerOptions"
    @change="datePickerRangeChange">
  </el-date-picker>
</template>
<script>
  export default {
    props: {
      type: {
        type: String,
        default: 'datetimerange'
      },
      defaultRange: {
        type: Array,
        default: []
      },
      format: {
        type: String,
        default: 'yyyy-MM-dd HH:mm:ss'
      },
      rangeSeparator: {
        type: String,
        default: ''
      },
      startPlace: {
        type: String,
        default: ''
      },
      endPlace: {
        type: String,
        default: ''
      },
      clearable: {
        type: Boolean,
        default: true
      },
      editable: {
        type: Boolean,
        default: true
      },
      pickerOptions: {
        type: Object,
        // 
        default: () => {
          return {
            disabledDate: (time) => {
              return time.getTime() > Date.now() + 86400000
            }
          }
        }
      }
    },
    data() {
      return {
        datePickerRange: this.defaultRange
      }
    },
    watch: {
      defaultRange() {
        this.datePickerRange = this.defaultRange
      }
    },
    methods: {
      datePickerRangeChange(range) {
        this.$emit("datePickerRangeChange", range)
      }
    }
  }
</script>


