<style>
  .c-g-sfw {
    padding-top: 0 !important;
    padding-bottom: 0 !important;
  }
  .switch-cp{
    margin: 0 10px 10px;
  }
</style>

<template>
  <div>
    <el-dialog :title="title" :visible.sync="isShow" width='720px'>
      <div class="switch-cp" v-if="noExclude">
        <el-switch
          v-model="chooseStoreTOF"
          active-color="#409EFF"
          inactive-color="#F56C6C"
          active-text=""
          inactive-text="">
        </el-switch>
      </div>
      <div class="search-form-wraper c-g-sfw">
        <el-form :model="searchContent" label-position="top" @submit.native.prevent>
          <el-form-item>
            <el-input
              class="search-input-small-width"
              placeholder=""
              v-model="searchContent.storeNo"
              @keyup.enter.native="doSearchHandel"
              clearable
              size="small">
            </el-input>
          </el-form-item>
          <el-form-item>
            <el-input
              class="search-input-small-width"
              placeholder=""
              v-model="searchContent.storeName"
              @keyup.enter.native="doSearchHandel"
              clearable
              size="small">
            </el-input>
          </el-form-item>
          <el-form-item>
            <el-select
              v-model="searchContent.saleType"
              size="small" 
              class="search-input-small-width"
              clearable
              placeholder=""
              @change="doSearchHandel">
              <el-option
                v-for="item in saleTypeOptions"
                :key="item.value"
                :label="item.label"
                :value="item.value">
              </el-option>
            </el-select>
          </el-form-item>
          <el-form-item>
            <el-cascader
            ref="cascaderCategory"
              placeholder=""
              class="add-input-inline"
              :options="storeGroupTypeNoOptions"
              size="small"
              filterable
              clearable
              :props="defaultProps"
              @change="storeGroupTypeNoChange"
              v-model="searchContent.storeGroupTypeNoList">
            </el-cascader>
          </el-form-item>
          <el-form-item>
            <el-button type="primary" @click="doSearchHandel" size="mini"></el-button>
          </el-form-item>
        </el-form>
      </div>
      <div>
        <el-table
          class="fixed-height"
          ref="multipleTable"
          v-loading="loading"
          :data="tableData"
          empty-text=""
          style="width:100%;"
          height="270"
          @select="handleSelect"
          @select-all="handleSelectAll"
          size="small">
          <el-table-column
            type="selection"
            width="50">
          </el-table-column>
          <el-table-column min-width="170" label="/">
            <template slot-scope="scope">
              <div>
                {{scope.row.storeNo}}_{{scope.row.storeName}}
              </div>
            </template>     
          </el-table-column>
          <el-table-column min-width="100" prop="managementTypeCode" label="" :formatter="managementTypeFormater"></el-table-column>
        </el-table>
      </div>
      <div slot="footer" class="dialog-footer">
        <el-button @click="isShow = false" size="mini"> </el-button>
        <el-button type="primary" @click="confirmClose" size="mini" :disabled="checkedList.length === 0"> </el-button>
      </div>
    </el-dialog>
  </div>
</template>

<script>
  import * as utils from '@/utils/utils'
  const reqUrl = {
    // 
    userStoreList: '/foundation/store/userStoreList',
    // 
    getStoreGroupType: '/backstage/storeGroup/storeGroupTypeList'
  }
  export default {
    data() {
      return {
        isShow: false,
        title: '', // title
        loading: false,
        chooseStoreTOF: true,
        searchContent: {
          storeNo: '',
          storeName: '',
          saleType: '',
          managementTypeCodeList: [],
          storeGroupTypeNo: '',
          storeGroupNo: '',
          storeGroupTypeNoList: []
        },
        storeGroupTypeNoOptions: [],
        defaultProps: {
          children: 'storeGroupList',
          label: 'labelName',
          value: 'valueName'
        },
        saleTypeOptions: [
          {
            label: '',
            value: 1
          },
          {
            label: '',
            value: 2
          },
          {
            label: '',
            value: 3
          },
          {
            label: '',
            value: 4
          }
        ],
        tableData: [],
        // 
        tableDataTemp: [],
        // 
        checkedList: [],

        // 
        noExclude: true 
      }
    },
    methods: {
      /**
       * [setDefault ]
       * @param {[type]} _val [description]
       */
      setDefault(_val, noExclude) {
        if(_val) {
          this.title = _val.title
          this.chooseStoreTOF = _val.chooseStoreTOF
          this.checkedList = JSON.parse(JSON.stringify(_val.checkedList)) 
        }

        if(noExclude === 'noExclude') {
          this.noExclude = false
        }else{
          this.noExclude = true
        }

        // 
        this.searchContent = {
          storeNo: '',
          storeName: '',
          saleType: '',
          managementTypeCodeList: [],
          storeGroupTypeNo: '',
          storeGroupNo: '',
          storeGroupTypeNoList: []
        }

        this.isShow = true
        this.getStoreGroupType()
        this.doSearchHandel(true)
      },
      storeGroupTypeNoChange(_val) {
        this.searchContent.storeGroupTypeNo = _val[0]
        this.searchContent.storeGroupNo = _val[1]
        this.doSearchHandel(true)
      },
      /**
       * @description: 
       * @param {type} 
       * @return: 
       */
       getStoreGroupType() {
        this.$http.get(reqUrl.getStoreGroupType).then(res => {
          if(res.code === 0) {
            let dataList = res.data && res.data.length ? res.data: []
            this.modifyStoreGroupeTree(dataList)
            this.storeGroupTypeNoOptions = dataList
            return 
          }
          throw new Error(res.msg)
        }).catch(e => {
          console.log("" + e.message)
        })
      },
      modifyStoreGroupeTree(storeTree, typeNo) {
        storeTree.forEach(v => {
          if(v.storeGroupList && v.storeGroupTypeNo) {
            // 
            v.valueName = v.storeGroupTypeNo
            v.labelName = v.storeGroupTypeName ? v.storeGroupTypeNo + '.' + v.storeGroupTypeName : ''
            if(v.storeGroupList.length) {
              this.modifyStoreGroupeTree(v.storeGroupList, v.storeGroupTypeNo)
            }
          }else if(v.storeGroupNo) {
            // 
            v.valueName = v.storeGroupNo
            v.labelName = v.storeGroupName ? v.storeGroupNo + '.' + v.storeGroupName : ''
            if(v.childrenList && v.childrenList.length) {
              v.storeGroupList = JSON.parse(JSON.stringify(v.childrenList))
              this.modifyStoreGroupeTree(v.storeGroupList)
            }
          }
        })
      },
      /**
       * [managementTypeFormater ]
       */
      managementTypeFormater(row, column, cellValue) {
        if(cellValue == 1) {
          cellValue = ''
        }else if(cellValue == 2) {
          cellValue = ''
        }else if(cellValue == 3) {
          cellValue = ''
        }else if(cellValue == 4) {
          cellValue = ''
        }
        return cellValue
      },
      /**
       * [doSearchHandel ]
       * @param  {[type]} _param [description]
       * @return {[type]}         [description]
       */
      doSearchHandel(_param) {
        this.loading = true
        this.searchContent.managementTypeCodeList = []
        if(this.searchContent.saleType) {
          this.searchContent.managementTypeCodeList.push(this.searchContent.saleType)
        }
        this.$http.post(reqUrl.userStoreList, {...this.searchContent, pageSize: -1}).then(res => {
          this.loading = false
          if(res.code === 0) {
            // 
            if(res.data && res.data.result && res.data.result.length) {
              this.tableData = res.data.result
              this.tableDataTemp = res.data.result
            }else {
              this.tableData = []
              this.tableDataTemp = []
            }
            this.defaultSelectFun()
            return 
          }
          throw new Error(res.msg)
        }).catch(e => {
          this.loading = false
          this.$notify.error({
            title: '',
            message: e.message
          })
        })
      },
      /**
       * [handleSelect ]
       * @return {[type]} [description]
       */
      handleSelect(val, row) {
        let hash = {}
        let checkedListTemp = []

        if(utils.isKeyInArrayBykey(row.storeNo, 'storeNo', this.checkedList)) {
          this.checkedList = utils.removeItemByKeyVal('storeNo', row.storeNo, this.checkedList)
          return
        }else {
          checkedListTemp.push(row)
        }

        let checkedListTempArr = [...checkedListTemp, ...this.checkedList]
        this.checkedList = checkedListTempArr.reduceRight((item, next) => {
          hash[next.storeNo] ? '' : hash[next.storeNo] = true && item.push(next)
          return item
        }, [])
      },
      /**
       * [handleSelectAll ]
       * @return {[type]} [description]
       */
      handleSelectAll(val) {
        let hash = {}
        if(val.length > 0) {        
          // 
          for(let item of val) {
            this.checkedList.push(item)
          }
          // 
          this.checkedList = this.checkedList.reduceRight((item, next) => {
            hash[next.storeNo] ? '' : hash[next.storeNo] = true && item.push(next)
            return item
          }, [])
        }else { 
          //  val.length<=0
          // 1.tableid
          let checkedListTemp = []
          for(let item of this.tableData) {
            checkedListTemp.push(item)
          }
          // 2.
          let checkedListArr = utils.getSubtract(this.checkedList, checkedListTemp, 'storeNo')
          this.checkedList = checkedListArr && checkedListArr.length > 0 ? checkedListArr : [] 
        }
      },
      /**
       * [defaultSelectFun ]
       * @param    {[type]}                 row [description]
       * @return   {[type]}                     [description]
       */
      defaultSelectFun() {
        let _this = this
        let rows = []
        for(let item of this.tableData) {
          for(let key of this.checkedList) {
            if(item.storeNo === key.storeNo) {
              rows.push(item)
            }
          } 
        }
        if (rows.length > 0) {
          this.$refs.multipleTable.clearSelection()
          setTimeout(function() {
            rows.forEach(rowItem => {
              _this.$refs.multipleTable.toggleRowSelection(rowItem)
            });
          }, 100)
        } else {
          this.$refs.multipleTable.clearSelection()
        }
      },
      /**
       * [confirmClose ]
       * @return {[type]} [description]
       */
      confirmClose() {
        this.checkedList.forEach(item => {
          if(item.managementTypeCode == 1) {
            item.managementTypeValue = ''
          }else if(item.managementTypeCode == 2) {
            item.managementTypeValue = ''
          }else if(item.managementTypeCode == 3) {
            item.managementTypeValue = ''
          }else if(item.managementTypeCode == 4) {
            item.managementTypeValue = ''
          }
        })
        let _params = {
          chooseStoreTOF: this.chooseStoreTOF,
          checkedList: this.checkedList
        }
        this.$emit("confirmChooseStore", _params)
        this.isShow = false
      }

    }
  }
</script>



