<style scoped>  
  .l-table, .r-choose {
    padding: 10px;
    background-color: #fff;
    border: 1px solid #ececec;
  }
  .r-choose {
    padding-bottom: 0;
  }
  
  .l-t-top, .r-c-top {
    padding-bottom: 8px;
    border-bottom: 1px solid #ececec;
  }
  .r-c-top {
    height: 37px;
    line-height: 37px;
  }
  .l-t-bottom {
    padding: 8px 0;
    height: 300px;
  }
  .r-c-bottom {
    overflow-y: auto;
    height: 310px;
  }
  .r-c-bottom>ul {
    list-style: none;
  }
  .r-c-bottom>ul>li {
    height: 40px;
    border-bottom: 1px solid #ececec;
    line-height: 40px;
    display: flex;
    flex-direction: row;
    flex-wrap:nowrap;
  }
  .r-c-bottom>ul>li>span {
    display: inline-block;
  }
  .r-c-bottom>ul>li>span:nth-child(1) {
    padding: 0 10px;
    flex: 1;
  }
  .r-c-bottom>ul>li {
    padding-right: 10px;
  }
  .r-c-bottom>ul>li>span:nth-child(2) {
    width: 18px;
    height: 18px;
    border-radius: 50%;
    background-color: #ececec;
    margin-top: 11px;
    cursor: pointer;
    line-height: 18px;
    text-align: center;
  }
  .r-c-bottom>ul>li>span:nth-child(2)>i {
    font-size: 12px;
  }
  .r-c-bottom>ul>li>span:nth-child(2):hover {
    background-color: #409EFF;
    color: #fff;
  }

</style>

<template>
  <div class="d-w">
    <el-dialog :title="title" :visible.sync="isShow" width='600px'>
      <el-row>
        <el-col :span="12">
          <div class="l-table">
            <div class="l-t-top">
              <el-input
                placeholder="ERP/"
                prefix-icon="el-icon-search"
                size="mini"
                v-model="condition"
                @keyup.enter.native="searchStore">
              </el-input>
            </div>
            <div class="l-t-bottom">
              <el-table
                class="fixed-height"
                ref="multiple"
                v-loading="loading"
                :data="tableData"
                @select="handleSelect"
                @select-all="handleSelectAll"
                empty-text=""
                style="width:100%;"
                height="302"
                size="small"
                :header-cell-style="headerStyle">
                <el-table-column prop="tempStoreName" label=""></el-table-column>
                <el-table-column
                  type="selection"
                  width="50">
                </el-table-column>
              </el-table>
            </div>
          </div>
        </el-col>
        <el-col :span="12">
          <div class="r-choose">
            <div class="r-c-top">
              <el-row>
                <el-col :span="16">&nbsp;<b>{{checkedList.length}}</b>&nbsp;</el-col>
                <el-col :span="8" style="text-align:right;"><el-button type="text" size="mini" @click="clearChooseData"></el-button></el-col>
              </el-row>
            </div>
            <div class="r-c-bottom">
              <ul>
                <li v-for="(item, idx) of checkedList" :key="idx">
                  <span>{{item.tempStoreName}}</span>
                  <span @click="deleteChooseItem(item)"><i class="el-icon-close"></i></span>
                </li>
              </ul>
            </div>  
          </div>
        </el-col>
      </el-row>

      <div slot="footer" class="dialog-footer">
        <el-button @click="isShow = false" size="mini"> </el-button>
        <el-button type="primary" @click="confirmClose" size="mini" :disabled="isDisabled"> </el-button>
      </div>
    </el-dialog>
  </div>
</template>

<script>
  import * as utils from '@/utils/utils'
  const goodsMgtUrl = {
    // -
    getStoreList: '/plateform/a/platformCityGoods/presale/store/list',
    // 
    batchStoreImport: '/plateform/a/platformCityGoods/presale/store/import'
  }
  export default {
    data() {
      return {
        isDisabled: false,
        headerStyle: {
          "padding-top": 0,
          "padding-bottom": "8px"
        },
        loading: false,
        isShow: false,
        title: '', // title
        tableData: [],
        tableTempData: [],
        condition: '', // ERP/
        // list
        checkedList: [],
        // id
        cityId: '',
        //    id
        platformCityGoodsIdList: [],
        platformId: ''

      }
    },
    methods: {
      /**
       * [setDefault ]
       * @param {[type]} _val [description]
       */
      setDefault(_val) {
        if(_val) {
          this.title = _val.title
          this.cityId = _val.cityId
          this.platformCityGoodsIdList = _val.platformCityGoodsIdList
          this.platformId = _val.platformId
        }

        this.isShow = true
        this.doSearchHandel()
      },
      /**
       * [doSearchHandel ]
       * @return {[type]} [description]
       */
      doSearchHandel() {
        // 
        this.checkedList = []
        this.loading = true
        this.$http.post(goodsMgtUrl.getStoreList, {cityId: this.cityId, platformId: this.platformId}).then(res => {
          this.loading = false
          if(res.code === 0) {
            if(res.data && res.data.length > 0) {
              for(let item of res.data) {
                item.uniqueId = item.cityId + '-' + item.storeNo 
                item.tempStoreName = item.storeNo + '-' + item.storeName 
              }
              this.tableData = JSON.parse(JSON.stringify(res.data))

              this.tableTempData = JSON.parse(JSON.stringify(res.data))
            }else {
              this.tableData = []
              this.tableTempData = []
            }
            return
          }
          throw new Error(res.msg)
        }).catch(e => {
          this.loading = false
          this.$notify.error({
            title: '',
            message: e.message
          })
        })

      },
      /**
       * [confirmClose ]
       * @return {[type]} [description]
       */
      confirmClose() {
        let storeNoListArr = []
        for(let item of this.checkedList) {
          storeNoListArr.push(item.storeNo)
        }
      
        let _param = {
          cityId: this.cityId,
          platformCityGoodsIdList: this.platformCityGoodsIdList,
          platformId: this.platformId,
          storeNoList: storeNoListArr
        }
        this.isDisabled = true
        this.$http.post(goodsMgtUrl.batchStoreImport, _param).then(res => {
          setTimeout(() => {
            this.isDisabled = false
          }, 800)
          if(res.code === 0) {
            this.isShow = false
            this.$message.success("")
            this.$emit("doSearchHandel") 
            return
          }
          throw new Error(res.msg)
        }).catch(e => {
          this.isDisabled = false
          this.$notify.error({
            title: '',
            message: e.message
          })
        })
      },
      /**
       * [handleSelect ]
       * @return {[type]} [description]
       */
      handleSelect(val, row) {
        let hash = {}
        let checkedListTemp = []

        if(utils.isKeyInArray(row.uniqueId, this.checkedList)) {
          this.checkedList = utils.removeItemByKeyVal('uniqueId', row.uniqueId, this.checkedList)
          return
        }else {
          checkedListTemp.push(row)
        }

        let checkedListTempArr = [...checkedListTemp, ...this.checkedList]
        this.checkedList = checkedListTempArr.reduceRight((item, next) => {
          hash[next.uniqueId] ? '' : hash[next.uniqueId] = true && item.push(next)
          return item
        }, [])
      },
      /**
       * [handleSelectAll ]
       * @return {[type]} [description]
       */
      handleSelectAll(val) {
        let hash = {}
        if(val.length > 0) {        
          // 
          for(let item of val) {
            this.checkedList.push(item)
          }
          // 
          this.checkedList = this.checkedList.reduceRight((item, next) => {
            hash[next.uniqueId] ? '' : hash[next.uniqueId] = true && item.push(next)
            return item
          }, [])
        }else { 
          //  val.length<=0
          // 1.tableid
          let checkedListTemp = []
          for(let item of this.tableData) {
            checkedListTemp.push(item)
          }
          // 2.
          let checkedListArr = utils.getSubtract(this.checkedList, checkedListTemp, 'uniqueId')
          this.checkedList = checkedListArr && checkedListArr.length > 0 ? checkedListArr : [] 
        }
      },
      /**
       * [defauleSelectFun ]
       * @param    {[type]}                 row [description]
       * @return   {[type]}                     [description]
       */
      defauleSelectFun() {
        let _this = this
        let rows = []
        for(let i of this.tableData) {
          for(let j of this.checkedList) {
            if(i.uniqueId == j.uniqueId) {
              rows.push(i)
            }
          }            
        }
        if (rows.length > 0) {
          this.$refs.multiple.clearSelection()
          setTimeout(function() {
            rows.forEach(rowItem => {
              _this.$refs.multiple.toggleRowSelection(rowItem)
            });
          }, 100)
        } else {
          this.$refs.multiple.clearSelection()
        }
      },

      /**
       * [deleteChooseItem ]
       * @return {[type]} [description]
       */
      deleteChooseItem(row) {
        const index = this.checkedList.findIndex(item => {
          return item.uniqueId === row.uniqueId
        })
        this.checkedList.splice(index, 1)
        // 
        this.defauleSelectFun()
      },
      /**
       * [clearChooseData ]
       * @return {[type]} [description]
       */
      clearChooseData() {
        this.checkedList = []
        this.handleSelectAll(this.checkedList)
        // 
        this.defauleSelectFun()
      },
      /**
       * [searchStore ]
       * @return {[type]} [description]
       */
      searchStore() {
        if(this.condition != '') {
          let matchObj = []
          for(let item of this.tableTempData) {
            if(item.storeNo === this.condition || item.storeName.indexOf(this.condition) > -1) {
              matchObj.push(item)
            }
          }
          this.tableData = matchObj
        }else {
          this.tableData = this.tableTempData
        }
        // 
        this.defauleSelectFun()
      }


    }

  }
</script>


