<style scoped>
  .el-wrap {
    display: inline;
  }
</style>
<template>
  <div class="el-wrap">
    <el-form-item label="">
      <el-select 
        :class="className" 
        v-model="regionCode" 
        filterable 
        placeholder="" 
        size="small" 
        @change="regionChange">
        <el-option
          v-for="item in regionOptions"
          :key="item.storeGroupNo"
          :label="item.storeGroupName"
          :value="item.storeGroupNo">
        </el-option>
      </el-select>
    </el-form-item>
    <el-form-item label="" v-if="showCity">
      <el-select 
        :class="className" 
        v-model="cityId" 
        filterable 
        placeholder="" 
        clearable 
        size="small" 
        @change="cityChange">
        <el-option
          v-for="item in cityOptions"
          :key="item.cityId"
          :label="item.cityName"
          :value="item.cityId">
        </el-option>
      </el-select>
    </el-form-item>
    <el-form-item label="" v-if="showGroup">
      <el-cascader
        ref="cascaderCategory"
        placeholder=""
        :class="className"
        :options="storeGroupTypeNoOptions"
        size="small"
        filterable
        clearable
        :props="defaultProps"
        @change="storeGroupTypeNoChange"
        v-model="storeGroupTypeNoList">
      </el-cascader>
    </el-form-item>
    <el-form-item label="" v-if="showStore">
      <el-select 
        :class="className" 
        v-model="storeNo" 
        filterable 
        placeholder="" 
        clearable 
        size="small" 
        @change="storeChange">
        <el-option
          v-for="item in storeOptions"
          :key="item.value"
          :label="item.label"
          :value="item.value">
        </el-option>
      </el-select>
    </el-form-item>
  </div>
</template>
<script>
  import Storage from '@/utils/localStorage'
  const requestUrl = {
    // 
    userAreaStoreGroupList: '/foundation/storeGroup/userAreaStoreGroupList',
    // 
    getCityList: '/foundation/store/storeCityList/',
    // 
    getStoreGroupType: '/backstage/storeGroup/storeGroupTypeList',
    // 
    search: '/plateform/a/store/condition/search'
  }
  export default {
    props: {
      className: {
        type: String,
        default: 'search-input'
      },
      showCity: {
        type: Boolean,
        default: true
      },
      showGroup: {
        type: Boolean,
        default: true
      },
      showStore: {
        type: Boolean,
        default: true
      },
      defaultCityId: {
        default: ''
      }
    },
    data() {
      return {
        storeGroupTypeNoOptions: [],
        storeGroupTypeNoList: [],
        // 
        defaultProps: {
          children: 'storeGroupList',
          label: 'labelName',
          value: 'valueName'
        },
        // 
        storeOptions: [],
        storeNo: [],
        // 
        regionOptions: [],
        regionCode: '',
        // 
        cityOptions: [],
        cityId: ''
      }
    },
    created() {
      // 
      this.getUserAreaList()
      // 
      if(this.showGroup) this.getStoreGroupType()
    },
    watch: {
      defaultCityId(newVal) {
        this.cityId = newVal
      }
    },
    methods: {
      /**
       * @description: 
       * @param {type} 
       * @return: 
       */
      getUserAreaList() {
        this.$http.get(requestUrl.userAreaStoreGroupList).then(res => {
          if(res.code === 0) {
            this.regionOptions = res.data && res.data.length ? res.data : []
            if(Storage.get('regionCode')) {
              let hasRegionOpt = this.regionOptions.some(item => {
                return item.storeGroupNo === Storage.get('regionCode')
              })
              if(hasRegionOpt) {
                this.regionCode = Storage.get('regionCode')
              }else {
                this.regionCode = this.regionOptions[0] && this.regionOptions[0].storeGroupNo ? this.regionOptions[0].storeGroupNo : ''
              }
            }else {
              this.regionCode = this.regionOptions[0] && this.regionOptions[0].storeGroupNo ? this.regionOptions[0].storeGroupNo : ''
              Storage.set('regionCode', this.regionCode)
            }
            // 
            if(this.showCity) this.getCityList(this.regionCode)
            // 
            if (this.showStore) this.getStoreList(this.regionCode, this.cityId, this.storeGroupTypeNoList[0], this.storeGroupTypeNoList[1]) 
            return 
          }
          throw new Error(res.msg)
        }).catch(e => {
          console.log("" + e.message)
        })
      },
      /**
       * @description: 
       * @param {type} 
       * @return: 
       */
      getCityList(regionCode) {
        this.$http.post(`${requestUrl.getCityList}${regionCode}`, {DIFFCITY: true}).then(res => {
          if(res.code === 0) {
            this.cityOptions = res.data && res.data.length ? res.data : []
            this.cityOptions.unshift({
              cityId: '',
              cityName: ''
            })
            return 
          }
          throw new Error(res.msg)
        }).catch(e => {
          console.log("" + e.message)
        })
      },
      /**
       * @description: 
       * @param {type} 
       * @return: 
       */
       getStoreGroupType() {
        this.$http.get(requestUrl.getStoreGroupType).then(res => {
          if(res.code === 0) {
            let dataList = res.data && res.data.length ? res.data: []
            this.modifyStoreGroupeTree(dataList)
            this.storeGroupTypeNoOptions = dataList
            return 
          }
          throw new Error(res.msg)
        }).catch(e => {
          console.log("" + e.message)
        })
      },
      modifyStoreGroupeTree(storeTree, typeNo) {
        storeTree.forEach(v => {
          if(v.storeGroupList && v.storeGroupTypeNo) {
            // 
            v.valueName = v.storeGroupTypeNo
            v.labelName = v.storeGroupTypeName ? v.storeGroupTypeNo + '.' + v.storeGroupTypeName : ''
            if(v.storeGroupList.length) {
              this.modifyStoreGroupeTree(v.storeGroupList, v.storeGroupTypeNo)
            }
          }else if(v.storeGroupNo) {
            // 
            v.valueName = v.storeGroupNo
            v.labelName = v.storeGroupName ? v.storeGroupNo + '.' + v.storeGroupName : ''
            if(v.childrenList && v.childrenList.length) {
              v.storeGroupList = JSON.parse(JSON.stringify(v.childrenList))
              this.modifyStoreGroupeTree(v.storeGroupList)
            }
          }
        })
      },
      /**
       * [getStoreList ]
       * @param regionCode {String} 
       * @param cityId {String} 
       * @param typeNo {String} 
       * @param groupNo {String} 
       * @param DIFFCITY {Boolean} true id false cityId
       * @return {[type]} [description]
       */
      getStoreList(regionCode, cityId, typeNo, groupNo) {
        this.$http.post(requestUrl.search, {regionCode: regionCode || null, cityId: cityId || null, storeGroupTypeNo: typeNo || null, storeGroupNo: groupNo || null, DIFFCITY: true}).then(res => {
          if (res.code === 0) {
            let optionsArr = res.data && res.data.length ? res.data : []
            this.storeOptions = optionsArr.map(item => {
              return {
                value: item.storeNo,
                label: item.storeNo + '-' + item.storeName
              }
            })
            return
          }
          throw new Error(res.msg)
        }).catch(e => {
          console.log("():", e.message)
        })
      },
      /**
       * @description: 
       * @param {type} 
       * @return: 
       */
      regionChange(val) {
        Storage.set('regionCode', val)
        this.cityId = ""
        this.storeGroupTypeNoList = []
        this.storeNo = ""
        // 
        if(this.showCity) this.getCityList(val)
        // 
        if (this.showStore) this.getStoreList(val, this.cityId, this.storeGroupTypeNoList[0], this.storeGroupTypeNoList[1]) 
        let params = {
          regionCode: this.regionCode,
          cityId: this.cityId,
          storeNo: this.storeNo,
          storeGroupTypeNoList: this.storeGroupTypeNoList
        }
        this.$emit("StoreGroupChange", params)
      },
      /**
       * @description: 
       * @param {type} 
       * @return: 
       */
      cityChange(val) {
        this.storeGroupTypeNoList = []
        this.storeNo = ""
        // 
        if (this.showStore) this.getStoreList(this.regionCode, val, this.storeGroupTypeNoList[0], this.storeGroupTypeNoList[1]) 
        let params = {
          regionCode: this.regionCode,
          cityId: this.cityId,
          storeNo: this.storeNo,
          storeGroupTypeNoList: this.storeGroupTypeNoList
        }
        this.$emit("StoreGroupChange", params)
      },
      /**
       * @description: 
       * @param {type} 
       * @return: 
       */
      storeGroupTypeNoChange(val) {
        this.storeNo = ''
        this.getStoreList(this.regionCode, this.cityId, val[0], val[1])
        let params = {
          regionCode: this.regionCode,
          cityId: this.cityId,
          storeNo: this.storeNo,
          storeGroupTypeNoList: this.storeGroupTypeNoList
        }
        this.$emit("StoreGroupChange", params)
      },
      /**
       * @description: 
       * @param {type} 
       * @return: 
       */
      storeChange(val) {
        let params = {
          regionCode: this.regionCode,
          cityId: this.cityId,
          storeNo: this.storeNo,
          storeGroupTypeNoList: this.storeGroupTypeNoList
        }
        this.$emit("StoreGroupChange", params)
      }
    }
  }
</script>


