<style scoped>
  .form-item-wrap {
    margin-bottom: 0 !important;
  }
</style>
<template>
  <div>
    <el-dialog 
      :close-on-press-escape="false"
      :close-on-click-modal="false"
      :title="title" 
      :visible.sync="isShow" 
      width='740px'>
      <el-form ref="form" @submit.native.prevent>
        <el-form-item class="form-item-wrap">
          <region-store :regionCode="regionCode" :platformId="platformId" @setCheckedData="setCheckedData"></region-store>
        </el-form-item>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button @click="isShow = false" size="mini"> </el-button>
        <el-button type="primary" @click="confirmClose" size="mini" :disabled="isDisabled || checkedList.length === 0"> </el-button>
      </div>
    </el-dialog>
  </div>
</template>

<script>
  import * as utils from '@/utils/utils'
  import RegionStore from '@/components/Common/StoreCommon/RegionStore'
  const requestUrl = {
    // -
    getStoreList: '/plateform/a/platformCityGoods/presale/store/list',
    // 
    getCityList: '/foundation/store/storeCityList/'
  }
  export default {
    data() {
      return {
        isDisabled: false,
        isShow: false,
        title: '', // title
        // list
        checkedList: [],
        activityType: '',
        platformId: '', // id
        regionCode: '' // id
      }
    },
    components: {
      "region-store": RegionStore
    },
    methods: {
      /**
       * [setDefault ]
       * @param {[type]} val [description]
       */
      setDefault(val) {
        if(val) {
          this.title = val.title
          this.platformId = val.platformId
          this.regionCode = val.regionCode
          this.activityType = val.activityType
        }
        this.isShow = true
        this.isDisabled = false
      },
      /**
       * [confirmClose ]
       * @return {[type]} [description]
       */
      confirmClose() {
        this.isDisabled = true
        this.$emit("confirmHandel", {checkedList: this.checkedList, activityType: this.activityType}) 
      },
      closeDialog() {
        this.isShow = false
      },
      closeDisabled() {
        this.isDisabled = false
      },
      /**
       * @description: 
       * @param {type} 
       * @return: 
       */
      setCheckedData(checkedList) {
        this.checkedList = checkedList || []
      }

    }
  }
</script>


