<style>
  .show-choose{
    color: #409EFF;
    cursor: pointer;
  }
  .upload{
    display: inline-block;
    line-height: 1;
    white-space: nowrap;
    cursor: pointer;
    border: 1px solid #dcdfe6;
    color: #fff;
    -webkit-appearance: none;
    text-align: center;
    box-sizing: border-box;
    outline: 0;
    margin: 0;
    padding: 10px 15px;
    font-weight: 500;
    border-radius: 3px;
    background: #2077FF;
  }
</style>
<template>
  <div>
    <!--  -->
    <div>
      <el-form-item label="">
        <el-select size="small" class="add-input" v-model.trim="addOffline.chooseOfflineType" placeholder="" @change='clearType'>
          <el-option :label="item.label" :value="item.value" :key="item.value" v-for="item in chooseOfflineTypeList"></el-option>
        </el-select>
      </el-form-item>
      <el-form-item label="" v-if="addOffline.chooseOfflineType === 1">
        <el-button size="small" @click="chooseOfflineGoodsOpen" icon="el-icon-plus"></el-button>
        <span class="show-choose" v-if="addOffline.offlineGoodsCheckedList.length && addOffline.chooseOfflineGoodsTOF" @click="showOfflineGoodsChecked"></span>
        <span class="show-choose" v-else-if="addOffline.offlineGoodsCheckedList.length && !addOffline.chooseOfflineGoodsTOF" @click="showOfflineGoodsChecked"></span>
      </el-form-item>
      <el-form-item label="" v-if="addOffline.chooseOfflineType === 2">
        <el-button size="small" @click="chooseCateOpen" icon="el-icon-plus"></el-button>
        <span class="show-choose" v-if="addOffline.offlineCategoryCheckedList.length && addOffline.chooseFCateTOF" @click="showOfflineCategoryChecked"></span>
        <span class="show-choose" v-else-if="addOffline.offlineCategoryCheckedList.length && !addOffline.chooseFCateTOF" @click="showOfflineCategoryChecked"></span>
      </el-form-item>
      <el-form-item label="" v-if="addOffline.chooseOfflineType === 3">
        <el-button size="small" type="info" @click="downloadModel"></el-button>
        <label for="excelFileF">
          <span class="upload"></span>
          <input type="file" id="excelFileF" hidden @change="handleFileSelectF" @click="chooseFileF" v-if="fileF.isShow"/>
          <p style="display: inline-block;">{{ addOffline.fileName }}</p>
        </label>
        <span v-if="addOffline.uploadFGoodsNo.length" class="show-choose" @click="showOfflineGoodsUpload"></span>
      </el-form-item>
      <el-form-item label="">
        <el-checkbox-group v-model="addOffline.activityTypeList" @change='changeOffline' v-if="erpPromotionType.length">
          <el-checkbox :label="item.code" v-for="(item, index) in erpPromotionType" :key="index">{{item.value}}</el-checkbox>
        </el-checkbox-group>
        <span v-if="erpPromotionType.length"></span>
        <span v-else></span>
      </el-form-item>
    </div>

    <!--  -->
    <div>
      <choose-offline-goods ref="chooseOfflineGoods" @confirmChooseOfflineGoods="confirmChooseOfflineGoods"></choose-offline-goods>
    </div>
    <!--  -->
    <div>
      <choose-category ref="chooseCate" @confirmChooseCate="confirmChooseCate"></choose-category>
    </div>
  </div>
</template>
<script>
  import * as utils from '@/utils/utils'
  import ChooseOfflineGoods from "../Dialog/ChooseOfflineGoods"
  import ChooseCategory from "../Dialog/ChooseCategory"
  import exportSheet from '@/utils/exportSheet'
  const reqUrl = {
    // 
    productList: '/foundation/goods/info/query',
    // CommonCode
    listByCommonCodes: '/foundation/businessConfig/listByCommonCodes'
  }
  export default {
    data() {
      return {
        // 
        chooseOfflineTypeList: [
          {
            value: 1,
            label: ''
          },
          {
            value: 2,
            label: ''
          },
          {
            value: 3,
            label: ''
          }
        ],
        addOffline: {
          promType: '',
          chooseOfflineType: 1, //  --
          chooseOfflineGoodsTOF: true, // 
          offlineGoodsCheckedList: [], // 
          chooseFCateTOF: true, // 
          offlineCategoryCheckedList: [], // 
          uploadFGoodsNo: [], // 
          activityTypeList: [], // 
          fileName: ''
        },
        fileF: {name: '', isShow: true}, // 
        tempConfigF: {
          GoodsNO: '',
          GoodsName: ''
        },
        initOffline: {
          promType: '',
          chooseOfflineType: 1, //  --
          chooseOfflineGoodsTOF: true, // 
          offlineGoodsCheckedList: [], // 
          chooseFCateTOF: true, // 
          offlineCategoryCheckedList: [], // 
          uploadFGoodsNo: [], // 
          activityTypeList: [] // 
        },
        erpPromotionType: []
      }
    },
    created() {
      this.getErpPromotionType()
    },
    mounted() {
      
    },
    components: {
      "choose-offline-goods": ChooseOfflineGoods,
      "choose-category": ChooseCategory
    },
    methods: {
      promTypeChange(promType, promReqs) {
        if(promType === '2') {
          this.chooseOfflineTypeList = [
            {
              value: 1,
              label: ''
            },
            {
              value: 2,
              label: ''
            },
            {
              value: 3,
              label: ''
            }
          ]
        }else if(promType === '5') {
          this.chooseOfflineTypeList = [
            {
              value: 1,
              label: ''
            },
            {
              value: 3,
              label: ''
            }
          ]
        }
        this.addOffline = {...this.initOffline, ...promReqs}
      },
      getErpPromotionType() {
        this.$http.post(reqUrl.listByCommonCodes, ['ErpPromotionType'], true).then((res) => {
          if (res.code === 0) {
            if(res.data && res.data['ErpPromotionType']) {
              this.erpPromotionType = res.data['ErpPromotionType']
            }else{
              this.erpPromotionType = []
            }
            return
          }
          throw new Error(res.msg)
        }).catch(e => {
          console.log(e.message)
        })
      },
      /**
       * [chooseOfflineGoodsOpen ]
       * @return {[type]} [description]
       */
      chooseOfflineGoodsOpen() {
        // 
        if(this.addOffline.promType === '5') {
          let _params = {
            title: '',
            checkedList: this.addOffline.offlineGoodsCheckedList
          }
          this.$refs.chooseOfflineGoods.setDefault(_params, false, 'noExclude')
        }else{
          let _params = {
            title: '',
            chooseOfflineGoodsTOF: this.addOffline.chooseOfflineGoodsTOF,
            checkedList: this.addOffline.offlineGoodsCheckedList
          }
          this.$refs.chooseOfflineGoods.setDefault(_params, false)
        }
      },
      /**
       * [chooseCateOpen ]
       * @return {[type]} [description]
       */
      chooseCateOpen() {
          // 
          let _params = {
            title: '',
            chooseFCateTOF: this.addOffline.chooseFCateTOF,
            checkedList: this.addOffline.offlineCategoryCheckedList
          }
          this.$refs.chooseCate.setDefault(_params, false)
      },
      /**
       * [confirmChooseOfflineGoods ]
       * @param  {[type]} _value [description]
       * @return {[type]}        [description]
       */
      confirmChooseOfflineGoods(_value) {
        if(_value.chooseOfflineGoodsTOF !== undefined) {
          this.addOffline.chooseOfflineGoodsTOF = _value.chooseOfflineGoodsTOF
        }
        if(_value.checkedList && _value.checkedList.length) {
          this.addOffline.offlineGoodsCheckedList = _value.checkedList
        }else {
          this.addOffline.offlineGoodsCheckedList = []
        }
        this.changeOffline()
      },      /**
       * [confirmChooseCate ]
       * @param  {[type]} _value [description]
       * @return {[type]}        [description]
       */
      confirmChooseCate(_value) {
        if(_value.checkedOfflineList && _value.checkedOfflineList.length) {
          this.addOffline.chooseFCateTOF = _value.chooseFCateTOF
          this.addOffline.offlineCategoryCheckedList = _value.checkedOfflineList
        }else {
          this.addOffline.offlineCategoryCheckedList = []
        }
        this.changeOffline()
      },
      /**
       * [showOfflineGoodsChecked ]
       */
      showOfflineGoodsChecked() {
        let title = ''
        if(this.addOffline.chooseOfflineGoodsTOF) {
          title = ''
        }else{
          title = ''
        }
        let _params = {
          title: title,
          checkedList: this.addOffline.offlineGoodsCheckedList
        }
        this.$refs.chooseOfflineGoods.setDefault(_params, 'show')
      },
      /**
       * [showOfflineCategoryChecked ]
       */
      showOfflineCategoryChecked() {
        let title = ''
        if(this.addOffline.chooseFCateTOF) {
          title = ''
        }else{
          title = ''
        }
        let _params = {
          title: title,
          checkedList: this.addOffline.offlineCategoryCheckedList
        }
        this.$refs.chooseCate.setDefault(_params, 'show')
      },
      /**
       * [showOfflineGoodsUpload ]
       */
      showOfflineGoodsUpload() {
        let _params = {
          title: '',
          checkedList: this.addOffline.uploadFGoodsNo
        }
        this.$refs.chooseOfflineGoods.setDefault(_params, 'show')
      },
      /**
       * [downloadModel ]
       * @return {[type]} [description]
       */
      downloadModel(_params) {
        try {
          exportSheet.exportFile([{ GoodsNO: '', GoodsName: '' }], '.xlsx', this.tempConfigF)
          this.$notify.success({
            title: '',
            message: ''
          })
        } catch (e) {
          this.$notify.error({
            title: '',
            message: ''
          })
        }
      },
      // 
      chooseFileF() {
        this.fileF = {
          name: '',
          isShow: false
        }
        this.addOffline.fileName = ''
        this.addOffline.uploadFGoodsNo = []
        setTimeout(() => {
          this.$set(this.fileF, 'isShow', true)
        }, 100)
        this.changeOffline()
      },
      /**
       * [handleFileSelectF --]
       * @param  {[type]} e [description]
       * @return {[type]}   [description]
       */
      handleFileSelectF(e) {
        // 
        if(!e.target.files.length) {
          return
        }
        this.fileF = e.target.files[0] || {name: ''}
        this.addOffline.fileName = this.fileF.name
        this.$set(this.fileF, 'isShow', true)
        let idx = this.fileF.name.lastIndexOf(".")
        let strLength = this.fileF.name.length
        let postf = this.fileF.name.substring(idx, strLength) // 

        if (!this.fileF.name || postf != '.xlsx') {
          this.$notify.warning({
            title: '',
            message: 'excel'
          })
          return
        }

        let items = []
        let parseConfig = {}
        for (let key in this.tempConfigF) {
          parseConfig[this.tempConfigF[key]] = key
        }
        items = exportSheet.parseToJSON(this.fileF, parseConfig)
        items.then((result) => {
          // 
          let resultVal = result.filter(item => !!item.GoodsNO)

          if(resultVal.length <= 0) {
            this.$notify.warning({
              title: '',
              message: ''
            })
            return
          }
          let goodsNoArr = []
          let goodsNameArr = []
          // 
          for(let i of resultVal) {
            goodsNoArr.push(i.GoodsNO)
            goodsNameArr.push(i.GoodsName)
          }
          this.addOffline.uploadFGoodsNo = goodsNoArr.join(";")
          //
          this.$http.post(reqUrl.productList, {includeGoodsNos: goodsNoArr, pageNum: 1, pageSize: -1}).then(res => {
            if(res.code === 0) {
              if(res.data && res.data.result && res.data.result.length) {
                this.addOffline.uploadFGoodsNo = res.data.result
              }else {
                this.addOffline.uploadFGoodsNo = []
                this.$notify.error({
                  title: '',
                  message: ''
                })
              }
              this.changeOffline()
              return
            }
            throw new Error(res.msg)
          }).catch(e => {
            this.$notify.error({
              title: '',
              message: e.message
            })
          })
        })
      },
      changeOffline() {
        this.$emit("changeOffline", this.addOffline)
      },
      clearType(val) {
        if(val === 1) {
          // 
          this.addOffline.chooseFCateTOF = true
          this.addOffline.offlineCategoryCheckedList = []
          this.addOffline.uploadFGoodsNo = []
          this.fileF = {name: '', isShow: true}
          this.addOffline.fileName = ''
        }else if(val === 2) {
          // 
          this.addOffline.chooseOfflineGoodsTOF = true
          this.addOffline.offlineGoodsCheckedList = []
          this.addOffline.uploadFGoodsNo = []
          this.fileF = {name: '', isShow: true}
          this.addOffline.fileName = ''
        }else if(val === 3) {
          // 
          this.addOffline.chooseOfflineGoodsTOF = true
          this.addOffline.offlineGoodsCheckedList = []
          this.addOffline.chooseFCateTOF = true
          this.addOffline.offlineCategoryCheckedList = []
        }
        this.changeOffline()
      }
    }
  }

</script>


