<style>

</style>
<template>
  <div>
    <el-dialog :title="title" :visible.sync="visible" width="520px">
      <el-form label-width="140px" label-position="left" v-loading="loading" style="max-height: 300px;overflow-y: auto;">
        <el-form-item label="">
          <p style="font-size:16px !important;color:#555;font-weight:bold;">{{addParamMesg.platformName}}</p>
        </el-form-item>

        <el-form-item label="ID" required v-if="addParamMesg.platform !== 5 && addParamMesg.platform !== 6">
          <el-input v-model="addParamMesg.outerPlatformId" size="small" placeholder="ID" class="search-date-picker"></el-input>
        </el-form-item>

        <el-form-item label="" required>
          <el-input v-model="addParamMesg.storeName" size="small" placeholder="" class="search-date-picker" clearable></el-input>
        </el-form-item>

        <el-form-item label="" required>
          <el-input v-model="addParamMesg.servicePhone" size="small" placeholder="" class="search-date-picker" clearable></el-input>
        </el-form-item>

        <el-form-item label="" required>
          <el-radio-group v-model="addParamMesg.openStatus" size="small">
            <el-radio-button :label="item.label" v-for="(item, idx) in openStatusRadioOpt" :key="idx">{{item.labelName}}</el-radio-button>
          </el-radio-group>
        </el-form-item>

        <el-form-item label="" required v-if="addParamMesg.storeType === 3" >
          <div><span style="color: red;"></span></div>
          <el-select class="search-date-picker" v-model="cityIdList" filterable placeholder="" size="small" multiple>
            <el-option
              v-for="item in cityOptions"
              :key="item.cityId"
              :label="item.cityName"
              :value="item.cityId">
            </el-option>
          </el-select>
        </el-form-item>
        <el-form-item v-if="addParamMesg.platform === 5" label="" required>
          <el-time-picker
            class="search-date-picker"
            size="small"
            is-range
            v-model="addParamMesg.pickupTimeRange"
            value-format="HH:mm"
            format="HH:mm"
            range-separator="-"
            start-placeholder=""
            end-placeholder=""
            placeholder="">
          </el-time-picker>
        </el-form-item>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button @click="visible = false" size="mini"> </el-button>
        <el-button type="primary" @click="doeditHandel" size="mini" :disabled="isDisabled"> </el-button>
      </div>
    </el-dialog>
  </div>
</template>

<script>
const storeMGTUrl = {
  // 
  platformInfo: '/plateform/a/store/platform/info',
  // 
  platformOnline: '/plateform/a/store/platform/online',
  // 
  userStoreCityList: '/foundation/store/userStoreCityList'
}
export default {
  data() {
    return {
      loading: false,
      isDisabled: false, // 
      visible: false,
      storeNo: '',
      title: '',
      addParamMesg: {
        platform: '', // 1-2-3-4-, 5-
        storeId: '', // id
        storeNo: '', // 
        storeType: '', // 
        outerPlatformId: '', // id
        openStatus: 1, // 1-;2-
        platformName: '',
        // 
        servicePhone: '',
        storeName: '', // 
        pickupStartTime: '',
        pickupEndTime: '',
        pickupTimeRange: []
      },
      openStatusRadioOpt: [
        {
          label: 1,
          labelName: ''
        },
        {
          label:2,
          labelName: ''
        }
      ],
      cityIdList: [],
      cityOptions: []

    }
  },
  methods: {
    /**
     * [setDefaule ]
     */
    setDefaule(_val) {
      this.addParamMesg.platform = _val.platformId
      this.addParamMesg.storeNo = _val.storeNo
      this.addParamMesg.storeId = _val.storeId

      // 
      this.getDetailMesg()
      // 
      this.visible = true
    },
    /**
     * [getDetailMesg ]
     * @return {[type]} [description]
     */
    getDetailMesg() {
      this.loading = true
      this.$http.post(storeMGTUrl.platformInfo, {platformId: this.addParamMesg.platform, storeNo: this.addParamMesg.storeNo, storeId: this.addParamMesg.storeId}).then(res => {
        this.loading = false
        if(res.code === 0) {
           if(res.data) {
            this.addParamMesg.platform = res.data.platform
            this.addParamMesg.platformName = res.data.platformName
            this.addParamMesg.openStatus = res.data.openStatus ? parseInt(res.data.openStatus) : 1
            this.addParamMesg.outerPlatformId = res.data.outerPlatformId || ''
            this.addParamMesg.servicePhone = res.data.servicePhone || ''
            this.addParamMesg.storeName = res.data.storeName || ''
            this.addParamMesg.storeType = res.data.storeType || ''
            this.addParamMesg.pickupStartTime = res.data.pickupStartTime || ''
            this.addParamMesg.pickupEndTime = res.data.pickupEndTime || ''
            if(res.data.pickupStartTime && res.data.pickupEndTime) {
              this.addParamMesg.pickupTimeRange = [res.data.pickupStartTime, res.data.pickupEndTime]
            } else {
              this.addParamMesg.pickupTimeRange = []
            }
           }
           if(res.data.storeType === 3) {
            this.cityIdList = res.data.cityIds || []
            this.getCityList()
           }
          return
        }
        throw new Error(res.msg)
      }).catch(e => {
        this.loading = false
        this.$notify.error({
          title: '',
          message: e.message
        })
      })
    },
    /**
     * [doeditHandel ]
     * @return {[type]} [description]
     */
    doeditHandel() {
      let mobile = /^(\d{3,4}-)\d{7,8}$/
      let phone = /^[1][2,3,4,5,6,7,8][0-9]{9}$/
      // 1  openDcdcOpenTimedcCloseTimedcScope
      // 2  id
      // 3openStatus 
      let numReg = /^\d+(?=\.{0,1}\d+$|$)/ 
      if(this.addParamMesg.platform === '') {
        this.$message("")
        return
      }
      if(this.addParamMesg.platform !== 5 && this.addParamMesg.platform !== 6) {
        // 
        if(this.addParamMesg.outerPlatformId === '') {
          this.$message("ID")
          return
        }
      }
      if(!this.addParamMesg.servicePhone) {
        this.$message("")
        return
      }
      // 
      // else if(!phone.test(this.addParamMesg.servicePhone)) {
      //   if(!mobile.test(this.addParamMesg.servicePhone)) {
      //     this.$message("(131********)(0377-*******)")
      //     return
      //   }
      // }
      if(!this.addParamMesg.storeName) {
        this.$message("")
        return
      }
      if(this.addParamMesg.openStatus === '') {
        this.$message("")
        return
      }
      if(this.addParamMesg.storeType === 3) {
        if(this.cityIdList.length === 0) {
          this.$message("")
          return
        }else {
          this.addParamMesg.cityIds = this.cityIdList
        }
      }else {
        delete this.addParamMesg.cityIds
      }
      if(this.addParamMesg.platform === 5) {
        if(!this.addParamMesg.pickupTimeRange || !this.addParamMesg.pickupTimeRange.length) {
          this.$message("")
          return
        }else {
          this.addParamMesg.pickupStartTime = this.addParamMesg.pickupTimeRange[0] || ''
          this.addParamMesg.pickupEndTime = this.addParamMesg.pickupTimeRange[1] || ''
        }
      }
      this.isDisabled = true
      this.$http.post(storeMGTUrl.platformOnline, {...this.addParamMesg}).then(res => {
        setTimeout(() => {
          this.isDisabled = false
        }, 800)
        if(res.code === 0) {
          this.$message.success("")
          this.visible = false
          this.$emit("doSearchHandel")
          return
        }
        throw new Error(res.msg)
      }).catch(e => {
        this.isDisabled = false
        this.$notify.error({
          title: '',
          message: e.message
        })
      })
    },
    /**
     * [getCityList ]
     * @return {[type]} [description]
     */
     getCityList () {
      this.$http.get(storeMGTUrl.userStoreCityList).then(res => {
        if (res.code === 0) {
          if (res.data && res.data.length) {
            this.cityOptions = res.data
          }
          return
        }
        throw new Error(res.msg)
      }).catch(e => {
        console.log(':', e.message)
      })
    }
  }
}
</script>



