<style>
  .bl-draggable-wrap {
    overflow: hidden;
  }
  .bl-draggable-wrap .bl-upload-wrap {
    float: left;
    margin: 0 5px;
  }
  .bl-upload-wrap .uploader11 {
    width: 120px;
    height: 120px;
    display: inline-block;
  }
  .bl-upload-wrap .uploader34 {
    width: 90px;
    height: 120px;
    display: inline-block;
  }
  .bl-upload-wrap .uploader43 {
    width: 120px;
    height: 90px;
    display: inline-block;
  }
  .bl-upload-wrap .el-upload {
    width: 100%;
    height: 100%;
    border: 1px dashed #d9d9d9;
    border-radius: 6px;
    cursor: pointer;
    position: relative;
    overflow: hidden;
  }
  .bl-upload-wrap .el-upload:hover {
    border-color: #409EFF;
  }
  .bl-upload-wrap img {
    width: 100%;
    height: 100%;
    display: block;
  }
  .upload-img-wrap {
    width: 100%;
    height: 100%;
    display: flex;
    align-items: center;
    justify-content: center;
    font-size: 28px;
    color: #8c939d;
  }
  .btn-w {
    padding: 0 5px;
    line-height: 0;
    margin-top: 8px;
    margin-bottom: 8px;
    text-align: center;
  }
  .btn-w .up-h-i {
    cursor: pointer;
    padding-left: 6px;
    padding-right: 6px;
  }
  .btn-w .up-h-i:hover {
    color: #3080fe;
  }
</style>

<template>
  <!-- 
    <image-upload 
      ref="imageUpload" 
      @setImageList="setImageList"
      :maxImgsNum="6"
      :imgSize="2048"
      :checkWidth="true"
      :width="800"
      :checkHeight="true"
      :height="800"/> -->
  <draggable v-model="imageList" @update="datadragEnd" @start="datadragStart" class="bl-draggable-wrap">
    <transition-group>
      <div class="bl-upload-wrap" 
        v-for="(item, idx) of imageList" 
        @click="setItemIdx(idx)" 
        :key="item.imgUrl">
        <el-upload
          :class="imgAspect === 2 ? 'uploader34' : imgAspect === 3 ? 'uploader43' : 'uploader11'"
          action=""
          :show-file-list="false"
          :before-upload="imageUploadReg"
          :http-request="emitUpload">
          <div v-loading="imageUploadLoading && currentUpFileIdx == idx" class="upload-img-wrap">
            <img v-if="item.imgUrl" :src="item.imgUrl">
            <i v-else class="el-icon-plus"></i>
          </div>
        </el-upload>
        <div class="btn-w" v-if="item.imgUrl && item.imgUrl != ''"> 
          <span class="up-h-i" 
            @click="deleteUpFile(idx)">
            <i class="el-icon-delete"></i>
          </span>
          <span class="up-h-i">
            <i class="el-icon-rank"></i>
          </span>
        </div>
      </div>
    </transition-group>
  </draggable>
</template>

<script>
  import Draggable from 'vuedraggable'
  import * as qiniu from 'qiniu-js'
  const fileUrlList = {
    getQiNiuToken: '/foundation/qiniu/auth/file'
  }
  export default {
    props: {
      // imgAspect  11:1  2: 3:4  3:  4:3
      imgAspect: {
        type: Number,
        default: 1
      },
      // imgSize
      checkImgSize: {
        type: Boolean,
        default: false
      },
      // imgSize ,1024Kb1M
      imgSize: {
        type: Number,
        default: 1024
      },
      // 
      maxImgsNum: {
        type: Number,
        default: 1000000000
      },
      // width
      checkWidth: {
        type: Boolean,
        default: false
      },
      width: {
        type: Number,
        default: 0
      },
      // height
      checkHeight: {
        type: Boolean,
        default: false
      },
      height: {
        type: Number,
        default: 0
      }
    },
    data() {
      return {
        imageUploadLoading: false,
        currentUpFileIdx: -1,
        imageList: []
      }
    },
    components: {
      'draggable': Draggable
    },
    methods: {
      /**
       * @description: 
       * @param {object} params {
       * imageList {array}  
       * }
       */
      initHandel(params) {
        let images = params.imageList || []
        let imageList = images.filter(item => {
          return item.imgUrl
        })
        if(imageList.length < this.maxImgsNum) {
          imageList.push({
            imgUrl: '',
            index: imageList.length
          })
        }
        this.imageList = imageList
      },
      datadragStart() {
        if(this.imageUploadLoading || this.maxImgsNum === 1) return
      },
      datadragEnd(evt) {
        const imageListTemp = [...this.imageList]
        let newImageList = imageListTemp.filter((item, index) => {
          item.index = index
          return item.imgUrl && item.imgUrl != ''
        });
        // 
        this.$emit("setImageList", {
          imageList: newImageList
        })
        if(this.maxImgsNum > 1 && newImageList.length < this.maxImgsNum) {
          const imageListItem = {
            imgUrl: '',
            index: imageListTemp.length
          }
          newImageList.push(imageListItem)
        }
        this.imageList = newImageList
      },
      setItemIdx(idx) {
        if(this.imageUploadLoading) return
        this.currentUpFileIdx = idx
      },
      imageUploadReg (file) {
        const isPic = file.type.indexOf('image/') !== -1
        const isJPG = file.type === 'image/jpeg' || 'image/png'
        if (!isPic) {
          this.$message.warning('!')
        }
        if (!isJPG) {
          this.$message.warning('JPGPNG!')
        }
        let isSizeNum = true
        if(this.checkImgSize) {
          isSizeNum = file.size <= this.imgSize * 1024
          let sizeValiTipStr = ''
          if(this.imgSize < 1024) {
            sizeValiTipStr = `${this.imgSize}KB`
          }else {
            sizeValiTipStr = `${this.imgSize / 1024}M`
          }
          if (!isSizeNum) {
            this.$message.warning(`${sizeValiTipStr}`)
          }
        }
        let imgWidthHeightCheck = null
        if(this.checkWidth && this.width && this.checkHeight && this.height) {
          // 
          const that = this
          imgWidthHeightCheck = new Promise(function(resolve, reject) {
            let _URL = window.URL || window.webkitURL
            let img = new Image();
            img.onload = function() {
              let valid = img.width == that.width && img.height == that.height
              valid ? resolve() : reject()
            }
            img.src = _URL.createObjectURL(file)
          }).then(() => {
            return file
          }, () => {
            this.$message.warning(`: ${that.width}: ${that.height}${sizeValiTipStr}JPGPNG`)
            return Promise.reject()
          })
        }else if(this.checkWidth && this.width) {
          // 
          const that = this
          imgWidthHeightCheck = new Promise(function(resolve, reject) {
            let _URL = window.URL || window.webkitURL
            let img = new Image();
            img.onload = function() {
              let valid = img.width == that.width
              valid ? resolve() : reject()
            }
            img.src = _URL.createObjectURL(file)
          }).then(() => {
            return file
          }, () => {
            this.$message.warning(`: ${that.width}${sizeValiTipStr}JPGPNG`)
            return Promise.reject()
          })
        }else if(this.checkHeight && this.height) {
          // 
          const that = this
          imgWidthHeightCheck = new Promise(function(resolve, reject) {
            let _URL = window.URL || window.webkitURL
            let img = new Image();
            img.onload = function() {
              let valid = img.height == that.height
              valid ? resolve() : reject()
            }
            img.src = _URL.createObjectURL(file)
          }).then(() => {
            return file
          }, () => {
            this.$message.warning(`: ${that.height}${sizeValiTipStr}JPGPNG`)
            return Promise.reject()
          })
        }
        if((this.checkWidth && this.width && this.checkHeight && this.height) || (this.checkWidth && this.width) || (this.checkHeight && this.height)) {
          return isPic && isJPG && isSizeNum && imgWidthHeightCheck
        }else {
          return isPic && isJPG && isSizeNum
        }
      },
      emitUpload (e) {
        if(this.maxImgsNum !== 1) {
          if(this.imageList.length > this.maxImgsNum) {
            this.$message.warning(`${this.maxImgsNum}`)
            return false
          }
        }
        this.imageUploadLoading = true
        this.getToken().then((res) => {
          // 
          let uploader = qiniu.upload(e.file, res.fileName, res.token)
          uploader.subscribe({
            error: () => {
              this.imageUploadLoading = false
            },
            complete: (res) => {
              this.imageUploadLoading = false
              const urlImage = `http://retailimg01.benlailife.com/${res.key}`
              this.imageList[this.currentUpFileIdx].imgUrl = urlImage
              let newImageList = this.imageList.filter(item => {
                return item.imgUrl && item.imgUrl != ''
              })
              // 
              this.$emit("setImageList", {
                imageList: newImageList
              })
              if(this.maxImgsNum > 1 && newImageList.length < this.maxImgsNum) {
                newImageList.push({
                  index: this.currentUpFileIdx + 1,
                  imgUrl: ''
                })
              }
              this.imageList = newImageList
            },
            next: res => {
            }
          })
        })
      },
      /**
       * [getToken token]
       * @param  {Function} callback [description]
       * @return {[type]}            [description]
       */
      getToken () {
        return this.$http.post(fileUrlList.getQiNiuToken, {}).then(res => {
          if (res.code == 0) {
            let token = res.data.token
            let fileName = res.data.key
            return {fileName, token}
          }
          throw new Error(res.msg)
        }).catch(e => {
          this.imageUploadLoading = false
          this.$notify.error({
            title: '',
            message: e.message
          })
        })
      },
      deleteUpFile(idx) {
        if(this.imageUploadLoading) return
        this.imageList.splice(idx, 1)
        let newImageList = this.imageList.filter(item => {
          return item.imgUrl && item.imgUrl != ''
        })
        // 
        this.$emit("setImageList", {
          imageList: newImageList
        })
        newImageList.push({
          index: newImageList.length,
          imgUrl: ''
        })
        newImageList.forEach((item, index) => {
          item.index = index
        })
        this.imageList = newImageList
      }
    }
  }
</script>


