<style>
  .c-g-sfw {
    padding-top: 0 !important;
    padding-bottom: 0 !important;
  }
  .switch-cp{
    margin: 0 10px 10px;
  }
</style>

<template>
  <div>
    <el-dialog :title="title" :visible.sync="isShow" width='540px'>
      <div class="switch-cp" v-if="online && !showData">
        <el-switch
          v-model="chooseNCateTOF"
          active-color="#409EFF"
          inactive-color="#F56C6C"
          active-text=""
          inactive-text="">
        </el-switch>
      </div>
      <div class="switch-cp" v-else-if="!showData">
        <el-switch
          v-model="chooseFCateTOF"
          active-color="#409EFF"
          inactive-color="#F56C6C"
          active-text=""
          inactive-text="">
        </el-switch>
      </div>
      <div class="search-form-wraper c-g-sfw" v-if="!showData">
        <el-form :model="searchContent" label-position="top" @submit.native.prevent>
          <el-form-item>
            <el-input
              class="search-input"
              placeholder="/"
              v-model="searchContent.searchValue"
              @keyup.enter.native="getCateList"
              clearable
              size="small">
            </el-input>
          </el-form-item>
          <el-form-item>
            <el-button type="primary" @click="getCateList" size="mini"></el-button>
          </el-form-item>
        </el-form>
      </div>
      <div>
        <el-table
          class="fixed-height"
          ref="multipleTable"
          v-loading="loading"
          :data="tableData"
          empty-text=""
          style="width:100%;"
          height="270"
          @select="handleSelect"
          @select-all="handleSelectAll"
          size="small">
          <el-table-column
            type="selection"
            width="50" 
            v-if="!showData">
          </el-table-column>
          <el-table-column v-if="online" min-width="100" prop="categoryId" label=""></el-table-column>
          <el-table-column v-else min-width="100" prop="categoryNo" label=""></el-table-column>
          <el-table-column min-width="100" prop="categoryName" label=""></el-table-column>
        </el-table>
      </div>
      <div slot="footer" class="dialog-footer" v-if="!showData">
        <el-button @click="isShow = false" size="mini"> </el-button>
        <el-button type="primary" @click="confirmClose" size="mini" :disabled="checkedList.length === 0"> </el-button>
      </div>
    </el-dialog>
  </div>
</template>

<script>
  import * as utils from '@/utils/utils'
  const reqUrl = {
    // 
    selectCategoryList: '/plateform/a/baseCommon/category/selectCategoryList',
    // 
    categoryList: '/foundation/category/list'
  }
  export default {
    data() {
      return {
        isShow: false,
        title: '', // title
        loading: false,
        online: true,
        chooseNCateTOF: true,
        chooseFCateTOF: true,
        searchContent: {
          searchValue: ''
        },
        tableData: [],
        // 
        tableDataTemp: [],
        // 
        checkedList: [],
        showData: false
      }
    },
    methods: {
      /**
       * [setDefault ]
       * @param {[type]} _val [description]
       */
      setDefault(_val, _param) {
        if(_param === 'show') {
          this.title = _val.title
          this.showData = true
          this.tableData = JSON.parse(JSON.stringify(_val.checkedList)) 
          this.isShow = true
        }else{
          this.online = _param
          this.showData = false
          if(this.online === true) {
            this.chooseNCateTOF = _val.chooseNCateTOF
          }else{
            this.chooseFCateTOF = _val.chooseFCateTOF
          }
          if(_val) {
            this.title = _val.title
            this.checkedList = JSON.parse(JSON.stringify(_val.checkedList)) 
          }
          this.isShow = true
          this.doSearchHandel(true)
        }
      },
      /**
       * [doSearchHandel ]
       * @param  {[type]} _param [description]
       * @return {[type]}         [description]
       */
      doSearchHandel(_param) {
        this.loading = true
        if(this.online) {
          this.$http.post(reqUrl.selectCategoryList).then(res => {
            this.loading = false
            if(res.code === 0) {
              // 
              if(res.data && res.data.length) {
                let result = res.data
                result.forEach(item => {
                  item.categoryNo = item.categoryId.toString()
                })
                this.tableData = result
                this.tableDataTemp = result      
              }else {
                this.tableData = []
                this.tableDataTemp = []
              }
              this.defauleSelectFun()
              return 
            }
            throw new Error(res.msg)
          }).catch(e => {
            this.loading = false
            this.$notify.error({
              title: '',
              message: e.message
            })
          })
        }else{
          this.$http.get(reqUrl.categoryList).then(res => {
            this.loading = false
            if(res.code === 0) {
              // 
              if(res.data && res.data[0].childrenList && res.data[0].childrenList.length) {
                let result = res.data[0].childrenList
                result.forEach(item => {
                  delete item['childrenList']
                })
                this.tableData = result
                this.tableDataTemp = result      
              }else {
                this.tableData = []
                this.tableDataTemp = []
              }
              this.defauleSelectFun()
              return 
            }
            throw new Error(res.msg)
          }).catch(e => {
            this.loading = false
            this.$notify.error({
              title: '',
              message: e.message
            })
          })
        }
      },
      /**
       * [getCateList ]
       * @return {[type]} [description]
       */
      getCateList() {
        let searchValue = this.searchContent.searchValue
        if(!searchValue) {
          this.tableData = this.tableDataTemp
          this.defauleSelectFun()
          return
        }
        this.tableData = this.tableDataTemp.filter(item => {
          return item.categoryNo.toLowerCase() === searchValue.toLowerCase() || item.categoryName.indexOf(searchValue) > -1
        })
        return
        this.defauleSelectFun()
      },
      /**
       * [handleSelect ]
       * @return {[type]} [description]
       */
      handleSelect(val, row) {
        let hash = {}
        let checkedListTemp = []

        if(utils.isKeyInArrayBykey(row.categoryNo, 'categoryNo', this.checkedList)) {
          this.checkedList = utils.removeItemByKeyVal('categoryNo', row.categoryNo, this.checkedList)
          return
        }else {
          checkedListTemp.push(row)
        }

        let checkedListTempArr = [...checkedListTemp, ...this.checkedList]
        this.checkedList = checkedListTempArr.reduceRight((item, next) => {
          hash[next.categoryNo] ? '' : hash[next.categoryNo] = true && item.push(next)
          return item
        }, [])
      },
      /**
       * [handleSelectAll ]
       * @return {[type]} [description]
       */
      handleSelectAll(val) {
        let hash = {}
        if(val.length > 0) {        
          // 
          for(let item of val) {
            this.checkedList.push(item)
          }
          // 
          this.checkedList = this.checkedList.reduceRight((item, next) => {
            hash[next.categoryNo] ? '' : hash[next.categoryNo] = true && item.push(next)
            return item
          }, [])
        }else { 
          //  val.length<=0
          // 1.tableid
          let checkedListTemp = []
          for(let item of this.tableData) {
            checkedListTemp.push(item)
          }
          // 2.
          let checkedListArr = utils.getSubtract(this.checkedList, checkedListTemp, 'categoryNo')
          this.checkedList = checkedListArr && checkedListArr.length > 0 ? checkedListArr : [] 
        }
      },
      /**
       * [defauleSelectFun ]
       * @param    {[type]}                 row [description]
       * @return   {[type]}                     [description]
       */
      defauleSelectFun() {
        let _this = this
        let rows = []
        for(let item of this.tableData) {
          for(let key of this.checkedList) {
            if(item.categoryNo === key.categoryNo) {
              rows.push(item)
            }
          }            
        }
        if (rows.length > 0) {
          this.$refs.multipleTable.clearSelection()
          setTimeout(function() {
            rows.forEach(rowItem => {
              _this.$refs.multipleTable.toggleRowSelection(rowItem)
            });
          }, 100)
        } else {
          this.$refs.multipleTable.clearSelection()
        }
      },
      /**
       * [confirmClose ]
       * @return {[type]} [description]
       */
      confirmClose() {
        let _params = {}
        if(this.online) {
          _params = {
            chooseNCateTOF: this.chooseNCateTOF,
            checkedOnlineList: this.checkedList
          }
        }else{
          _params = {
            chooseFCateTOF: this.chooseFCateTOF,
            checkedOfflineList: this.checkedList
          }
        }
        this.$emit("confirmChooseCate", _params)
        this.isShow = false
      }

    }
  }
</script>



