<style>
.p-t {
  margin-bottom: 10px;
}

.u-btn-wrap {
  display: flex;
  flex-direction: row;
  flex-wrap: nowrap;
}

.file-name-wrap {
  width: 200px;
  height: 32px;
  line-height: 32px;
  overflow: hidden;
  border: 1px solid #ececec;
  border-radius: 4px;
  padding: 0 10px;
  position: relative;
  top: 5px;
}

.label-wrap {
  border: 1px solid #ececec;
  border-radius: 4px;
  position: relative;
  top: 5px;
  height: 32px;
  line-height: 32px;
}

.label-wrap > .excel-file-label {
  line-height: 32px;
  cursor: pointer;
  padding: 7px 10px;
}

.label-wrap > .excel-file-label:hover {
  color: #fff;
  background-color: #66b1ff;
  border-radius: 4px;
}
</style>

<template>
  <div>
    <el-dialog 
      :title="title" 
      :visible.sync="isShow" 
      :close-on-click-modal="false"
      :close-on-press-escape="false"
      width='600px'>
      <el-steps :active="activeNum" align-center>
        <el-step title="" icon="el-icon-upload2"></el-step>
        <el-step title="" icon="el-icon-circle-check"></el-step>
      </el-steps>
      <!--  -->
      <div v-show="activeNum === 1" style="margin-top: 30px;">
        <el-form ref="form" label-width="80px">
          <el-form-item label="">
            <div class="excel-u-d-wrap">
              <div class="u-btn-wrap">
                <div class="file-name-wrap">{{ file.name }}</div>
                <div class="label-wrap">
                  <label for="excelFile" class="excel-file-label">
                    <input type="file" id="excelFile" hidden @change="handleFileSelect"/>
                    <span class="u-txt"></span>
                  </label>
                </div>
              </div>
              <div class="d-btn-wrap">
                <span>EXCEL</span>
                <el-button type="text" @click="downloadModel" size="mini" :disabled="isDisabled"></el-button>
              </div>
            </div>
          </el-form-item>
        </el-form>
      </div>
      <!--  -->
      <div v-show="activeNum === 2">
        <el-table
          class="fixed-height"
          :data="tableData"
          empty-text=""
          style="width:100%;"
          max-height="350"
          size="small">
          <el-table-column min-width="80px" prop="goodsNo" label="SKU"></el-table-column>
          <el-table-column min-width="120px" prop="platformCityGoodsName" label=""></el-table-column>
          <el-table-column min-width="70px" prop="presalePrice" label=""></el-table-column>
          <el-table-column v-if="platformId === 5" min-width="140px" prop="vendorType" :formatter="vendorTypeFormatter" label=""></el-table-column>
          <el-table-column min-width="120px" label="">
            <template slot-scope="scope" size="small">
              <div>
                <p v-if="scope.row.msgType === 1" style="color: #F56C6C;">{{scope.row.msg}}</p>
                <p v-else style="color: #67C23A;">{{scope.row.msg}}</p>
              </div>
            </template>
          </el-table-column>
        </el-table>
        <div style="margin-top: 10px;">
          <p><span style="color: #67C23A;">{{goodsCount}}</span>&nbsp;&nbsp;&nbsp;&nbsp;<span style="color: #F56C6C;">{{expGoodsCount}}</span></p>
          <p></p>
        </div>
      </div>

      <div slot="footer" class="dialog-footer" v-if="activeNum === 1">
        <el-button @click="isShow = false" size="mini"></el-button>
        <el-button type="primary" @click="doNextStepHandel" size="mini" :disabled="!file.name || conDisabled" :loading="saveLoading"></el-button>
      </div>
      <div slot="footer" class="dialog-footer" v-else-if="activeNum === 2">
        <el-button @click="isShow = false" size="mini"></el-button>
        <el-button type="primary" @click="doConfirmHandel" size="mini" :loading="saveLoading" :disabled="goodsExcelReqs.length === 0 || conDisabled"></el-button>
      </div>
    </el-dialog>

  </div>
</template>

<script>
import Storage from '@/utils/localStorage'
import * as utils from '@/utils/utils'
import exportSheet from '@/utils/exportExcel'

const reqUrls = {
  // 
  checkBatchInsert: '/plateform/a/platformCityGoods/presale/checkBatchInsert',
  // 
  batchInsert: '/plateform/a/platformCityGoods/presale/batchInsert'
}
export default {
  data () {
    return {
      conDisabled: false, // 
      isDisabled: false, // 
      saveLoading: false,
      title: '',
      isShow: false,
      regionCode: '',
      DIFFCITY: false,
      file: {name: ''}, // 
      // 
      tempConfig: {},
      // 
      goodsExcelReqs: [],

      // 1:23
      saleType: '' ,
      platformId: '',

      // 
      activeNum: 1,
      // 
      tableData: [],
      expGoodsCount: '', // 
      // 
      goodsCount: '' 
    }
  },
  methods: {
    /**
     * [setDefaultMesg ]
     * @param {[type]} _params []
     */
    setDefaultMesg (_params) {
      this.isShow = true
      this.title = _params.title || ''
      this.saleType = _params.saleType || ''
      this.regionCode = _params.regionCode || ''
      this.DIFFCITY = _params.DIFFCITY || false
      if (_params.platformId) {
        this.platformId = _params.platformId
        // 1-2-3-4-5-
        let tempConfigObj = {}
        if(_params.platformId === 5) {
          tempConfigObj = {
            goodsNo: 'SKU',
            platformCityGoodsName: '30',
            presalePrice: '2',
            vendorTypeStr: 'or',
            bigImageUrl: 'url'
          }
        }else {
          tempConfigObj = {
            goodsNo: 'SKU',
            platformCityGoodsName: '30',
            presalePrice: '2',
            platformCategoryId: 'id'
          }
        }
        this.tempConfig = tempConfigObj
      }
      
      // 
      this.file = {name: ''}
      let excelFileDom = document.getElementById('excelFile')
      if(excelFileDom) {
        excelFileDom.value = ""
      }

      this.activeNum = 1
      this.tableData = []
      this.goodsExcelReqs = []
    },
    /**
     * [downloadModel ]
     * @return {[type]} [description]
     */
    downloadModel () {
      // 1-2-3-4-5-
      let platformName = ''
      if(this.platformId === 1) {
        platformName = ''
      }else if(this.platformId === 2) {
        platformName = ''
      }else if(this.platformId === 3) {
        platformName = ''
      }else if(this.platformId === 4) {
        platformName = ''
      }else if(this.platformId === 5) {
        platformName = ''
      }

      let dataItem = {}
      if(this.platformId === 5) {
        dataItem = {
          goodsNo: '',
          platformCityGoodsName: '',
          presalePrice: '',
          vendorTypeStr: '',
          bigImageUrl: ''
        }
      }else {
        dataItem = {
          goodsNo: '',
          platformCityGoodsName: '',
          presalePrice: '',
          platformCategoryId: ''
        }
      }
      const tHeader = utils.objectToArray(this.tempConfig, false)
      const filterVal = utils.objectToArray(this.tempConfig, true)
      exportSheet.ExportJsonToExcel({
        header: tHeader,
        filterVal,
        data: [dataItem],
        filename: `${platformName}_`
      })
      this.$notify.success({
        title: '',
        message: `${platformName}_`
      })
      setTimeout(() => {
        this.isDisabled = false
      }, 500)
    },
    /**
     * [handleFileSelect ]
     * @param  {[type]} e [description]
     * @return {[type]}   [description]
     */
    handleFileSelect (e) {
      let salePriceReg = /((^[1-9]\d*)|^0)(\.\d{0,2}){0,1}$/
      // 
      if (!e.target.files.length) {
        return
      }

      this.file = e.target.files[0] || {name: ''}
      let idx = this.file.name.lastIndexOf(".")
      let strLength = this.file.name.length
      let postf = this.file.name.substring(idx, strLength) // 

      if (!this.file.name || postf != '.xlsx') {
        this.$notify.warning({
          title: '',
          message: 'excel'
        })
        // 
        this.file = {name: ''}
        document.getElementById('excelFile').value = ""
        return
      }

      let items = []
      let parseConfig = {}
      for (let key in this.tempConfig) {
        parseConfig[this.tempConfig[key]] = key
      }
      items = exportSheet.parseToJSON(this.file, parseConfig)
      items.then((result) => {
        // 
        let resultVal = result.filter(item => !!item.goodsNo)
        let goodsExcelReqsArr = []
        for (let item of resultVal) {
          if(!item.goodsNo) {
            this.$notify.warning({
              title: '',
              message: `(${item.goodsNo || '-'}_${item.platformCityGoodsName || '-'})SKU`
            })
            // 
            this.file = {name: ''}
            document.getElementById('excelFile').value = ""
            return
          }
          if(!item.platformCityGoodsName) {
            this.$notify.warning({
              title: '',
              message: `(${item.goodsNo || '-'}_${item.platformCityGoodsName || '-'})`
            })
            // 
            this.file = {name: ''}
            document.getElementById('excelFile').value = ""
            return
          }else {
            if(item.platformCityGoodsName.length > 30) {
              this.$notify.warning({
                title: '',
                message: `(${item.goodsNo || '-'}_${item.platformCityGoodsName || '-'})30`
              })
              // 
              this.file = {name: ''}
              document.getElementById('excelFile').value = ""
            }
          }
          if(!item.presalePrice) {
            this.$notify.warning({
              title: '',
              message: `(${item.goodsNo || '-'}_${item.platformCityGoodsName || '-'})`
            })
            // 
            this.file = {name: ''}
            document.getElementById('excelFile').value = ""
            return
          }else {
            if(!salePriceReg.test(item.presalePrice)) {
              this.$notify.warning({
                title: '',
                message: `(${item.goodsNo || '-'}_${item.platformCityGoodsName || '-'})02`
              })
              // 
              this.file = {name: ''}
              document.getElementById('excelFile').value = ""
              return
            }
          }
          if(this.platformId === 5) {
            // 
            if(!item.vendorTypeStr) {
              this.$notify.warning({
                title: '',
                message: `(${item.goodsNo || '-'}_${item.platformCityGoodsName || '-'})`
              })
              // 
              this.file = {name: ''}
              document.getElementById('excelFile').value = ""
              return
            }else {
              item.vendorType = item.vendorTypeStr === '' ? 1 : item.vendorTypeStr === '' ? 2 : ''
            }
          }else {
            // 
            if(!item.platformCategoryId) {
              this.$notify.warning({
                title: '',
                message: `(${item.goodsNo || '-'}_${item.platformCityGoodsName || '-'})id`
              })
              // 
              this.file = {name: ''}
              document.getElementById('excelFile').value = ""
              return
            }
          }

          let goodsExcelReqsItem = {
            goodsNo: item.goodsNo,
            platformCityGoodsName: item.platformCityGoodsName,
            presalePrice: item.presalePrice
          }
          if(this.platformId === 5) {
            goodsExcelReqsItem.bigImageUrl = item.bigImageUrl
            goodsExcelReqsItem.vendorType = item.vendorType
            goodsExcelReqsItem.vendorTypeStr = item.vendorTypeStr
          }else {
            // 
            goodsExcelReqsItem.platformCategoryId = item.platformCategoryId
          }
          goodsExcelReqsArr.push(goodsExcelReqsItem)
        }
        this.goodsExcelReqs = goodsExcelReqsArr
        console.log("this.goodsExcelReqs:", this.goodsExcelReqs);
      }).catch()
    },
    /**
     * [doNextStepHandel ]
     * @return {[type]} [description]
     */
    doNextStepHandel () {
      this.conDisabled = true
      this.saveLoading = true
      this.$http.post(reqUrls.checkBatchInsert, {platformId: this.platformId, saleType: this.saleType, goodsExcelReqs: this.goodsExcelReqs, regionCode: this.regionCode, DIFFCITY: this.DIFFCITY}).then(res => {
        setTimeout(() => {
          this.conDisabled = false
        }, 800)
        this.saveLoading = false
        if(res.code === 0) {
          if(res.data && res.data.cityGoodsVos && res.data.cityGoodsVos.length) {
            this.goodsCount = res.data.goodsCount
            this.expGoodsCount = res.data.expGoodsCount
            this.tableData = res.data.cityGoodsVos
          }
          
          this.activeNum = parseInt(this.activeNum) + 1
          return
        }
        throw new Error(res.msg)
      }).catch(e => {
        this.conDisabled = false
        this.saveLoading = false
        this.$notify.error({
          title: '',
          message: e.message
        })
      })
    },

    /**
     * [doConfirmHandel ]
     * @return {[type]} [description]
     */
    doConfirmHandel() {
      let goodsExcelReqsArr = []
      let goodsExcelReqsArrItem = {}
      for(let item of this.tableData) {
        if(item.msgType === 0) {
          let goodsExcelReqsArrItem = {
            goodsNo: item.goodsNo,
            platformCityGoodsName: item.platformCityGoodsName,
            presalePrice: item.presalePrice
          }
          if(this.platformId === 5) {
            goodsExcelReqsArrItem.bigImageUrl = item.bigImageUrl
            goodsExcelReqsArrItem.vendorType = item.vendorType
          }else {
            // 
            goodsExcelReqsArrItem.platformCategoryId = item.platformCategoryId
          }
          goodsExcelReqsArr.push(goodsExcelReqsArrItem)
        }
      }
      if(goodsExcelReqsArr.length === 0) {
        this.$notify.warning({
          title: '',
          message: ``
        })
        return
      }

      this.conDisabled = true
      this.saveLoading = true
      this.$http.post(reqUrls.batchInsert, {platformId: this.platformId, saleType: this.saleType, goodsExcelReqs: goodsExcelReqsArr, regionCode: this.regionCode, DIFFCITY: this.DIFFCITY}).then(res => {
        setTimeout(() => {
          this.conDisabled = false
          this.saveLoading = false
        }, 800)
        if(res.code === 0) {
          this.$message.success("")
          this.isShow = false
          this.$emit("doSearchHandel")
          return
        }
        throw new Error(res.msg)
      }).catch(e => {
        this.conDisabled = false
        this.saveLoading = false
        this.$notify.error({
          title: '',
          message: e.message
        })
      })
    },
    vendorTypeFormatter(row, column, cellValue, index) {
      if(cellValue === 1) {
        return ''
      }else if(cellValue === 2) {
        return ''
      }else {
        return '-'
      }

    }
  }
}
</script>



