<style scoped>
  .store-cp-wraper {
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
  }
  .store-cp-item {
    position: relative;
    border: 1px solid #EBEEF5;
    border-radius: 6px;
    padding: 0 20px 0 12px;
    margin: 10px 15px 0 0;
    height: 36px;
    line-height: 36px;
    background: #EBEEF5;
  }
  .store-cp-item.t:hover {
    border: 1px solid #409EFF;
  }
  .store-cp-item.f:hover {
    border: 1px solid #F56C6C;
  }
  .del-store-icon-w {
    position: absolute;
    top: -8px;
    right: 2px;
    cursor: pointer;
  }
</style>
<template>
  <div>
    <div>
      <el-form-item label="">
        <el-button size="small" @click="chooseStoreOpen" icon="el-icon-plus"></el-button>
        <span v-if="storesCheckedList.length > 0 && chooseStoreTOF"></span>
        <span v-if="storesCheckedList.length > 0 && !chooseStoreTOF"></span>
        <div v-if="storesCheckedList.length > 0" class="store-cp-wraper">
          <div v-for="(item, idx) in storesCheckedList" :key="idx" class="store-cp-item" :class="chooseStoreTOF? 't' : 'f'">
            {{item.storeNo}}_{{item.storeName}}({{item.managementTypeValue}})
            <span class="del-store-icon-w" @click="deleteStoreItem(item)">
              <i class="el-icon-error"></i>
            </span>
          </div>
        </div>
      </el-form-item>
      <el-form-item label="" required>
        <el-select size="small" class="add-input" v-model.trim="addParamMesg.useType" placeholder="" clearable @change="changeStore">
          <el-option :label="item.label" :value="item.value" :key="item.value" v-for="item in useTypeList"></el-option>
        </el-select>
      </el-form-item>
      <el-form-item label="" v-if="addParamMesg.useType === 1">
        <el-date-picker
          class="add-input"
          v-model="addParamMesg.useBeginTime"
          type="date"
          placeholder=""
          :picker-options="setDisableTime"
          size="small"
          @change="changeStore">
        </el-date-picker>
      </el-form-item>
      <el-form-item label="" v-if="addParamMesg.useType === 1">
        <el-date-picker
          class="add-input"
          v-model="addParamMesg.useEndTime"
          type="date"
          placeholder=""
          :picker-options="setDisableTime"
          size="small"
          @change="changeStore">
        </el-date-picker>
      </el-form-item>
      <el-form-item label="" v-if="addParamMesg.useType === 2">
        <el-input class="add-input" v-model="addParamMesg.useDayCount" placeholder="" size="small" @change="changeStore">
          <template slot="append"></template>
        </el-input>
        <small></small>
      </el-form-item>
    </div>
    
    <!--  -->
    <div>
      <choose-store ref="chooseStore" @confirmChooseStore="confirmChooseStore"></choose-store>
    </div>
  </div>
</template>
<script>
  import * as utils from '@/utils/utils'
  import ChooseStore from "../Dialog/ChooseStore"

  export default {
    data() {
      return {
        addParamMesg: {
          useBeginTime: '',
          useEndTime: '',
          useDayCount: '',
          // 
          useType: '' // 
        },
        // 
        useTypeList: [
          {
            value: 1,
            label: ''
          },
          {
            value: 2,
            label: ''
          }
        ],
        // 
        storesCheckedList: [],
        chooseStoreTOF: true, // 
        setDisableTime: {
          disabledDate(time) {
            return time.getTime() < Date.now() - 8.64e7;
          }
        }
      }
    },
    created() {

    },
    mounted() {
      
    },
    components: {
      "choose-store": ChooseStore
    },
    methods: {
      /**
       * [chooseStoreOpen ]
       * @return {[type]} [description]
       */
      chooseStoreOpen() {
        let _params = {
          title: '',
          chooseStoreTOF: this.chooseStoreTOF,
          checkedList: this.storesCheckedList
        }
        this.$refs.chooseStore.setDefault(_params)
      },
      /**
       * [confirmChooseStore ]
       * @param  {[type]} _value [description]
       * @return {[type]}        [description]
       */
      confirmChooseStore(_value) {
        this.chooseStoreTOF = _value.chooseStoreTOF
        if(_value.checkedList && _value.checkedList.length) {
          this.storesCheckedList = _value.checkedList
        }else {
          this.storesCheckedList = []
        }
        this.changeStore()
      },
      deleteStoreItem(_row) {
        let storeNo = _row.storeNo
        this.storesCheckedList = this.storesCheckedList.filter(item => {
          return item.storeNo !== storeNo
        })
        this.changeStore()
      },
      changeStore() {
        //  
        let chooseStoreNo = []
        this.storesCheckedList.forEach(item => {
          chooseStoreNo.push(item.storeNo)
        })
        if(this.chooseStoreTOF === true) {
          this.addParamMesg.storeNo = chooseStoreNo.join(';')
          this.addParamMesg.excludeStoreNo = null
        }else if(this.chooseStoreTOF === false) {
          this.addParamMesg.storeNo = null
          this.addParamMesg.excludeStoreNo = chooseStoreNo.join(';')
        }
        // 
        this.$emit("changeStore", this.addParamMesg)
      }
    }
  }

</script>


