<style>
  .ll-p-m-w {
    display: flex;
    flex-direction: row;
    flex-wrap: nowrap;
  }
  .ll-img-w {
    width: 60px;
    height: 60px;
  }
  .ll-img-w>img {
    width: 60px;
  }
  .ll-p-m-c-r {
    padding: 0 10px;
  }
  .ll-erp-p-no {
    color: #666;
    font-size: 12px;
  }
  .c-g-sfw {
    padding-top: 0 !important;
    padding-bottom: 0 !important;
  }
</style>

<template>
  <div>
    <el-dialog 
      :close-on-press-escape="false"
      :close-on-click-modal="false"
      :title="title" 
      :visible.sync="isShow" 
      width='860px'>
      <div class="search-form-wraper c-g-sfw">
        <el-form :model="searchContent" label-position="top">
          <el-form-item>
            <el-cascader
              placeholder=""
              class="search-input"
              :options="categoryList"
              change-on-select
              size="small"
              clearable
              filterable
              @change="doCategorysChangeHandel"
              :props="cateDefaultProps"
              v-model="searchContent.categoryIdList">
            </el-cascader>
          </el-form-item>
          <el-form-item>
            <goods-select-for-dialog ref="GoodsSelectForDialog" @DoSelectGoodsHandel="DoSelectGoodsHandel"></goods-select-for-dialog>
          </el-form-item>
          <el-form-item>
            <el-button type="primary" @click="doSearchHandel(true)" size="mini"></el-button>
          </el-form-item>
        </el-form>
      </div>
      
      <div>
        <el-table
          ref="multipleTable"
          v-loading="loading"
          :data="tableData"
          empty-text=""
          style="width:100%;"
          height="400"
          size="small">
          <el-table-column min-width="226px" label="">
            <template slot-scope="scope">
              <div class="ll-p-m-w">
                <div class="ll-img-w">
                  <img v-if="scope.row.imgUrl && scope.row.imgUrl != ''" :src="scope.row.imgUrl" alt="" onerror="onerror=null;src='/shop/static/img_error.png'">
                  <img v-else src="/shop/static/img_error.png" alt="">
                </div>
                <div class="ll-p-m-c-r">
                  <div>{{scope.row.goodsName || '-'}}</div>
                  <div class="ll-erp-p-no">SKU{{scope.row.goodsNo || '--'}}</div>
                  <div class="ll-erp-p-no">ID{{scope.row.platformCityGoodsId || '--'}}</div>
                </div>
              </div>
            </template>     
          </el-table-column>
          <el-table-column min-width="100px" prop="spec" label=""></el-table-column>
          <el-table-column min-width="90px" prop="presalePrice" label=""></el-table-column>
          <el-table-column min-width="180px" label="">
            <template slot-scope="scope">
              <div>
                <p>{{scope.row.presaleStartDate}} {{scope.row.presaleStartTime}}</p>
                <p></p>
                <p>{{scope.row.presaleEndDate}} {{scope.row.presaleEndTime}}</p>
              </div>
            </template>
          </el-table-column>
          <el-table-column min-width="90px" prop="activityStatus" label="" :formatter="activityStatusFormatter"></el-table-column>
          <el-table-column
            fixed="right"
            label=""
            width="100">
            <template slot-scope="scope" size="small">
              <el-button @click="checkHandel(scope.row)" type="primary" size="mini" icon="el-icon-check"></el-button>
            </template>
          </el-table-column>
        </el-table>
        <!--  -->
        <el-pagination
          v-show="tableData.length>0"
          class="tc mt20 mb20"
          background
          @size-change="changeSize"
          @current-change="changeCurrent"
          :current-page.sync="pageNum"
          :page-sizes="[10, 50, 100, 150, 200]"
          :pager-count="5"
          layout="total, sizes, prev, pager, next, jumper"
          :total="total">
        </el-pagination>
      </div>
    </el-dialog>
  </div>
</template>

<script>
  import * as utils from '@/utils/utils'
  import GoodsSelect from "@/components/Common/GoodsCommon/SearchByRegion"
  const presaleGoodsMgt = {
    // 
    getCategoryTree: '/plateform/a/category/getCategoryTree',
    // 
    selectPageGoodsList: '/plateform/a/mall/pageTemp/selectPageGoodsList'
  }
  export default {
    data() {
      return {
        isShow: false,
        title: '', // title
        loading: false,
        pageNum: 1,
        pageSize: 10,
        total: 0,
        searchContent: {
          DIFFCITY: true,
          regionCode: '',
          isAdvance: 0, // (null,0:,1:,2:,3:)
          queryString: '', // 
          platformCityGoodsIds: [], // ID
          platformId: 5,
          saleType: 4, // 123:4()
          categoryIdList: [],
          categoryId: '' // ID
        },
        tableData: [],
        // 
        cateDefaultProps: {
          children: 'categoryVoList',
          label: 'categoryName',
          value: 'categoryId'
        },
        categoryList: [] // id
      }
    },
    components: {
      "goods-select-for-dialog": GoodsSelect
    },
    methods: {
      /**
       * [setDefault ]
       * @param {[type]} _val [description]
       */
      setDefault(_val) {
        this.title = _val.title || ''
        this.searchContent.regionCode = _val.regionCode || ''
        this.searchContent.isAdvance = _val.isAdvance || 0
        this.isShow = true
        // 
        this.getCategoryId()
        this.doSearchHandel(true)
        // SKU
        setTimeout(() => {
          this.goodsSelectForDialogInit()
        }, 500)
      },
      /**
       * [getCategoryId ]
       * @return {[type]} [description]
       */
      getCategoryId() {
        this.$http.post(presaleGoodsMgt.getCategoryTree, {DIFFCITY: true}).then(res => {
          if(res.code === 0) {
            if(res.data && res.data.length) {
              for(let item of res.data) {
                if(item.categoryVoList && item.categoryVoList.length === 0) {
                  item.categoryVoList = null
                }
              }
              this.categoryList = res.data
            }else {
              this.categoryList = []
            }
            return 
          }
          throw new Error(res.msg)
        }).catch(e => {
          console.log("():", e.message)
        })
      },
      /**
       * [changeSize description]
       * @param    {[type]}                 value [description]
       * @return   {[type]}                       [description]
       */
      changeSize (value) {
        this.pageSize = value
        this.doSearchHandel()
      },
      /**
       * [changeCurrent ]
       * @param    {[type]}                 value []
       * @return   {[type]}                       [description]
       */
      changeCurrent (value) {
        this.pageNum = value
        this.doSearchHandel()
      },
      /**
       * [doSearchHandel ]
       * @param  {[type]} _param [description]
       * @return {[type]}         [description]
       */
      doSearchHandel(_param) {
        let spliceLen = this.searchContent.categoryIdList.length - 1
        this.searchContent.categoryId = this.searchContent.categoryIdList[spliceLen]
        this.loading = true
        if(_param === true) {
          this.pageNum = 1
        }
        this.$http.post(presaleGoodsMgt.selectPageGoodsList, { ...this.searchContent, pageNum: this.pageNum, pageSize: this.pageSize }).then(res => {
          this.loading = false
          if(res.code === 0) {
            // 
            this.tableData = res.data && res.data.result && res.data.result.length ? res.data.result : []
            this.total = res.data.total
            return 
          }
          throw new Error(res.msg)
        }).catch(e => {
          this.loading = false
          this.$notify.error({
            title: '',
            message: e.message
          })
        })
      },
      /**
       * [confirmClose ]
       * @return {[type]} [description]
       */
      checkHandel(_row) {
        this.$emit("confirmChooseGoods", _row)
        this.isShow = false
      },

      /**
       * [doCategorysChangeHandel ]
       * @return {[type]} [description]
       */
      doCategorysChangeHandel() {
        // 
        this.goodsSelectForDialogInit()

        this.doSearchHandel(true)
      },
      /**
       * [goodsSelectForDialogInit ]
       * @return {[type]} [description]
       */
      goodsSelectForDialogInit() {
        if(this.$refs.GoodsSelectForDialog) {
          let _val = {
            queryString: this.searchContent.queryString,
            categoryIdList: this.searchContent.categoryIdList,
            saleType: this.searchContent.saleType,
            platformId: this.searchContent.platformId,
            platformCityGoodsIds: this.searchContent.platformCityGoodsIds
          }
          this.$refs.GoodsSelectForDialog.setDefaultMesg(_val)
        }
      },
      /**
       * [DoSelectGoodsHandel ]
       */
      DoSelectGoodsHandel(_params) {
        let checkedList = _params.checkedList
        let queryString = _params.queryString
        let platformCityGoodsIds = []
        for(let item of checkedList) {
          platformCityGoodsIds.push(item.platformCityGoodsId)
        }
        this.searchContent.platformCityGoodsIds = platformCityGoodsIds
        this.searchContent.queryString = queryString
        this.doSearchHandel(true)
      },
      /**
       * [activityStatusFormatter ]
       * @return {[type]} [description]
       */
       activityStatusFormatter(row, column, cellValue, index) {
        if(cellValue === 0) {
          return ''
        }else if(cellValue === 1) {
          return ''
        }else if(cellValue === 2) {
          return ''
        }else if(cellValue === 3) {
          return ''
        }
      }

    }
  }
</script>


