<template>
  <div class="goods-select">
    <el-popover
      placement="bottom-start"
      width="390"
      trigger="hover"
      v-model="isShow">
      <div>
        <el-table
          v-loading="loading"
          ref="multipleTable"
          :data="tableData"
          max-height="240"
          empty-text=""
          size="small"
          style="width: 100%"
          @select="handleSelect"
          @select-all="handleSelectAll">
          <el-table-column
            align="center"
            type="selection">
          </el-table-column>
          <el-table-column
            prop="goodsNo"
            label="SKU"
            width="90">
          </el-table-column>
          <el-table-column
            prop="platformCityGoodsName"
            label="">
          </el-table-column>
        </el-table>
      </div>
      <div style="text-align: right; margin-top: 10px;">
        <el-button size="mini" @click="isShow = false"></el-button>
        <el-button type="primary" size="mini" @click="doConfirmHandel"></el-button>
      </div>
      <el-input 
        slot="reference" 
        class="search-input" 
        placeholder="SKU/" 
        size="small" 
        v-model.trim="queryString" 
       >
       </el-input>
    </el-popover>
  </div>
</template>

<script>
  import * as utils from '@/utils/utils'
  import Storage from '@/utils/localStorage'
  const reqUrl = {
    // -
    selectGoods: '/plateform/a/platformCityGoods/presale/selectCityGoods'
  }
  // 
  const debounce = (function() {
    let timer = null;
    return function(fun, wait) {
      timer && clearTimeout(timer);
      timer = setTimeout(fun, wait);
    };
  })();
  export default {
    data() {
      return {
        loading: false,

        queryString: '',
        categoryIdList: [],
        categoryId: '',
        platformId: '',
        saleType: '',

        tableData: [],
        isShow: false,

        platformCityGoodsIds: [],
        // 
        checkedList: [],

        regionCode: Storage.get("regionCode"),
        searchSupplierGoods: 1 // 0-1-
      }
    },
    watch: {
      queryString(newVal, oldVal) {
        if(newVal && newVal !== oldVal) {
          debounce(() => {
            this.doRequestHandel()
          }, 300)
        }
      }
    },
    methods: {
      /**
       * [setDefaultMesg ]
       * @param {[type]} _val [description]
       */
      setDefaultMesg(_val) {
        this.platformId = _val.platformId ? _val.platformId : ''
        this.saleType = _val.saleType ? _val.saleType : ''
        this.platformCityGoodsIds = _val.platformCityGoodsIds ? _val.platformCityGoodsIds : []
        this.searchSupplierGoods = _val.searchSupplierGoods === 0 ? 0 : 1
        if(_val.categoryIdList) {
          this.categoryIdList = _val.categoryIdList
          this.categoryId = this.categoryIdList[this.categoryIdList.length - 1]
        }else {
          this.categoryIdList = []
          this.categoryId = ''
        }

        if(_val.queryString) {
          this.queryString = _val.queryString
          this.doRequestHandel()
        }
      },
      doRequestHandel() {
        this.$nextTick(() => {
          if(this.queryString){
            this.loading = true
            let params = {
              DIFFCITY: true,
              regionCode: this.regionCode,
              categoryId: this.categoryId || '',
              categoryIdList: this.categoryIdList,
              platformId: this.platformId,
              saleType: this.saleType,
              searchValue: this.queryString.replace(/\s+/g,""),
              searchSupplierGoods: this.searchSupplierGoods
            }
            this.$http.post(reqUrl.selectGoods, params).then(res => {
              this.loading = false
              if(res.code === 0) {
                if(res.data && res.data.length) {
                  this.tableData = res.data
                }else {
                  this.tableData = []
                }

                this.defauleSelectFun()
                return
              }
              throw new Error(res.msg)
            }).catch(e => {
              this.loading = false
              console.log("()", e.message)
            })
          }
        }) 
      },
      /**
       * [handleSelect ]
       * @return {[type]} [description]
       */
      handleSelect(val, row) {
        let hash = {}
        let checkedListTemp = []

        if(utils.isKeyInArrayBykey(row.platformCityGoodsId, 'platformCityGoodsId', this.checkedList)) {
          this.checkedList = utils.removeItemByKeyVal('platformCityGoodsId', row.platformCityGoodsId, this.checkedList)
          return
        }else {
          checkedListTemp.push(row)
        }

        let checkedListTempArr = [...checkedListTemp, ...this.checkedList]
        this.checkedList = checkedListTempArr.reduceRight((item, next) => {
          hash[next.platformCityGoodsId] ? '' : hash[next.platformCityGoodsId] = true && item.push(next)
          return item
        }, [])
      },
      /**
       * [handleSelectAll ]
       * @return {[type]} [description]
       */
      handleSelectAll(val) {
        let hash = {}
        if(val.length > 0) {        
          // 
          for(let item of val) {
            this.checkedList.push(item)
          }
          // 
          this.checkedList = this.checkedList.reduceRight((item, next) => {
            hash[next.platformCityGoodsId] ? '' : hash[next.platformCityGoodsId] = true && item.push(next)
            return item
          }, [])
        }else { 
          //  val.length<=0
          // 1.tableid
          let checkedListTemp = []
          for(let item of this.tableData) {
            checkedListTemp.push(item)
          }
          // 2.
          let checkedListArr = utils.getSubtract(this.checkedList, checkedListTemp, 'platformCityGoodsId')
          this.checkedList = checkedListArr && checkedListArr.length > 0 ? checkedListArr : [] 
        }
      },
      /**
       * [defauleSelectFun ]
       * @param    {[type]}                 row [description]
       * @return   {[type]}                     [description]
       */
      defauleSelectFun() {
        let rows = []
        //   
        for(let i of this.tableData) {
          for(let j of this.platformCityGoodsIds) {
            if(i.platformCityGoodsId == j) {
              rows.push(i)
            }
          }            
        }

        // 
        this.checkedList = rows

        if(rows.length > 0) {
          this.$refs.multipleTable.clearSelection()
          setTimeout(() => {
            rows.forEach(rowItem => {
              this.$refs.multipleTable.toggleRowSelection(rowItem)
            });
          }, 100)
        } else {
          this.$refs.multipleTable.clearSelection()
        }
      },
      /**
       * [doConfirmHandel ]
       * @return {[type]} [description]
       */
      doConfirmHandel() {
        this.isShow = false
        let _value = {
          checkedList: this.checkedList,
          queryString: this.queryString
        }
        this.$emit("DoSelectGoodsHandel", _value)
      }
    }
  }
</script>


