import XLSX from 'xlsx'
import * as util from '@/utils/utils'

// jsexecl
function s2ab (s) {
  if (typeof ArrayBuffer !== 'undefined') {
    let buf = new ArrayBuffer(s.length)
    let view = new Uint8Array(buf)
    for (let i = 0; i !== s.length; ++i) {
      view[i] = s.charCodeAt(i) & 0xFF
    }
    return buf
  } else {
    let buf = new Array(s.length)
    for (let i = 0; i !== s.length; ++i) {
      buf[i] = s.charCodeAt(i) & 0xFF
    }
    return buf
  }
}

// 26[0-25] -> [A-Z]
function getCharCol (n) {
  let s = ''
  let m = 0
  while (n > 0) {
    m = n % 26 + 1
    s = String.fromCharCode(m + 64) + s
    n = (n - m) / 26
  }
  return s
}

function exportFileByConfig (json, fileName, exportConfig, extraData = [[]]) {
  if (exportConfig) {
    json = json.map((item, index) => {
      let newItem = {}
      for (let k of Object.keys(exportConfig)) {
        let value = item[k]
        if (value === true) {
          value = ''
        }
        if (value === false) {
          value = ''
        }
        if (value === null || value === undefined) {
          value = ''
        }
        newItem[exportConfig[k]] = value
      }
      return newItem
    })
  }
  createFile(json, fileName, extraData)
}

// exportFileByConfig
function exportFile (json, fileName, exportConfig, extraData = [[]]) {
  if (exportConfig) {
    json = json.map((item, index) => {
      let newItem = {}
      // 
      for (let k of Object.keys(item)) {
        if (!(k in exportConfig)) {
          // newItem[k] = item[k] === null ? '' : (item[k] === true ? '' : '')  //
          continue
        }
        let value = item[k]
        if (value === true) {
          value = ''
        }
        if (value === false) {
          value = ''
        }
        if (value === null || value === undefined) {
          value = ''
        }
        newItem[exportConfig[k]] = value
      }
      return newItem
    })
  }
  createFile(json, fileName, extraData)
}

/**
 *
 * @param json
 * @param fileName .xlsx
 * @param extraList [[label: '', value: ''],[label: '', value: ''],[label: '', value: '']]
 */
function createFile (json, fileName, extraList) {
  let _tmpdata = json[0]
  json.unshift({})
  var keyMap = [] // keys
  for (var k in _tmpdata) {
    keyMap.push(k)
    json[0][k] = k
  }
  let tmpdata = [] // json
  json.map((v, i) => keyMap.map((k, j) => Object.assign({}, {
    v: v[k],
    // position: (j > 25 ? getCharCol(j) : String.fromCharCode(65 + j)) + (i + 1 + extraList.length) // 
    position: (j > 25 ? getCharCol(j) : String.fromCharCode(65 + j)) + (i + extraList.length)
  }))).reduce((prev, next) => prev.concat(next)).forEach((v, i) => {
    tmpdata[v.position] = {
      v: v.v
    }
  })
  let temObj = {}
  for (let i = 0; i < extraList.length; i++) {
    let item = extraList[i]
    for (let j = 0; j < item.length; j++) {
      let key = String.fromCharCode(65 + i) + (j + 1)
      temObj[key] = {v: item[j].label + ':' + item[j].value}
    }
  }
  tmpdata = Object.assign(temObj, tmpdata)
  var outputPos = Object.keys(tmpdata) // ,A1D10
  var tmpWB = {
    SheetNames: ['mySheet'], // 
    Sheets: {
      'mySheet': Object.assign({},
        tmpdata, // 
        {
          '!ref': 'A1' + ':' + outputPos[outputPos.length - 1] // 
        })
    }
  }
  let wbout = XLSX.write(tmpWB, {bookType: 'xlsx', type: 'binary'})
  /* force a download */
  let tmpDown = new Blob([s2ab(wbout)], {type: 'application/octet-stream'})
  var href = URL.createObjectURL(tmpDown) // 
  var a = document.createElement('a')
  a.href = href // a
  a.download = fileName || `${util.formatDate(new Date())}.xlsx`
  document.body.appendChild(a)
  a.click() // 
  document.body.removeChild(a)
  setTimeout(function () { // 
    URL.revokeObjectURL(tmpDown) // URL.revokeObjectURL()object URL
  }, 100)
}

function toJSON (excelFile, config) {
  return new Promise((resolve, reject) => {
    if (window.FileReader) {
      let result = []
      let fr = new FileReader()
      fr.readAsBinaryString(excelFile)
      fr.onload = ev => {
        try {
          let data = ev.target.result
          // excel
          let workbook = XLSX.read(data, {
            type: 'binary'
          })
          // 
          for (let sheet of workbook.SheetNames) {
            if (workbook.Sheets[sheet]) {
              // field
              result = config ? result.concat(XLSX.utils.sheet_to_json(workbook.Sheets[sheet], config)) : result.concat(XLSX.utils.sheet_to_json(workbook.Sheets[sheet]))
            }
          }
          resolve(result)
        } catch (e) {
          return reject(new Error(""))
        }
      }
      return
    }
    reject(new Error(""))
  })
}

/*
 *  @excelFile File
 *  @config Object 
 *  @setter Object XLSX.utils.sheet_to_json
*/
function parseToJSON (excelFile, config, setter = {}) {
  return new Promise((resolve, reject) => {
    if (window.FileReader) {
      let result = []
      let fr = new FileReader()
      fr.readAsBinaryString(excelFile)
      fr.onload = ev => {
        try {
          let data = ev.target.result
          // excel
          let workbook = XLSX.read(data, {
            type: 'binary'
          })
          // 
          let name = workbook.SheetNames[0]
          let sheet = workbook.Sheets[name]
          if (sheet) {
            result = XLSX.utils.sheet_to_json(sheet, {defval: '', ...setter})
            if (config) {
              result.forEach(item => {
                for (let key in item) {
                  if (config[key]) {
                    item[config[key]] = item[key]
                    delete item[key]
                  }
                }
              })
            }
          }
          resolve(result)
        } catch (e) {
          reject(new Error(""))
        }
      }
      return
    }
    reject(new Error(""))
  })
}

export default {
  exportFile,
  exportFileByConfig,
  toJSON,
  parseToJSON
}




