<style>
@import "../../../assets/fileUpload.css";
.p-t {
  margin-bottom: 10px;
}
</style>

<template>
  <div>
    <el-dialog 
      :close-on-press-escape="false"
      :close-on-click-modal="false"
      :title="title" 
      :visible.sync="isShow" 
      width='450px'>
      <el-form ref="form" label-width="80px">
        <el-form-item label="">
          <el-select class="search-date-picker" v-model="storeNo" filterable placeholder="" clearable size="small">
            <el-option
              v-for="item in storeOptions"
              :key="item.value"
              :label="item.label"
              :value="item.value">
            </el-option>
          </el-select>
          <span></span>
        </el-form-item>
        <el-form-item label="">
          <div class="excel-u-d-wrap">
            <div class="u-btn-wrap">
              <div class="file-name-wrap">{{ file.name }}</div>
              <div class="label-wrap">
                <label for="excelFile" class="excel-file-label">
                  <input type="file" id="excelFile" hidden @change="handleFileSelect"/>
                  <span class="u-txt"></span>
                </label>
              </div>
            </div>
            <div class="d-btn-wrap">
              <span>EXCEL</span>
              <el-button type="text" @click="downloadModel" size="mini" :disabled="isDisabled"></el-button>
            </div>
          </div>
        </el-form-item>
      </el-form>


      <div slot="footer" class="dialog-footer">
        <el-button @click="isShow = false" size="mini"></el-button>
        <el-button type="primary" @click="doConfirmHandel" size="mini" :disabled="!file.name || conDisabled" :loading="saveLoading"></el-button>
      </div>
    </el-dialog>

  </div>
</template>

<script>
import * as utils from '@/utils/utils'
import exportSheet from '@/utils/exportSheet'
import Storage from '@/utils/localStorage'
const storePresaleGoods = {
  // 
  search: '/plateform/a/store/condition/search',
  // 
  searchTableData: '/plateform/a/product/store/presale/search',
  // excel
  excelUpdatePrice: '/plateform/a/product/store/presale/excelUpdatePrice',
  // excel
  excelUpdateStock: '/plateform/a/product/store/presale/excelUpdateStock'
}
export default {
  data () {
    return {
      conDisabled: false, // 
      isDisabled: false, // 
      saveLoading: false,
      title: '',
      isShow: false,
      formLabelWidth: '70px',
      file: {name: ''}, // 
      // 
      exportGoods: [],
      tempConfig: {
        platformName: '()',
        storeNo: '()',
        storeName: '()',
        presaleId: 'ID()',
        goodsNo: 'SKU()',
        productName: '()',
        spec: '()',
        stockNum: '()'
      },
      // 
      presaleList: [],
      // 123:4()
      saleType: '',
      platformId: '',
      // 
      storeNo: '',
      // 
      storeOptions: [],
      regionCode: '' // id

    }
  },
  methods: {
    /**
     * [setDefaultMesg ]
     * @param {[type]} _params []
     */
    setDefaultMesg (_params) {
      this.isShow = true
      if (_params.title) {
        this.title = _params.title
      }
      if (_params.saleType) {
        this.saleType = _params.saleType
      }
      if (_params.platformId) {
        this.platformId = _params.platformId
      }
      if (_params.storeNo) {
        this.storeNo = _params.storeNo
      }
      if (_params.regionCode) {
        this.regionCode = _params.regionCode
      }
      // 
      this.file = {name: ''}
      let excelFileDom = document.getElementById('excelFile')
      if(excelFileDom) {
        excelFileDom.value = ""
      }
      // 
      this.getStoreList()
    },
    /**
     * [getStoreList ]
     * @return {[type]} [description]
     */
    getStoreList() {
      let params = {
        regionCode: this.regionCode
      }
      params.DIFFCITY = this.regionCode ? true : false
      this.$http.post(storePresaleGoods.search, {...params}).then(res => {
        if(res.code === 0) {
          if(res.data && res.data.length) {
            let optionsArr = []
            for(let item of res.data) {
              let optionItem = {
                value: item.storeNo,
                label: item.storeNo +'-'+ item.storeName
              }
              optionsArr.push(optionItem)
            }

            this.storeOptions = optionsArr
          }else {
            this.storeOptions = []
          }
          return
        }
        throw new Error(res.msg)
      }).catch(e => {
        console.log("():", e.message)
      })
    },
    /**
     * [downloadModel ]
     * @return {[type]} [description]
     */
    downloadModel () {
      if(!this.storeNo) {
        this.$message("")
        return
      }
      this.isDisabled = true
      this.$http.post(storePresaleGoods.searchTableData, {platformId: this.platformId, saleType: this.saleType, storeNo: this.storeNo, limitFlag: false, DIFFCITY: true}).then(res => {
        setTimeout(() => {
          this.isDisabled = false
        }, 800)
        if(res.code === 0) {
          if(res.data && res.data.pageVo && res.data.pageVo.result && res.data.pageVo.result.length) {
            let tableDataArr = res.data.pageVo.result
            exportSheet.exportFileByConfig(tableDataArr, '.xlsx', this.tempConfig)
            this.$notify.success({
              title: '',
              message: ''
            })
          }else {
            this.$notify.warning({
              title: '',
              message: ''
            })
          }
          return 
        }
        throw new Error(res.msg)
      }).catch(e => {
        this.isDisabled = true
        this.$notify.error({
          title: '()',
          message: e.message
        })
      })

    },
    /**
     * [handleFileSelect ]
     * @param  {[type]} e [description]
     * @return {[type]}   [description]
     */
    handleFileSelect (e) {
      // 
      if (!e.target.files.length) {
        return
      }
      this.file = e.target.files[0] || {name: ''}
      let idx = this.file.name.lastIndexOf(".")
      let strLength = this.file.name.length
      let postf = this.file.name.substring(idx, strLength) // 

      if (!this.file.name || postf != '.xlsx') {
        this.$notify.warning({
          title: '',
          message: 'excel'
        })
        // 
        this.file = {name: ''}
        document.getElementById('excelFile').value = ""
        return
      }

      let items = []
      let parseConfig = {}
      for (let key in this.tempConfig) {
        parseConfig[this.tempConfig[key]] = key
      }
      items = exportSheet.parseToJSON(this.file, parseConfig)
      items.then((result) => {
        // 
        let resultVal = result.filter(item => !!item.presaleId)
        let presaleListArr = []
        for (let item of resultVal) {
          if (item.stockNum === "") {
            this.$notify.warning({
              title: '',
              message: `(${item.productName})`
            })
            // 
            this.file = {name: ''}
            document.getElementById('excelFile').value = ""
            return
          }
          let presaleListItem = {
            presaleId: item.presaleId,
            stockNum: item.stockNum,
            skuNo: item.productNo,
            goodsNo: item.goodsNo
          }
          presaleListArr.push(presaleListItem)
        }
        
        this.presaleList = presaleListArr
      }).catch()
    },
    /**
     * [doConfirmHandel ]
     * @return {[type]} [description]
     */
    doConfirmHandel() {
      let params = {
        regionCode: this.regionCode
      }
      params.DIFFCITY = this.regionCode ? true : false
      this.conDisabled = true
      this.saveLoading = true
      this.$http.post(storePresaleGoods.excelUpdateStock, {presaleList: this.presaleList, ...params}).then(res => {
        setTimeout(() => {
          this.conDisabled = false
          this.saveLoading = false
        }, 800)
        if(res.code === 0) {
          this.$message.success("")
          this.isShow = false
          this.$emit("doSearchHandel")
          return
        }
        throw new Error(res.msg)
      }).catch(e => {
        this.conDisabled = false
        this.saveLoading = false
        this.$notify.error({
          title: '',
          message: e.message
        })
      })
    }
    
  }
}
</script>



