<style>
  .d-i-b {
    display: inline-block;
    vertical-align: top;
  }
  .c-p-t-b {
    display: inline-block;
    line-height: 50px;
  }
  .cs-b-b {
    border-top: 1px solid #ececec;
    border-left: 1px solid #ececec !important;
    border-right: 1px solid #ececec;
  }

  .des-p-style {
    line-height: 20px;
    font-size: 12px;
    color: #888;
  }

  .td-t-w {
    line-height: 36px;
    background-color: #fafafa;
    padding: 0 8px;
    border-bottom: 1px solid #ececec;
    box-sizing: border-box;
  }
  .mh-t-s-item-l-w {
    max-height: 300px;
    overflow-y: auto;
  }
</style>

<template>
  <div>
    <el-dialog :title="title" :visible.sync="isShow" width='680px'>
      <el-form label-position="left" :model="addParamMesg">
        <div>
          <el-form-item class="d-i-b">
            <el-select class="search-input" v-model="addParamMesg.fromStoreNo" filterable placeholder="" size="small">
              <el-option
                v-for="item in storeNoOptions"
                :key="item.value"
                :label="item.label"
                :value="item.value">
              </el-option>
            </el-select>
          </el-form-item>
          <div class="c-p-t-b">
            
          </div>
          <el-form-item class="d-i-b">
            <div class='pcs-mh-td-w cs-b-b'>
              <div class='td-t-w'>
                <span>
                  {{checkedList.length}}
                </span>
                <el-input
                  style="margin-left:10px;"
                  class="search-input-small-width"
                  placeholder="/"
                  v-model="searchContent.searchValue"
                  @keyup.enter.native="doSearchHandel"
                  suffix-icon="el-icon-search"
                  clearable
                  size="mini">
                </el-input>
                <el-cascader
                  ref="cascaderCategory"
                  placeholder=""
                  class="add-input-inline"
                  :options="storeGroupTypeNoOptions"
                  size="small"
                  filterable
                  clearable
                  :props="defaultProps"
                  @change="storeGroupTypeNoChange"
                  v-model="searchContent.storeGroupTypeNoList">
                </el-cascader>
              </div>
              <div class="mh-t-s-item-l-w">
                <el-table
                  v-loading="loading"
                  ref="multiple"
                  height="300px"
                  :data="tableData"
                  @select="handleSelect"
                  @select-all="handleSelectAll"
                  empty-text=""
                  size="small"
                  :header-cell-style="{
                    'padding': '0'
                  }">
                  <el-table-column
                    type="selection">
                  </el-table-column>
                  <el-table-column prop="label" label=""></el-table-column>
                </el-table>
              </div>
            </div>
          </el-form-item>
        </div>
        <div>
          <p class='des-p-style'>1ABERPB</p>
          <p class='des-p-style'>2</p>
          <p class='des-p-style'>3</p>
        </div>
      </el-form>

      <div slot="footer" class="dialog-footer">
        <el-button @click="isShow = false" size="mini"> </el-button>
        <el-button type="primary" @click="confirmClose" size="mini" :disabled="isDisabled"> </el-button>
      </div>
    </el-dialog>
  </div>
</template>

<script>
  import * as utils from '@/utils/utils'
  const requestUrl = {
    // 
    getPlatformStoreByCityId: '/plateform/a/platformStore/getPlatformStoreByCityId',
    // 
    copyProduct: '/plateform/a/product/store/presale/copyProduct',
    // 
    getStoreGroupType: '/backstage/storeGroup/storeGroupTypeList'

  }
  export default {
    data() {
      return {  
        isDisabled: false, // 
        // 
        isShow: false,
        title: '',
        addParamMesg: {
          saleType: '',
          fromStoreNo: '', // 
          platformId: '',
          toStoreNo: [] // 
        },
        storeGroupTypeNoOptions: [],
        defaultProps: {
          children: 'storeGroupList',
          label: 'labelName',
          value: 'valueName'
        },
        tableData: [],
        storeNoOptions: [],
        checkedList: [], // 
        condition: '', //   ERP/ 
        loading: false,
        searchContent: {
          searchValue: '',
          platformId: '',
          storeGroupTypeNo: '',
          storeGroupNo: '',
          storeGroupTypeNoList: []
        },
        regionCode: ''

      }
    },
    methods: {
      /**
       * [setDefaultMesg ]
       */
      setDefaultMesg(_params) {
        this.isShow = true
        if (_params.title) {
          this.title = _params.title
        }
        if (_params.platformId) {
          this.addParamMesg.platformId = _params.platformId
        }
        if (_params.saleType) {
          this.addParamMesg.saleType = _params.saleType
        }
        if (_params.regionCode) {
          this.regionCode = _params.regionCode
        }
        // 
        this.searchContent = {
          searchValue: '',
          platformId: _params.platformId,
          storeGroupTypeNo: '',
          storeGroupNo: '',
          storeGroupTypeNoList: []
        }

        // 
        this.getStoreList()
        this.getStoreGroupType()
        this.doSearchHandel()
      },
      storeGroupTypeNoChange(_val) {
        this.searchContent.storeGroupTypeNo = _val[0]
        this.searchContent.storeGroupNo = _val[1]
        this.doSearchHandel()
      },
      /**
       * @description: 
       * @param {type} 
       * @return: 
       */
       getStoreGroupType() {
        this.$http.get(requestUrl.getStoreGroupType).then(res => {
          if(res.code === 0) {
            let dataList = res.data && res.data.length ? res.data: []
            this.modifyStoreGroupeTree(dataList)
            this.storeGroupTypeNoOptions = dataList
            return 
          }
          throw new Error(res.msg)
        }).catch(e => {
          console.log("" + e.message)
        })
      },
      modifyStoreGroupeTree(storeTree, typeNo) {
        storeTree.forEach(v => {
          if(v.storeGroupList && v.storeGroupTypeNo) {
            // 
            v.valueName = v.storeGroupTypeNo
            v.labelName = v.storeGroupTypeName ? v.storeGroupTypeNo + '.' + v.storeGroupTypeName : ''
            if(v.storeGroupList.length) {
              this.modifyStoreGroupeTree(v.storeGroupList, v.storeGroupTypeNo)
            }
          }else if(v.storeGroupNo) {
            // 
            v.valueName = v.storeGroupNo
            v.labelName = v.storeGroupName ? v.storeGroupNo + '.' + v.storeGroupName : ''
            if(v.childrenList && v.childrenList.length) {
              v.storeGroupList = JSON.parse(JSON.stringify(v.childrenList))
              this.modifyStoreGroupeTree(v.storeGroupList)
            }
          }
        })
      },
      /**
       * [getStoreList ]
       * @return {[type]} [description]
       */
      getStoreList() {
        let params = {
          regionCode: this.regionCode
        }
        params.DIFFCITY = this.regionCode ? true : false
        this.addParamMesg.fromStoreNo = ''
        this.$http.post(requestUrl.getPlatformStoreByCityId, {...this.searchContent, ...params}).then(res => {
          if(res.code === 0) {
            if(res.data && res.data.length) {
              this.addParamMesg.fromStoreNo = res.data[0].storeNo
              this.storeNoOptions = res.data.map(item => {
                return {
                  value: item.storeNo,
                  label: item.storeNo +'-'+ item.storeName
                }
              })
            }else {
              this.storeNoOptions = []
            }
            return
          }
          throw new Error(res.msg)
        }).catch(e => {
          console.log("", e.message)
        })
      },
      /**
       * @description: 
       * @param {type} 
       * @return: 
       */
      doSearchHandel() {
        let params = {
          regionCode: this.regionCode
        }
        params.DIFFCITY = this.regionCode ? true : false
        this.loading = true
        this.$http.post(requestUrl.getPlatformStoreByCityId, {...this.searchContent, ...params}).then(res => {
          this.loading = false
          if(res.code === 0) {
            if(res.data && res.data.length) {
              this.tableData = res.data.map(item => {
                return {
                  storeNo: item.storeNo,
                  storeName: item.storeName,
                  value: item.storeNo,
                  label: item.storeNo +'-'+ item.storeName
                }
              })
            }else {
              this.tableData = []
            }
            this.defaultSelectFun()
            return
          }
          throw new Error(res.msg)
        }).catch(e => {
          this.loading = false
          this.$notify.error({
            title: '',
            message: e.message
          })
        })
      },
      /**
       * [handleSelect ]
       * @return {[type]} [description]
       */
      handleSelect(val, row) {
        let hash = {}
        let checkedListTemp = []

        if(utils.isKeyInArrayBykey(row.storeNo, 'storeNo', this.checkedList)) {
          this.checkedList = utils.removeItemByKeyVal('storeNo', row.storeNo, this.checkedList)
          return
        }else {
          checkedListTemp.push(row)
        }

        let checkedListTempArr = [...checkedListTemp, ...this.checkedList]
        this.checkedList = checkedListTempArr.reduceRight((item, next) => {
          hash[next.storeNo] ? '' : hash[next.storeNo] = true && item.push(next)
          return item
        }, [])
      },
      /**
       * [handleSelectAll ]
       * @return {[type]} [description]
       */
      handleSelectAll(val) {
        let hash = {}
        if(val.length > 0) {
          // 
          for(let item of val) {
            this.checkedList.push(item)
          }
          // 
          this.checkedList = this.checkedList.reduceRight((item, next) => {
            hash[next.storeNo] ? '' : hash[next.storeNo] = true && item.push(next)
            return item
          }, [])
        }else { 
          //  val.length<=0
          // 1.tableid
          let checkedListTemp = []
          for(let item of this.tableData) {
            checkedListTemp.push(item)
          }
          // 2.
          let checkedListArr = utils.getSubtract(this.checkedList, checkedListTemp, 'storeNo')
          this.checkedList = checkedListArr && checkedListArr.length > 0 ? checkedListArr : [] 
        }
      },
      /**
       * [confirmClose ]
       * @return {[type]} [description]
       */
      confirmClose() {
        let params = {
          regionCode: this.regionCode
        }
        params.DIFFCITY = this.regionCode ? true : false
        let toStoreNoArr = this.checkedList.map(item => {
          return item.storeNo
        })
        this.addParamMesg.toStoreNo = toStoreNoArr
        if(this.addParamMesg.toStoreNo.length === 0) {
          this.$message.success("")
          return
        }
        this.isDisabled = true
        this.$http.post(requestUrl.copyProduct, {...this.addParamMesg, ...params}).then(res => {
          setTimeout(() => {
            this.isDisabled = false
          }, 800)
          if(res.code === 0) {
            this.isShow = false
            this.$message.success("")
            this.$emit("doSearchHandel")
            return
          }
          throw new Error(res.msg)
        }).catch(e => {
          this.isDisabled = false
          this.$notify.error({
            title: '',
            message: e.message
          })
        })
      },
      /**
       * [defaultSelectFun ]
       * @param    {[type]}                 row [description]
       * @return   {[type]}                     [description]
       */
      defaultSelectFun() {
        let _this = this
        let rows = []
        for(let i of this.tableData) {
          for(let j of this.checkedList) {
            if(i.storeNo == j.storeNo) {
              rows.push(i)
            }
          } 
        }
        if (rows.length > 0) {
          this.$refs.multiple.clearSelection()
          setTimeout(function() {
            rows.forEach(rowItem => {
              _this.$refs.multiple.toggleRowSelection(rowItem)
            });
          }, 100)
        } else {
          this.$refs.multiple.clearSelection()
        }
      }
    }
    
  }
</script>


