<style>
  .ll-p-m-w {
    display: flex;
    flex-direction: row;
    flex-wrap: nowrap;
  }
  .ll-img-w {
    width: 60px;
    height: 60px;
  }
  .ll-img-w>img {
    width: 60px;
  }
  .ll-p-m-c-r {
    padding: 0 10px;
  }
  .ll-erp-p-no {
    color: #666;
    font-size: 12px;
  }
  .c-g-sfw {
    padding-top: 0 !important;
    padding-bottom: 0 !important;
  }
  .switch-cp{
    margin: 0 10px 10px;
  }
</style>

<template>
  <div>
    <el-dialog :title="title" :visible.sync="isShow" width='720px'>
      <div class="switch-cp" v-if="!showData && noExclude && !chooseGoods">
        <el-switch
          v-model="chooseOnlineGoodsTOF"
          active-color="#409EFF"
          inactive-color="#F56C6C"
          active-text=""
          inactive-text="">
        </el-switch>
      </div>
      <div class="search-form-wraper c-g-sfw" v-if="!showData && !chooseGoods">
        <el-form :model="searchContent" label-position="top">
          <el-form-item>
            <el-cascader
              placeholder=""
              class="search-input"
              :options="categoryList"
              change-on-select
              size="small"
              clearable
              filterable
              @change="doSearchHandel(true)"
              :props="cateDefaultProps"
              v-model="searchContent.categoryIdList">
            </el-cascader>
          </el-form-item>
          <el-form-item>
            <el-select
              v-model="searchContent.goodsQueryVal"
              filterable
              clearable
              remote
              size="small" 
              class="search-input"
              default-first-option
              placeholder="/ERP"
              :remote-method="queryGoodsSearchAsync"
              :loading="queryGoodsAsyncLoading"
              @change="chooseGoodsChangeHandle">
              <el-option
                v-for="item in queryGoodsSearchAsyncList"
                :key="item.goodsId"
                :label="item.skuNametemp"
                :value="item.goodsId">
              </el-option>
            </el-select>
          </el-form-item>
          <el-form-item>
            <el-button type="primary" @click="doSearchHandel(true)" size="mini"></el-button>
          </el-form-item>
        </el-form>
      </div>
      
      <div>
        <div class="goods-table tab-wraper">
          <el-table
            class="fixed-height"
            ref="multipleTableTimely"
            v-loading="loading"
            :data="tableData"
            empty-text=""
            style="width:100%;"
            height="300"
            size="small"
            @select="handleSelect"
            @select-all="handleSelectAll">
            <el-table-column
              v-if="!showData"
              type="selection"
              width="50">
            </el-table-column>
            <el-table-column min-width="216px" label="">
              <template slot-scope="scope">
                <div class="ll-p-m-w">
                  <div class="ll-img-w">
                    <img v-if="scope.row.imgUrl && scope.row.imgUrl != ''" :src="scope.row.imgUrl" alt="" onerror="onerror=null;src='/shop/static/img_error.png'">
                    <img v-else src="/shop/static/img_error.png" alt="">
                  </div>
                  <div class="ll-p-m-c-r">
                    <div>{{scope.row.skuName || '-'}}</div>
                    <div class="ll-erp-p-no">ERP{{scope.row.skuNo}}</div>
                    <div class="ll-erp-p-no">ID{{scope.row.goodsId || '-'}}</div>
                  </div>
                </div>
              </template>     
            </el-table-column>
            <el-table-column min-width="110px" prop="spec" label=""></el-table-column>
            <el-table-column min-width="110px" prop="categoryName" label=""></el-table-column>
          </el-table>
        </div>

        <div class="tc mt20" v-show="tableData.length > 0 && !showData && !chooseGoods">
          <el-pagination
            background
            @size-change="changeSize"
            @current-change="changeCurrent"
            :current-page.sync="pageNum"
            :page-sizes="[10, 50, 100, 150, 200]"
            layout="total, sizes, prev, pager, next, jumper"
            :pager-count="5"
            :total="total">
          </el-pagination>
        </div>
      </div>
      <div slot="footer" class="dialog-footer" v-if="!showData">
        <el-button @click="isShow = false" size="mini"> </el-button>
        <el-button type="primary" @click="doConfirmHandel" size="mini" :disabled="checkedList.length === 0"> </el-button>
      </div>
    </el-dialog>
  </div>
</template>

<script>
  import * as utils from '@/utils/utils'
  const reqUrl = {
    // 
    getCategoryTree: '/plateform/a/category/getCategoryTree',
    // 
    selectPageGooods: '/plateform/a/baseCommon/goods/selectPageGooods',
    // -
    selectCityGoods: '/plateform/a/baseCommon/goods/selectGoods'
  }
  export default {
    data() {
      return {
        isShow: false,
        title: '', // title
        loading: false,
        chooseOnlineGoodsTOF: true,
        pageNum: 1,
        pageSize: 10,
        total: 0,
        searchContent: {
          // sku
          goodsQueryVal: '', // 
          goodsIds: [], // ID
          categoryIdList: [],
          categoryId: '' // ID
        },
        tableData: [],
        // 
        cateDefaultProps: {
          children: 'categoryVoList',
          label: 'categoryName',
          value: 'categoryId'
        },
        categoryList: [], // id
        // loading
        queryGoodsAsyncLoading: false,
        // 
        queryGoodsSearchAsyncList: [],
        // 
        checkedList: [],
        showData: false,
        noExclude: true,
        chooseGoods: false,
        chooseGoodsIndex: 0
      }
    },
    methods: {
      /**
       * [setDefault ]
       * @param {[type]} _val [description]
       */
      setDefault(_val, _param, noExclude) {
        this.chooseGoods = false
        if(noExclude === 'noExclude') {
          this.noExclude = false
        }else{
          this.noExclude = true
        }
        if(_param === 'show') {
          this.title = _val.title
          this.showData = true
          this.tableData = _val.checkedList
          this.isShow = true
        }else{
          this.showData = false
          if(_val) {
            this.title = _val.title
            if(_val.checkedList && _val.checkedList.length) {
              this.checkedList = _val.checkedList
            }else {
              this.checkedList = []
            }
          }

          this.isShow = true
          // 
          this.getCategoryId()
          this.doSearchHandel(true)
        }
      },
      setDefaultChoose(_params, index) {
        this.chooseGoods = true
        this.chooseGoodsIndex = index
        this.showData = false
        if(_params) {
          this.title = _params.title
          if(_params.checkedElseList && _params.checkedElseList.length) {
            this.tableData = _params.checkedElseList
            this.checkedList = []
            let checkedList = _params.checkedList
            setTimeout(() => {
              this.$refs.multipleTableTimely.clearSelection()
              setTimeout(() => {
                if(checkedList.length) {
                  this.tableData.forEach(item => {
                    checkedList.forEach(item_ => {
                      if(item_ == item.goodsId) {
                        this.$refs.multipleTableTimely.toggleRowSelection(item)
                        this.checkedList.push(item)
                      }
                    })
                  })
                }
              }, 100)
            }, 100)
          }else {
            this.tableData = []
          }
        }
        this.isShow = true
      },
      /**
       * [getCategoryId ]
       * @return {[type]} [description]
       */
      getCategoryId() {
        this.$http.post(reqUrl.getCategoryTree).then(res => {
          if(res.code === 0) {
            if(res.data && res.data.length) {
              for(let item of res.data) {
                if(item.categoryVoList && item.categoryVoList.length === 0) {
                  item.categoryVoList = null
                }
              }
              this.categoryList = res.data
            }else {
              this.categoryList = []
            }
            return 
          }
          throw new Error(res.msg)
        }).catch(e => {
          console.log("():", e.message)
        })
      },
      /**
       * [changeSize description]
       * @param    {[type]}                 value [description]
       * @return   {[type]}                       [description]
       */
      changeSize (value) {
        this.pageSize = value
        this.doSearchHandel()
      },
      /**
       * [changeCurrent ]
       * @param    {[type]}                 value []
       * @return   {[type]}                       [description]
       */
      changeCurrent (value) {
        this.pageNum = value
        this.doSearchHandel()
      },
      /**
       * [doSearchHandel ]
       * @param  {[type]} _param [description]
       * @return {[type]}         [description]
       */
      doSearchHandel(_param) {
        let spliceLen = this.searchContent.categoryIdList.length - 1
        this.searchContent.categoryId = this.searchContent.categoryIdList[spliceLen]
        this.loading = true
        if(_param === true) {
          this.pageNum = 1
        }
        this.$http.post(reqUrl.selectPageGooods, { ...this.searchContent, pageNum: this.pageNum, pageSize: this.pageSize, byCityId: true }).then(res => {
          this.loading = false
          if(res.code === 0) {
            // 
            if(res.data && res.data.result && res.data.result.length) {
              for(let item of res.data.result) {
                item.activityskuName = item.skuName
                item.activityId = '' // id
                item.id = '' // ID
                item.activityTimes = '' // 
                item.curStock = '' // 
                item.groupCount = '' // 
                item.groupPrice = item.price // 
                item.limitGroupType = 0 // : 012
                item.sortNum = 50 // 
                item.limitGoodsCount = '' // 
              }
              this.tableData = res.data.result            
            }else {
              this.tableData = []
            }
            if(this.pageNum == 1) {
              this.total = res.data.total
            }

            this.defauleSelectFun()
            return 
          }
          throw new Error(res.msg)
        }).catch(e => {
          this.loading = false
          this.$notify.error({
            title: '',
            message: e.message
          })
        })
      },
      /**
       * [queryGoodsSearchAsync SKU]
       * @param  {[type]} queryString []
       * @return {[type]}             [description]
       */
      queryGoodsSearchAsync(queryString) {
        if(queryString === "") {
          return
        }
        let spliceLen = this.searchContent.categoryIdList.length - 1
        this.searchContent.categoryId = this.searchContent.categoryIdList[spliceLen]

        let params = {
          categoryId: this.searchContent.categoryId || '',
          categoryIdList: this.searchContent.categoryIdList,
          searchValue: queryString
        }

        this.queryGoodsAsyncLoading = true
        this.$http.post(reqUrl.selectCityGoods, params).then(res => {
          this.queryGoodsAsyncLoading = false
          if(res.code === 0) {
            if(res.data && res.data.length) {
              for(let item of res.data) {
                item.skuNametemp = item.goodsId + '-' + item.goodsName
              }
              this.queryGoodsSearchAsyncList = res.data
            }else {
              this.queryGoodsSearchAsyncList = []
            }
            return
          }
          throw new Error(res.msg)
        }).catch(e => {
          this.queryGoodsAsyncLoading = false
          console.log("()", e.message)
        })
      },
      /**
       * [skuGoodsHandleSelect ]
       * @param  {[type]} _keyVal [description]
       * @return {[type]}         [description]
       */
      chooseGoodsChangeHandle(kVal) {
        if(!kVal) {
          this.searchContent.goodsIds = []
          this.searchContent.goodsQueryVal = ''
          // 
          this.doSearchHandel(true)
          return
        }
        this.searchContent.goodsIds = []
        let checkedItem = this.queryGoodsSearchAsyncList.filter(j => {
          return j.goodsId == kVal
        })[0]
        this.searchContent.goodsIds.push(checkedItem.goodsId)
        // 
        this.doSearchHandel(true)
      },
      /**
       * [handleSelect ]
       * @return {[type]} [description]
       */
      handleSelect(val, row) {
        let hash = {}
        let checkedListTemp = []

        if(utils.isKeyInArrayBykey(row.goodsId, 'goodsId', this.checkedList)) {
          this.checkedList = utils.removeItemByKeyVal('goodsId', row.goodsId, this.checkedList)
          return
        }else {
          checkedListTemp.push(row)
        }

        let checkedListTempArr = [...checkedListTemp, ...this.checkedList]
        this.checkedList = checkedListTempArr.reduceRight((item, next) => {
          hash[next.goodsId] ? '' : hash[next.goodsId] = true && item.push(next)
          return item
        }, [])
      },
      /**
       * [handleSelectAll ]
       * @return {[type]} [description]
       */
      handleSelectAll(val) {
        let hash = {}
        if(val.length > 0) {        
          // 
          for(let item of val) {
            this.checkedList.push(item)
          }
          // 
          this.checkedList = this.checkedList.reduceRight((item, next) => {
            hash[next.goodsId] ? '' : hash[next.goodsId] = true && item.push(next)
            return item
          }, [])
        }else { 
          //  val.length<=0
          // 1.tableid
          let checkedListTemp = []
          for(let item of this.tableData) {
            checkedListTemp.push(item)
          }
          // 2.
          let checkedListArr = utils.getSubtract(this.checkedList, checkedListTemp, 'goodsId')
          this.checkedList = checkedListArr && checkedListArr.length > 0 ? checkedListArr : [] 
        }
      },
      /**
       * [defauleSelectFun ]
       * @param    {[type]}                 row [description]
       * @return   {[type]}                     [description]
       */
      defauleSelectFun() {
        let _this = this
        let rows = []
        //   
        for(let i of this.tableData) {
          for(let j of this.checkedList) {
            if(i.goodsId == j.goodsId) {
              i.activityskuName = j.activityskuName
              i.activityId = j.activityId // id
              i.id = j.id // ID
              i.activityTimes = j.activityTimes // 
              i.curStock = j.curStock // 
              i.groupCount = j.groupCount // 
              i.groupPrice = j.groupPrice // 
              i.limitGroupType = j.limitGroupType // : 012
              i.sortNum = j.sortNum // 
              i.limitGoodsCount = j.limitGoodsCount // 
              rows.push(i)
            }
          }            
        }
        if(rows.length > 0) {
          this.$refs.multipleTableTimely.clearSelection()
          setTimeout(function() {
            rows.forEach(rowItem => {
              _this.$refs.multipleTableTimely.toggleRowSelection(rowItem)
            });
          }, 100)
        } else {
          this.$refs.multipleTableTimely.clearSelection()
        }
      },
      /**
       * [doConfirmHandel ]
       * @return {[type]} [description]
       */
      doConfirmHandel() {
        if(this.chooseGoods) {
          let goodsId = []
          let skuName = []
          this.checkedList.forEach(item => {
            goodsId.push(item.goodsId)
            skuName.push(item.skuName)
          })
          let _params = {
            index: this.chooseGoodsIndex,
            goodsId: goodsId.join(';'),
            skuName: skuName.join(';')
          }
          this.$emit("chooseOnlineGoods", _params)
          this.isShow = false
        }else{
          let _params = {}
          if(this.noExclude === true) {
            _params = {
              chooseOnlineGoodsTOF: this.chooseOnlineGoodsTOF,
              checkedList: this.checkedList
            }
          }else{
            _params = {
              checkedList: this.checkedList
            }
          }
          this.$emit("confirmChooseOnlineGoods", _params)
          this.isShow = false
        }
      }

    }
  }
</script>




