<style>
.coupon-input{
  width: 80px !important;
}
.coupon-input .el-input__inner,
.coupon-input .el-input-group__append{
  padding: 0 8px;
}
.push-remove {
  font-size: 18px;
  margin-left: 10px;
  cursor: pointer;
}
.popun-list-wrap {
  border: 1px solid #ececec;
  border-radius: 8px;
  margin-bottom: 6px;
}
.prom-reqs .el-form-item__content .el-input-group{
  vertical-align: middle;
}
</style>
<template>
  <div class="prom-reqs">
    <el-form-item label="" required v-if="promType === '2'">
      <div v-for="(item, index) in promDiscountsDis"  :key="index" class="popun-list-wrap">
        <div v-if="triggerType === 121 || triggerType === 111" style="display: inline-block;">
          <span></span>
          <el-input size="small" class="coupon-input" v-model="item.title"></el-input>
        </div>
        <el-select v-model="item.triggerAmount" placeholder="" style="width: 100px;" size="small" @change="triggerAmountOptionChange(item, index)" v-if="triggerType === 4 || triggerType === 42">
          <el-option
            v-for="item in triggerAmountOption"
            :key="item.value"
            :label="item.label"
            :value="item.value">
          </el-option>
        </el-select>
        <span></span>
        <el-input size="small" class="coupon-input" v-model="item.meetAmount">
          <template slot="append"></template>
        </el-input>
        <span>&nbsp;&nbsp;</span>
        <el-input size="small" class="coupon-input" v-model="item.discAmount">
          <template slot="append"></template>
        </el-input>
        <el-input size="small" class="add-input" v-model="item.remark" v-if="triggerType === 4 || triggerType === 42" placeholder="1220"></el-input>
        <div v-if="triggerType === 81 || triggerType === 82" style="display: inline-block;">
          <span>&nbsp;&nbsp;</span>
          <el-input size="small" class="coupon-input" v-model="item.sendCount">
            <template slot="append"></template>
          </el-input>
          <span>&nbsp;&nbsp;</span>
          <el-input class="coupon-input" size="small" v-model="item.winPromRate">
            <template slot="append">%</template>
          </el-input>
        </div>
        <div v-else style="display: inline-block;">
          <span>&nbsp;&nbsp;</span>
          <el-input size="small" class="coupon-input" v-model="item.sendPromCount">
            <template slot="append"></template>
          </el-input>
          <div v-if="triggerType === 42" style="display: inline-block;">
            <span>&nbsp;&nbsp;</span>
            <el-input size="small" class="coupon-input" v-model="item.sendCount">
              <template slot="append"></template>
            </el-input>
            <span>&nbsp;&nbsp;</span>
            <el-input size="small" class="coupon-input" v-model="item.sendIntervalDays">
              <template slot="append"></template>
            </el-input>
            <span>&nbsp;&nbsp;</span>
            <el-input size="small" class="coupon-input" v-model="item.discAmount*item.sendPromCount*item.sendCount || 0" disabled>
              <template slot="append"></template>
            </el-input>
          </div>
          <div v-else style="display: inline-block;">
            <span>&nbsp;&nbsp;</span>
            <el-input size="small" class="coupon-input" v-model="item.discAmount*item.sendPromCount || 0" disabled>
              <template slot="append"></template>
            </el-input>
          </div>
          <!--  -->
          <div v-if="triggerType === 121" style="display: inline-block;">
            <span>&nbsp;&nbsp;</span>
            <el-input size="small" class="coupon-input" v-model="item.triggerIntegralProm">
              <template slot="append"></template>
            </el-input>
          </div>
          <div v-if="triggerType === 121" style="display: inline-block;">
            <span>&nbsp;&nbsp;</span>
            <el-input size="small" class="coupon-input" v-model="item.curStock">
              <template slot="append"></template>
            </el-input>
          </div>
          <div v-if="triggerType === 121" style="display: inline-block;">
            <span>&nbsp;&nbsp;()</span>
            <el-input size="small" class="coupon-input" v-model="item.limitCount">
              <template slot="append"></template>
            </el-input>
          </div>
        </div>
        <i class="push-remove el-icon-circle-plus" @click="plusDiscount"></i>
        <i class="push-remove el-icon-remove" v-if="promDiscountsDis.length > 1" @click="minusDiscount(index)"></i>
      </div>
    </el-form-item>
    <el-form-item label="" required v-if="promType === '5'">
      <div v-for="(item, index) in promDiscountsGood"  :key="index" class="popun-list-wrap">
        <div v-if="triggerType === 121 || triggerType === 111" style="display: inline-block;">
          <span></span>
          <el-input size="small" class="coupon-input" v-model="item.title"></el-input>
        </div>
        <el-select v-model="item.triggerAmount" placeholder="" style="width: 100px;" size="small" @change="triggerAmountOptionChange(item, index)" v-if="triggerType === 4 || triggerType === 42">
          <el-option
            v-for="item in triggerAmountOption"
            :key="item.value"
            :label="item.label"
            :value="item.value">
          </el-option>
        </el-select>
        <div style="display: inline-block;" v-if="ruleSource === 0">
          <span></span>
          <el-select size="small" class="coupon-input" v-model.trim="item.range" placeholder="" @change="rangeChange(index)">
            <el-option :label="item.label" :value="item.value" :key="item.value" v-for="item in rangeList"></el-option>
          </el-select>
        </div>
        <span>&nbsp;&nbsp;</span>
        <el-input size="small" class="coupon-input" v-model="item.needPayAmount">
          <template slot="append"></template>
        </el-input>
        <span>&nbsp;&nbsp;</span>
        <el-input size="small" class="coupon-input" v-model="item.goodName" disabled>
          <el-button slot="append" icon="el-icon-search" @click="chooseGoods(item.range, index)"></el-button>
        </el-input>
        <span>&nbsp;&nbsp;</span>
        <el-input size="small" style="width: 50px;" v-model="item.productQtyLimit">
        </el-input>
        <span v-if="ruleSource === 1 || item.range === 2">{{item.productUnitStr}}</span>
        <span v-else-if="ruleSource === 2 || item.range === 1"></span>

        <div v-if="triggerType === 81 || triggerType === 82" style="display: inline-block;">
          <span>&nbsp;&nbsp;</span>
          <el-input size="small" class="coupon-input" v-model="item.sendCount">
            <template slot="append"></template>
          </el-input>
          <span>&nbsp;&nbsp;</span>
          <el-input class="coupon-input" size="small" v-model="item.winPromRate">
            <template slot="append">%</template>
          </el-input>
        </div>
        <div v-else style="display: inline-block;">
          <span>&nbsp;&nbsp;</span>
          <el-input size="small" class="coupon-input" v-model="item.sendPromCount">
            <template slot="append"></template>
          </el-input>
          <div v-if="triggerType === 42" style="display: inline-block;">     
            <span>&nbsp;&nbsp;</span>
            <el-input size="small" class="coupon-input" v-model="item.sendCount">
              <template slot="append"></template>
            </el-input>
            <span>&nbsp;&nbsp;</span>
            <el-input size="small" class="coupon-input" v-model="item.sendIntervalDays">
              <template slot="append"></template>
            </el-input>
          </div>
        </div>
        <!--  -->
        <div v-if="triggerType === 121" style="display: inline-block;">
          <span>&nbsp;&nbsp;</span>
          <el-input size="small" class="coupon-input" v-model="item.triggerIntegralProm">
            <template slot="append"></template>
          </el-input>
        </div>
        <div v-if="triggerType === 121" style="display: inline-block;">
          <span>&nbsp;&nbsp;</span>
          <el-input size="small" class="coupon-input" v-model="item.curStock">
            <template slot="append"></template>
          </el-input>
        </div>
        <div v-if="triggerType === 121" style="display: inline-block;">
          <span>&nbsp;&nbsp;()</span>
          <el-input size="small" class="coupon-input" v-model="item.limitCount">
            <template slot="append"></template>
          </el-input>
        </div>
        <el-input size="small" class="add-input" v-model="item.remark" v-if="triggerType === 4 || triggerType === 42" placeholder="1220"></el-input>
        <i class="push-remove el-icon-circle-plus" @click="plusDiscount"></i>
        <i class="push-remove el-icon-remove" v-if="promDiscountsGood.length > 1" @click="minusDiscount(index)"></i>
      </div>
    </el-form-item>
    <hr class="s-slip-tb">
    
    <!--  -->
    <choose-online-goods ref="chooseOnlineGoods" @chooseOnlineGoods="chooseOnlineGoods"></choose-online-goods>
    <!--  -->
    <choose-offline-goods ref="chooseOfflineGoods" @chooseOfflineGoods="chooseOfflineGoods"></choose-offline-goods>
    <!--  -->
    <el-dialog title="" :visible.sync="amountDialogVisible" width="40%" :show-close="false" :close-on-click-modal="false">
      <div style="margin-bottom: 20px;">
        <el-input size="small" placeholder="1" v-model="rechargeAmount" style="width:200px;"></el-input>
        <label></label>
      </div>
      <span slot="footer" class="dialog-footer">
        <el-button size="small" @click="sureAmountDialogVisible(false)"> </el-button>
        <el-button size="small" type="primary" @click="sureAmountDialogVisible"> </el-button>
      </span>
    </el-dialog>
  </div>
</template>
<script>
  import ChooseOfflineGoods from "../Dialog/ChooseOfflineGoods"
  import ChooseOnlineGoods from "../Dialog/ChooseOnlineGoods"

  const reqUrl = {

  }
  export default {
    props: ['triggerType'],
    data() {
      return {
        promType: '2',
        ruleSource: 0,
        chooseOnlineType: '',
        chooseOfflineType: '',
        onlineGoodsCheckedList: [], // 
        offlineGoodsCheckedList: [], // 
        promDiscountsDis: [{
          discAmount: '',
          meetAmount: '',
          sendPromCount: '',
          sendCount: ''
        }],
        promDiscountsGood: [{
          range: '',
          needPayAmount: '',
          goodName: '',
          productQtyLimit: '',
          sendPromCount: ''
        }],
        rangeList: [
          {
            value: 1,
            label: ''
          },
          {
            value: 2,
            label: ''
          }
        ],
        triggerAmountOption: [
          {
            value: '100',
            label: '100'
          }, 
          {
            value: '200',
            label: '200'
          }, 
          {
            value: '500',
            label: '500'
          }, 
          {
            value: '1000',
            label: '1000'
          }, 
          {
            value: '2000',
            label: '2000'
          }, 
          {
            value: 'else',
            label: ''
          }
        ],
        rechargeAmount: '',
        amountDialogVisible: false, // 
        amountDialogIndex: 0,
      }
    },
    created() {

    },
    mounted() {
    },
    components: {
      "choose-offline-goods": ChooseOfflineGoods,
      "choose-online-goods": ChooseOnlineGoods
    },
    watch: {
      promDiscountsDis: {
        handler(newVal) {
          // 
          let promDiscountsDis = JSON.parse(JSON.stringify(this.promDiscountsDis))
          if(this.triggerType === 1 || this.triggerType === 4 || this.triggerType === 71) {
            promDiscountsDis.forEach(item => {
              item.sendCount = '1'
            })
          }
          this.$emit("changeDisCoupon", promDiscountsDis)
        },
        deep: true,
        immediate: true
      },
      promDiscountsGood: {
        handler(newVal) {
          // 
          this.$emit("changeDisCoupon", this.promDiscountsGood)
        },
        deep: true,
        immediate: true
      }
    },
    methods: {
      // 
      promTypeChange(promType) {
        this.promType = promType
      },
      // 
      changeRuleSource(ruleSource) {
        this.ruleSource = ruleSource
      },
      /**
       * [rangeChange ]
       * @return {[type]} [description]
       */
      rangeChange(index) {
        this.$set(this.promDiscountsGood[index], 'goodName', '');
        this.$set(this.promDiscountsGood[index], 'goodsNo', '');
        this.$set(this.promDiscountsGood[index], 'productUnitStr', '');
      },
      plusDiscount() {
        if(this.promType === '2') {
          this.promDiscountsDis.push({
            discAmount: '',
            meetAmount: '',
            sendPromCount: '',
            sendCount: ''
          })
        }else if(this.promType === '5') {
          this.promDiscountsGood.push({
            range: '',
            needPayAmount: '',
            goodName: '',
            productQtyLimit: '',
            sendPromCount: ''
          })
        }
      },
      minusDiscount(index) {
        if(this.promType === '2') {
          this.promDiscountsDis.splice(index, 1)
        }else if(this.promType === '5') {
          this.promDiscountsGood.splice(index, 1)
        }
      },
      goodsChange(goods) {
        // 
        this.chooseOnlineType = goods.chooseOnlineType
        this.chooseOfflineType = goods.chooseOfflineType
        if(this.chooseOnlineType === 1) {
          this.onlineGoodsCheckedList = [...goods.onlineGoodsCheckedList]
        }else if(this.chooseOnlineType === 3) {
          this.onlineGoodsCheckedList = [...goods.uploadNGoodsNo]
        }
        if(this.chooseOfflineType === 1) {
          this.offlineGoodsCheckedList = [...goods.offlineGoodsCheckedList]
        }else if(this.chooseOfflineType === 3) {
          this.offlineGoodsCheckedList = [...goods.uploadFGoodsNo]
        }
      },
      /**
       * [chooseGoods ]
       * @return {[type]} [description]
       */
      chooseGoods(_val, index) {
        // 
        if(this.ruleSource === 0) {
          if(_val === 1) {
            if(!this.onlineGoodsCheckedList.length) {
              this.$message("")
              return
            }else{
              this.$set(this.promDiscountsGood[index], 'goodsType', 2);
            }
          }else if(_val === 2) {
            if(!this.offlineGoodsCheckedList.length) {
              this.$message("")
              return
            }else{
              this.$set(this.promDiscountsGood[index], 'goodsType', 1);
            }
          }else{
            this.$message("")
            return
          }
        }else if(this.ruleSource === 1) {
          if(!this.offlineGoodsCheckedList.length) {
            this.$message("")
            return
          }else{
            this.$set(this.promDiscountsGood[index], 'goodsType', 1);
          }
        }else if(this.ruleSource === 2) {
          if(!this.onlineGoodsCheckedList.length) {
            this.$message("")
            return
          }else{
            this.$set(this.promDiscountsGood[index], 'goodsType', 2);
          }
        }

        if(this.promDiscountsGood[index].goodsType === 1){
          let goodsCheckedALLList = []
          let goodsCheckedElseList = []
          let goodsCheckedList = []
          goodsCheckedALLList = JSON.parse(JSON.stringify(this.offlineGoodsCheckedList))
          goodsCheckedElseList = JSON.parse(JSON.stringify(goodsCheckedALLList))
          this.promDiscountsGood.forEach((item, i) => {
            if(i !== index) {
              if(item.goodsNo) {
                let goodsNoList = []
                goodsNoList = item.goodsNo.split(';')
                goodsNoList.forEach(item_ => {
                  goodsCheckedElseList = goodsCheckedElseList.filter(item__ => {
                    return item__.goodsNo != item_
                  })
                })
              }
            }else if(i === index) {
              if(item.goodsNo) {
                goodsCheckedList = item.goodsNo.split(';')
              }
            }
          })
          let _params = {
            title: '',
            checkedElseList: goodsCheckedElseList,
            checkedList: goodsCheckedList
          }
          this.$refs.chooseOfflineGoods.setDefaultChoose(_params, index)
        }else{
          let goodsCheckedALLList = []
          let goodsCheckedElseList = []
          let goodsCheckedList = []
          goodsCheckedALLList = JSON.parse(JSON.stringify(this.onlineGoodsCheckedList))
          goodsCheckedElseList = JSON.parse(JSON.stringify(goodsCheckedALLList))
          this.promDiscountsGood.forEach((item, i) => {
            if(i !== index) {
              if(item.goodsNo) {
                let goodsNoList = []
                goodsNoList = item.goodsNo.split(';')
                goodsNoList.forEach(item_ => {
                  goodsCheckedElseList = goodsCheckedElseList.filter(item__ => {
                    return item__.goodsId != item_
                  })
                })
              }
            }else if(i === index) {
              if(item.goodsNo) {
                goodsCheckedList = item.goodsNo.split(';')
              }
            }
          })
          let _params = {
            title: '',
            checkedElseList: goodsCheckedElseList,
            checkedList: goodsCheckedList
          }
          this.$refs.chooseOnlineGoods.setDefaultChoose(_params, index)
        }
      },
      /**
       * [chooseOfflineGoods ]
       * @return {[type]} [description]
       */
      chooseOfflineGoods(_params) {
        if(_params) {
          this.$set(this.promDiscountsGood[_params.index], 'goodName', _params.goodsName);
          this.$set(this.promDiscountsGood[_params.index], 'goodsNo', _params.goodsNo);
          this.$set(this.promDiscountsGood[_params.index], 'productUnitStr', _params.saleUnitName);
        }
      },
      /**
       * [chooseOnlineGoods ]
       * @return {[type]} [description]
       */
      chooseOnlineGoods(_params) {
        if(_params) {
          this.$set(this.promDiscountsGood[_params.index], 'goodName', _params.skuName);
          this.$set(this.promDiscountsGood[_params.index], 'goodsNo', _params.goodsId);
        }
      },
      /**
       * [triggerAmountOptionChange ]
       * @return {[type]} [description]
       */
      triggerAmountOptionChange(item, index) {
        if(item.triggerAmount == 'else') {
          this.amountDialogIndex = index
          this.amountDialogVisible = true  
        }
      },
      /**
       * [sureAmountDialogVisible ]
       * @return {[type]} [description]
       */
      sureAmountDialogVisible(param) {
        let countReg = /^\+?[1-9][0-9]*$/
        let index = this.amountDialogIndex

        if(this.promType === '2') {
          if(param == false) {
            // 
            this.promDiscountsDis[index].triggerAmount = ''
            this.rechargeAmount = ''
            this.amountDialogVisible = false
          }else{
            // 
            if(countReg.test(this.rechargeAmount) == false || this.rechargeAmount < 1) {
              this.$message("1");
              return
            }
            this.promDiscountsDis[index].triggerAmount = this.rechargeAmount + ''
            this.rechargeAmount = ''
            this.amountDialogVisible = false
          } 
        }else if(this.promType === '5') {
          if(param == false) {
            // 
            this.promDiscountsGood[index].triggerAmount = ''
            this.rechargeAmount = ''
            this.amountDialogVisible = false
          }else{
            // 
            if(countReg.test(this.rechargeAmount) == false || this.rechargeAmount < 1) {
              this.$message("1");
              return
            }
            this.promDiscountsGood[index].triggerAmount = this.rechargeAmount + ''
            this.rechargeAmount = ''
            this.amountDialogVisible = false
          } 
        }
      }
    }
  }

</script>


