<style>
  .ll-p-m-w {
    display: flex;
    flex-direction: row;
    flex-wrap: nowrap;
  }
  .ll-img-w {
    width: 60px;
    height: 60px;
  }
  .ll-img-w>img {
    width: 60px;
  }
  .ll-p-m-c-r {
    padding: 0 10px;
  }
  .ll-erp-p-no {
    color: #666;
    font-size: 12px;
  }
  .c-g-sfw {
    padding-top: 0 !important;
    padding-bottom: 0 !important;
  }
</style>

<template>
  <div>
    <el-dialog 
      :title="title" 
      :visible.sync="isShow" 
      :close-on-click-modal = "false"
      :close-on-press-escape = "false"
      width='720px'>
      <div class="search-form-wraper c-g-sfw">
        <el-form :model="searchContent" label-position="top" @submit.native.prevent>
          <el-form-item>
            <el-cascader
              placeholder=""
              class="search-input"
              :options="categoryList"
              change-on-select
              size="small"
              clearable
              filterable
              @change="doCategorysChangeHandel"
              :props="cateDefaultProps"
              v-model="searchContent.categoryIdList">
            </el-cascader>
          </el-form-item>
          <el-form-item>
            <goods-select-for-dialog ref="GoodsSelectForDialog" @DoSelectGoodsHandel="DoSelectGoodsHandel"></goods-select-for-dialog>
          </el-form-item>
          <el-form-item>
            <el-button type="primary" @click="doSearchHandel(true)" size="mini"></el-button>
          </el-form-item>
        </el-form>
      </div>
      
      <div>
        <div class="goods-table tab-wraper">
          <el-table
            class="fixed-height"
            ref="multipleTableTimely"
            v-loading="loading"
            :data="tableData"
            empty-text=""
            style="width:100%;"
            height="350"
            size="small"
            @select="handleSelect"
            @select-all="handleSelectAll">
            <el-table-column
              :selectable = "selectableHandel"
              type="selection"
              align="center">
            </el-table-column>
            <el-table-column min-width="216px" label="">
              <template slot-scope="scope">
                <div class="ll-p-m-w">
                  <div class="ll-img-w">
                    <img v-if="scope.row.imgUrl && scope.row.imgUrl != ''" :src="scope.row.imgUrl" alt="" onerror="onerror=null;src='/shop/static/img_error.png'">
                    <img v-else src="/shop/static/img_error.png" alt="">
                  </div>
                  <div class="ll-p-m-c-r">
                    <div>{{scope.row.goodsName || '-'}}</div>
                    <div class="ll-erp-p-no">SKU{{scope.row.goodsNo || '--'}}</div>
                    <div class="ll-erp-p-no">ID{{scope.row.platformCityGoodsId || '-'}}</div>
                  </div>
                </div>
              </template>     
            </el-table-column>
            <el-table-column min-width="110px" prop="spec" label=""></el-table-column>
            <el-table-column min-width="110px" prop="categoryName" label=""></el-table-column>
          </el-table>
        </div>

        <div class="tc mt20" v-show="tableData.length > 0">
          <el-pagination
            background
            @size-change="changeSize"
            @current-change="changeCurrent"
            :current-page.sync="pageNum"
            :page-sizes="pageSizes"
            layout="total, sizes, prev, pager, next, jumper"
            :pager-count="5"
            :total="total">
          </el-pagination>
        </div>
      </div>
      <div slot="footer" class="dialog-footer">
        <el-button @click="isShow = false" size="mini"> </el-button>
        <el-button type="primary" @click="doConfirmHandel" size="mini" :disabled="checkedList.length === 0"> </el-button>
      </div>
    </el-dialog>
  </div>
</template>

<script>
  import * as utils from '@/utils/utils'
  import config from "@/utils/config"
  import GoodsSelect from "@/components/Common/GoodsCommon/SearchByRegion"
  const requestUrl = {
    // 
    getCategoryTree: '/plateform/a/category/getCategoryTree',
    // 
    selectPageGooodsList: '/plateform/a/baseCommon/city/selectPageGoodsList',
    // -
    selectCityGoods: '/plateform/a/baseCommon/city/selectCityGoods'

  }
  export default {
    data() {
      return {
        pageSizes: config.pageSizes10,
        isShow: false,
        title: '', // title
        loading: false,
        pageNum: 1,
        pageSize: 10,
        total: 0,
        searchContent: {
          DIFFCITY: true,
          regionCode: '',
          queryString: '', // 
          platformCityGoodsIds: [], // ID
          saleType: 4, // 123:4()
          categoryIdList: [],
          platformId: 5, // 1-2-3-4-5-5-
          categoryId: '' // ID
        },
        tableData: [],
        // 
        cateDefaultProps: {
          children: 'categoryVoList',
          label: 'categoryName',
          value: 'categoryId'
        },
        categoryList: [], // id

        // 
        checkedList: [],

        // 
        scene: '',
        searchSupplierGoods: 1 // 0-1-
      }
    },
    components: {
      "goods-select-for-dialog": GoodsSelect
    },
    methods: {
      /**
       * [setDefault ]
       * @param {[type]} val [description]
       */
      setDefault(val) {
        if(val) {
          this.title = val.title
          this.checkedList = val.checkedList && val.checkedList.length ? JSON.parse(JSON.stringify(val.checkedList)) : []
          this.searchContent.storeNos = val.storeNos || []
          this.searchContent.regionCode = val.regionCode || ''
          this.scene = val.scene ? val.scene : ''
          this.searchSupplierGoods = val.searchSupplierGoods === 0 ? 0 : 1
        }

        this.isShow = true
        // 
        this.getCategoryId()

        this.doSearchHandel(true)
        // SKU
        setTimeout(() => {
          this.goodsSelectForDialogInit()
        }, 200)
      },
      /**
       * [getCategoryId ]
       * @return {[type]} [description]
       */
      getCategoryId() {
        this.$http.post(requestUrl.getCategoryTree, {DIFFCITY: true}).then(res => {
          if(res.code === 0) {
            if(res.data && res.data.length) {
              for(let item of res.data) {
                if(item.categoryVoList && item.categoryVoList.length === 0) {
                  item.categoryVoList = null
                }
              }
              this.categoryList = res.data
            }else {
              this.categoryList = []
            }
            return 
          }
          throw new Error(res.msg)
        }).catch(e => {
          console.log("():", e.message)
        })
      },
      /**
       * [changeSize description]
       * @param    {[type]}                 value [description]
       * @return   {[type]}                       [description]
       */
      changeSize (value) {
        this.pageSize = value
        this.doSearchHandel()
      },
      /**
       * [changeCurrent ]
       * @param    {[type]}                 value []
       * @return   {[type]}                       [description]
       */
      changeCurrent (value) {
        this.pageNum = value
        this.doSearchHandel()
      },
      /**
       * [doSearchHandel ]
       * @param  {[type]} _param [description]
       * @return {[type]}         [description]
       */
      doSearchHandel(_param) {
        let spliceLen = this.searchContent.categoryIdList.length - 1
        this.searchContent.categoryId = this.searchContent.categoryIdList[spliceLen]
        this.loading = true
        if(_param === true) {
          this.pageNum = 1
        }
        this.$http.post(requestUrl.selectPageGooodsList, { ...this.searchContent, pageNum: this.pageNum, pageSize: this.pageSize }).then(res => {
          this.loading = false
          if(res.code === 0) {
            // 
            if(res.data && res.data.result && res.data.result.length) {
              for(let item of res.data.result) {
                item.activityGoodsName = item.goodsName
                item.activityId = '' // id
                item.id = '' // ID
                item.activityTimes = '' // 
                item.curStock = '' // 
                item.groupCount = '' // 
                item.groupPrice = item.storeMinPrice // 
                item.limitGroupType = 0 // : 012
                item.sortNum = 50 // 
                item.limitGoodsCount = '' // 
                item.sfInStock = item.sfInStock || true
              }
              this.tableData = res.data.result
            }else {
              this.tableData = []
            }
            this.total = res.data.total
            this.defaultSelectFun()
            return 
          }
          throw new Error(res.msg)
        }).catch(e => {
          this.loading = false
          this.$notify.error({
            title: '',
            message: e.message
          })
        })
      },
      /**
       * [handleSelect ]
       * @return {[type]} [description]
       */
      handleSelect(checkedRows, row) {
        let isExist = checkedRows.some(item => {
          return item.platformCityGoodsId === row.platformCityGoodsId
        })
        if(isExist) {
          this.checkedList.push(row)
        }else {
          const index = this.checkedList.findIndex(item => {return item.platformCityGoodsId === row.platformCityGoodsId});
          this.checkedList.splice(index, 1)
        }
      },
      /**
       * [handleSelectAll ]
       * @return {[type]} [description]
       */
      handleSelectAll(arr) {
        if(arr.length) {
          // 
          this.tableData.forEach(i => {
            const index = this.checkedList.findIndex(j => {
              return j.platformCityGoodsId === i.platformCityGoodsId
            })
            if(index === -1) {
              this.checkedList.push(i)
            }
          })
        }else {
          // 
          this.tableData.forEach(i => {
            const index = this.checkedList.findIndex(j => {
              return j.platformCityGoodsId === i.platformCityGoodsId
            })
            if(index !== -1) {
              this.checkedList.splice(index, 1)
            }
          })
        }
      },
      /**
       * [defaultSelectFun ]
       * @param    {[type]}                 row [description]
       * @return   {[type]}                     [description]
       */
      defaultSelectFun() {
        let _this = this
        let rows = []
        //   
        for(let i of this.tableData) {
          for(let j of this.checkedList) {
            if(i.platformCityGoodsId == j.platformCityGoodsId) {
              i.activityGoodsName = j.activityGoodsName
              i.activityId = j.activityId // id
              i.id = j.id // ID
              i.activityTimes = j.activityTimes // 
              i.curStock = j.curStock // 
              i.groupCount = j.groupCount // 
              i.groupPrice = j.groupPrice // 
              i.limitGroupType = j.limitGroupType // : 012
              i.sortNum = j.sortNum // 
              i.limitGoodsCount = j.limitGoodsCount // 
              i.disabled = j.disabled
              i.sfInStock = j.sfInStock
              rows.push(i)
            }
          }
        }
        if(rows.length > 0) {
          this.$refs.multipleTableTimely.clearSelection()
          setTimeout(function() {
            rows.forEach(rowItem => {
              _this.$refs.multipleTableTimely.toggleRowSelection(rowItem)
            });
          }, 100)
        } else {
          this.$refs.multipleTableTimely.clearSelection()
        }
      },
      /**
       * [doConfirmHandel ]
       * @return {[type]} [description]
       */
      doConfirmHandel() {
        let _params = {
          checkedList: this.checkedList,
          scene: this.scene
        }
        this.$emit("confirmChooseGoods", _params)

        this.isShow = false
      },
      /**
       * [doCategorysChangeHandel ]
       * @return {[type]} [description]
       */
      doCategorysChangeHandel() {
        // 
        this.goodsSelectForDialogInit()

        this.doSearchHandel(true)
      },
      /**
       * [goodsSelectForDialogInit ]
       * @return {[type]} [description]
       */
      goodsSelectForDialogInit() {
        if(this.$refs.GoodsSelectForDialog) {
          let val = {
            queryString: this.searchContent.queryString,
            categoryIdList: this.searchContent.categoryIdList,
            saleType: this.searchContent.saleType,
            platformId: this.searchContent.platformId,
            platformCityGoodsIds: this.searchContent.platformCityGoodsIds,
            searchSupplierGoods: this.searchSupplierGoods
          }
          this.$refs.GoodsSelectForDialog.setDefaultMesg(val)
        }
      },
      /**
       * [DoSelectGoodsHandel ]
       */
      DoSelectGoodsHandel(_params) {
        let checkedList = _params.checkedList
        let queryString = _params.queryString
        let platformCityGoodsIds = []
        for(let item of checkedList) {
          platformCityGoodsIds.push(item.platformCityGoodsId)
        }
        this.searchContent.platformCityGoodsIds = platformCityGoodsIds
        this.searchContent.queryString = queryString
        this.doSearchHandel(true)
      },
      selectableHandel(row, index) {
        return !row.disabled
      }

    }
  }
</script>




