<style>
.p-t {
  margin-bottom: 10px;
}

.u-btn-wrap {
  display: flex;
  flex-direction: row;
  flex-wrap: nowrap;
}

.file-name-wrap {
  width: 200px;
  height: 32px;
  line-height: 32px;
  overflow: hidden;
  border: 1px solid #ececec;
  border-radius: 4px;
  padding: 0 10px;
  position: relative;
  top: 5px;
}

.label-wrap {
  border: 1px solid #ececec;
  border-radius: 4px;
  position: relative;
  top: 5px;
  height: 32px;
  line-height: 32px;
}

.label-wrap > .excel-file-label {
  line-height: 32px;
  cursor: pointer;
  padding: 7px 10px;
}

.label-wrap > .excel-file-label:hover {
  color: #fff;
  background-color: #66b1ff;
  border-radius: 4px;
}
</style>

<template>
  <div>
    <el-dialog :title="title" :visible.sync="isShow" width='450px'>
      <el-form ref="form" label-width="80px">
        <el-form-item label="">
          <div class="excel-u-d-wrap">
            <div class="u-btn-wrap">
              <div class="file-name-wrap">{{ file.name }}</div>
              <div class="label-wrap">
                <label for="excelFile" class="excel-file-label">
                  <input type="file" id="excelFile" hidden @change="handleFileSelect"/>
                  <span class="u-txt"></span>
                </label>
              </div>
            </div>
            <div class="d-btn-wrap">
              <span>EXCEL</span>
              <el-button type="text" @click="downloadModel" size="mini" :disabled="isDisabled"></el-button>
            </div>
          </div>
        </el-form-item>
      </el-form>


      <div slot="footer" class="dialog-footer">
        <el-button @click="isShow = false" size="mini"></el-button>
        <el-button type="primary" @click="doConfirmHandel" size="mini" :disabled="!file.name || conDisabled" :loading="saveLoading"></el-button>
      </div>
    </el-dialog>

  </div>
</template>

<script>
import Storage from '@/utils/localStorage'
import * as utils from '@/utils/utils'
import exportSheet from '@/utils/exportSheet'

const storePresaleGoods = {
  // 
  searchTableData: '/plateform/a/platformCityGoods/presale/platformCityGoodsList',
  // excel
  excelUpdatePrice: '/plateform/a/platformCityGoods/presale/excelUpdatePrice'
}
export default {
  data () {
    return {
      conDisabled: false, // 
      isDisabled: false, // 
      saveLoading: false,
      title: '',
      isShow: false,
      formLabelWidth: '70px',
      file: {name: ''}, // 
      // 
      exportGoods: [],
      tempConfig: {
        platformName: '()',
        platformCityGoodsId: 'ID()',
        goodsNo: 'SKU()',
        goodsName: '()',
        spec: '()',
        categoryName: '()',
        cityId: 'ID',
        cityName: '()',
        price: '()',
        newPrice: '()'
      },


      // 
      presaleList: [],
      // 1:23
      saleType: '' ,
      platformId: '',
      // 
      storeNo: ''
    }
  },
  methods: {
    /**
     * [setDefaultMesg ]
     * @param {[type]} _params []
     */
    setDefaultMesg (_params) {
      this.isShow = true
      if (_params.title) {
        this.title = _params.title
      }
      if (_params.saleType) {
        this.saleType = _params.saleType
      }
      if (_params.platformId) {
        this.platformId = _params.platformId
      }
      if (_params.storeNo) {
        this.storeNo = _params.storeNo
      }

      // 
      this.file = {name: ''}
      let excelFileDom = document.getElementById('excelFile')
      if(excelFileDom) {
        excelFileDom.value = ""
      }
    },
    /**
     * [downloadModel ]
     * @return {[type]} [description]
     */
    downloadModel () {
      this.isDisabled = true
      this.$http.post(storePresaleGoods.searchTableData, {platformId: this.platformId, saleType: this.saleType, storeNo: this.storeNo}).then(res => {
        setTimeout(() => {
          this.isDisabled = false
        }, 800)
        if(res.code === 0) {
          if(res.data && res.data.length) {
            let tableDataArr = res.data
            exportSheet.exportFileByConfig(tableDataArr, '.xlsx', this.tempConfig)
            this.$notify.success({
              title: '',
              message: ''
            })
          }else {
            this.$notify.warning({
              title: '',
              message: ''
            })
          }
          return 
        }
        throw new Error(res.msg)
      }).catch(e => {
        this.isDisabled = true
        this.$notify.error({
          title: '()',
          message: e.message
        })
      })

    },
    /**
     * [handleFileSelect ]
     * @param  {[type]} e [description]
     * @return {[type]}   [description]
     */
    handleFileSelect (e) {
      let salePriceReg = /((^[1-9]\d*)|^0)(\.\d{0,2}){0,1}$/
      // 
      if (!e.target.files.length) {
        return
      }
      this.file = e.target.files[0] || {name: ''}
      let idx = this.file.name.lastIndexOf(".")
      let strLength = this.file.name.length
      let postf = this.file.name.substring(idx, strLength) // 

      if (!this.file.name || postf != '.xlsx') {
        this.$notify.warning({
          title: '',
          message: 'excel'
        })
        // 
        this.file = {name: ''}
        document.getElementById('excelFile').value = ""
        return
      }

      let items = []
      let parseConfig = {}
      for (let key in this.tempConfig) {
        parseConfig[this.tempConfig[key]] = key
      }
      items = exportSheet.parseToJSON(this.file, parseConfig)
      items.then((result) => {
        // 
        let resultVal = result.filter(item => !!item.platformCityGoodsId)
        let presaleListArr = []
        for (let item of resultVal) {
          if (item.newPrice && item.newPrice !== "") {
            if(!item.newPrice) {
              this.$notify.warning({
                title: '',
                message: `(${item.platformCityGoodsId}_${item.goodsName})`
              })
              // 
              this.file = {name: ''}
              document.getElementById('excelFile').value = ""
              return
            }else {
              if(!salePriceReg.test(item.newPrice)) {
                this.$notify.warning({
                  title: '',
                  message: `(${item.platformCityGoodsId}_${item.goodsName})02`
                })
                // 
                this.file = {name: ''}
                document.getElementById('excelFile').value = ""
                return
              }
            }

            // 
            let presaleListItem = {
              platformCityGoodsId: item.platformCityGoodsId,
              price: item.newPrice,
              goodsNo: item.goodsNo
            }
            presaleListArr.push(presaleListItem)
          }
        }
        
        this.presaleList = presaleListArr
      }).catch()
    },
    /**
     * [doConfirmHandel ]
     * @return {[type]} [description]
     */
    doConfirmHandel() {
      this.conDisabled = true
      this.saveLoading = true
      this.$http.post(storePresaleGoods.excelUpdatePrice, {presaleList: this.presaleList}).then(res => {
        setTimeout(() => {
          this.conDisabled = false
          this.saveLoading = false
        }, 800)
        if(res.code === 0) {
          this.$message.success("")
          this.isShow = false
          this.$emit("doSearchHandel")
          return
        }
        throw new Error(res.msg)
      }).catch(e => {
        this.conDisabled = false
        this.saveLoading = false
        this.$notify.error({
          title: '',
          message: e.message
        })
      })
    } 
  }
}
</script>



