<template>
  <div>
    <el-form-item :label="label" :required="required">
      <el-select 
        :class="className" 
        v-model="regionCode" 
        filterable 
        placeholder="" 
        size="small" 
        :disabled='disabled'
        @change="regionChange">
        <el-option
          v-for="item in regionOptions"
          :key="item.storeGroupNo"
          :label="item.storeGroupName"
          :value="item.storeGroupNo">
        </el-option>
      </el-select>
    </el-form-item>
  </div>
</template>
<script>
  import Storage from '@/utils/localStorage'
  const requestUrl = {
    // 
    userAreaStoreGroupList: '/foundation/storeGroup/userAreaStoreGroupList'
  }
  export default {
    props: {
      label: {
        type: String,
        default: ''
      },
      className: {
        type: String,
        default: 'search-input'
      },
      //  true 
      required: {
        type: Boolean,
        default: false
      },
      //  false, 
      refresh: {
        type: Boolean,
        default: true
      },
      disabled: {
        type: Boolean,
        default: false
      }
    },
    data() {
      return {
        // 
        regionOptions: [],
        regionCode: ''
      }
    },
    created() {
      // 
      this.getUserAreaList()
    },
    methods: {
      /**
       * @description: 
       * @param {type} 
       * @return: 
       */
      getUserAreaList() {
        this.$http.get(requestUrl.userAreaStoreGroupList).then(res => {
          if(res.code === 0) {
            this.regionOptions = res.data && res.data.length ? res.data : []
            if(Storage.get('regionCode')) {
              let hasRegionOpt = this.regionOptions.some(item => {
                return item.storeGroupNo === Storage.get('regionCode')
              })
              if(hasRegionOpt) {
                this.regionCode = Storage.get('regionCode')
              }else {
                this.regionCode = this.regionOptions[0] && this.regionOptions[0].storeGroupNo ? this.regionOptions[0].storeGroupNo : ''
              }
            }else {
              this.regionCode = this.regionOptions[0] && this.regionOptions[0].storeGroupNo ? this.regionOptions[0].storeGroupNo : ''
              Storage.set('regionCode', this.regionCode)
            }
            return 
          }
          throw new Error(res.msg)
        }).catch(e => {
          console.log("" + e.message)
        })
      },
     
      /**
       * @description: 
       * @param {type} 
       * @return: 
       */
      regionChange(val) {
        Storage.set('regionCode', val)
        let params = {
          regionCode: this.regionCode
        }
        this.$emit("RegionChange", params)
        if(this.refresh) this.$store.dispatch("common/REGION_CHANGE")
      }

    }
  }
</script>


