<style scoped>
  .store-container {
    width: 100%;
  }
  .l-table, .r-choose {
    padding: 10px;
    background-color: #fff;
    border: 1px solid #ececec;
  }
  .r-choose {
    padding-bottom: 0;
  }
  
  .l-t-top, .r-c-top {
    border-bottom: 1px solid #ececec;
  }
  .r-c-top {
    height: 41px;
    line-height: 41px;
  }
  .l-t-bottom {
    height: 370px;
  }
  .r-c-bottom {
    overflow-y: auto;
    height: 380px;
  }
  .r-c-bottom>ul {
    list-style: none;
  }
  .r-c-bottom>ul>li {
    height: 40px;
    border-bottom: 1px solid #ececec;
    line-height: 40px;
    display: flex;
    flex-direction: row;
    flex-wrap:nowrap;
  }
  .r-c-bottom>ul>li>span {
    display: inline-block;
  }
  .r-c-bottom>ul>li>span:nth-child(1) {
    padding: 0 10px;
    flex: 1;
    overflow: hidden;
  }
  .r-c-bottom>ul>li {
    padding-right: 10px;
  }
  .r-c-bottom>ul>li>span:nth-child(2) {
    width: 18px;
    height: 18px;
    border-radius: 50%;
    background-color: #ececec;
    margin-top: 11px;
    cursor: pointer;
    line-height: 18px;
    text-align: center;
  }
  .r-c-bottom>ul>li>span:nth-child(2)>i {
    font-size: 12px;
  }
  .r-c-bottom>ul>li>span:nth-child(2):hover {
    background-color: #409EFF;
    color: #fff;
  }
</style>
<template>
  <el-row class="store-container">
    <el-col :span="showGroup || showSaleType ? 14 : 12">
      <div class="l-table">
        <div class="l-t-top">
          <el-select 
            class="search-input-small-width" 
            v-model="cityId" 
            filterable 
            placeholder="" 
            clearable 
            size="mini"
            @change="doSearchHandel">
            <el-option
              v-for="item in cityOptions"
              :key="item.cityId"
              :label="item.cityName"
              :value="item.cityId">
            </el-option>
          </el-select>
          <el-cascader
            v-if="showGroup"
            ref="cascaderCategory"
            placeholder=""
            class="search-input-small-width"
            :options="storeGroupTypeNoOptions"
            size="mini"
            filterable
            clearable
            :props="defaultProps"
            @change="doSearchHandel"
            v-model="storeGroupTypeNoList">
          </el-cascader>
          <el-select 
            v-if="showSaleType"
            class="search-input-small-width" 
            v-model="saleType" 
            filterable 
            placeholder="" 
            clearable 
            size="mini"
            @change="doSearchHandel">
            <el-option
              v-for="item in saleTypeOptions"
              :key="item.value"
              :label="item.label"
              :value="item.value">
            </el-option>
          </el-select>
          <el-input
            placeholder="ERP/"
            size="mini"
            clearable
            v-model="searchValue"
            class="search-input-small-width"
            @keyup.enter.native="doSearchHandel">
          </el-input>
        </div>
        <div class="l-t-bottom">
          <el-table
            class="fixed-height"
            ref="multiple"
            v-loading="loading"
            :data="tableData"
            @select="handleSelect"
            @select-all="handleSelectAll"
            empty-text=""
            height="372"
            size="small"
            :header-cell-style="headerStyle">
            <el-table-column prop="tempStoreName" label=""></el-table-column>
            <el-table-column width="100" prop="cityName" label=""></el-table-column>
            <el-table-column width="100" prop="saleTypeName" label="" v-if="showSaleType"></el-table-column>
            <el-table-column
              type="selection"
              align="center">
            </el-table-column>
          </el-table>
        </div>
      </div>
    </el-col>
    <el-col :span="showGroup ? 10 : 12">
      <div class="r-choose">
        <div class="r-c-top">
          <el-row>
            <el-col :span="16">&nbsp;<b>{{checkedList.length}}</b>&nbsp;</el-col>
            <el-col :span="8" style="text-align:right;"><el-button type="text" size="mini" @click="clearChooseData"></el-button></el-col>
          </el-row>
        </div>
        <div class="r-c-bottom">
          <ul>
            <li v-for="(item, idx) of checkedList" :key="idx">
              <span>{{item.tempStoreName}}</span>
              <span @click="deleteChooseItem(item)"><i class="el-icon-close"></i></span>
            </li>
          </ul>
        </div>  
      </div>
    </el-col>
  </el-row>
</template>
<script>
  const requestUrl = {
    // 
    getStoreGroupType: '/backstage/storeGroup/storeGroupTypeList',
    // 
    getCityList: '/foundation/store/storeCityList/'
  }
  export default {
    props: {
      // 
      getStoreListUrl: {
        type: String,
        default: '/plateform/a/platformCityGoods/presale/store/list'
      },
      regionCode: {
        type: String,
        default: ''
      },
      platformId: {
        default: ''
      },
      showGroup: {
        type: Boolean,
        default: false
      },
      showSaleType: {
        type: Boolean,
        default: false
      }
    },
    data() {
      return {
        headerStyle: {
          "padding": '0 !important'
        },
        loading: false,
        tableData: [],
        checkedList: [],
        storeGroupTypeNoList: [],
        storeGroupTypeNoOptions: [],
        defaultProps: {
          children: 'storeGroupList',
          label: 'labelName',
          value: 'valueName'
        },
        searchValue: '',
        cityId: '', // id
        cityOptions: [], // 
        saleType: '',
        saleTypeOptions: [
          {
            label: '',
            value: 1
          },
          {
            label: '',
            value: 2
          },
          {
            label: '',
            value: 3
          },
          {
            label: '',
            value: 4
          },
          {
            label: '',
            value: 5
          },
          {
            label: '',
            value: 6
          }
        ]
      }
    },
    watch: {
      checkedList(newVal) {
        this.emitMsg(newVal)
      },
      regionCode(newVal) {
        this.checkedList = []
        this.getCityList(newVal)
        this.doSearchHandel()
      }
    },
    created() {
      this.getCityList(this.regionCode)
      if(this.showGroup) this.getStoreGroupType()
      this.doSearchHandel()
    },
    methods: {
      /**
       * @description: 
       * @param {type} 
       * @return: 
       */
      getCityList(regionCode) {
        this.$http.post(`${requestUrl.getCityList}${regionCode}`, {DIFFCITY: true}).then(res => {
          if(res.code === 0) {
            this.cityOptions = res.data && res.data.length ? res.data : []
            return 
          }
          throw new Error(res.msg)
        }).catch(e => {
          console.log("" + e.message)
        })
      },
      /**
       * @description: 
       * @param {type} 
       * @return: 
       */
       getStoreGroupType() {
        this.$http.get(requestUrl.getStoreGroupType).then(res => {
          if(res.code === 0) {
            let dataList = res.data && res.data.length ? res.data: []
            this.modifyStoreGroupeTree(dataList)
            this.storeGroupTypeNoOptions = dataList
            return 
          }
          throw new Error(res.msg)
        }).catch(e => {
          console.log("" + e.message)
        })
      },
      modifyStoreGroupeTree(storeTree, typeNo) {
        storeTree.forEach(v => {
          if(v.storeGroupList && v.storeGroupTypeNo) {
            // 
            v.valueName = v.storeGroupTypeNo
            v.labelName = v.storeGroupTypeName ? v.storeGroupTypeNo + '.' + v.storeGroupTypeName : ''
            if(v.storeGroupList.length) {
              this.modifyStoreGroupeTree(v.storeGroupList, v.storeGroupTypeNo)
            }
          }else if(v.storeGroupNo) {
            // 
            v.valueName = v.storeGroupNo
            v.labelName = v.storeGroupName ? v.storeGroupNo + '.' + v.storeGroupName : ''
            if(v.childrenList && v.childrenList.length) {
              v.storeGroupList = JSON.parse(JSON.stringify(v.childrenList))
              this.modifyStoreGroupeTree(v.storeGroupList)
            }
          }
        })
      },
      /**
       * [doSearchHandel ]
       * @return {[type]} [description]
       */
       doSearchHandel() {
        // console.log("this.regionCode:", this.regionCode)
        this.loading = true
        let params = {
          cityId: this.cityId || '',
          key: this.searchValue || '',
          searchValue: this.searchValue,
          platformId: this.platformId || '',
          platform: this.platformId || '',
          regionCode: this.regionCode || '',
          storeGroupTypeNo: this.storeGroupTypeNoList && this.storeGroupTypeNoList[0] ? this.storeGroupTypeNoList[0] : '',
          storeGroupNo: this.storeGroupTypeNoList && this.storeGroupTypeNoList[1] ? this.storeGroupTypeNoList[1] : '',
          saleType: this.saleType || '',
          DIFFCITY: true
        }
        this.$http.post(this.getStoreListUrl, {...params}).then(res => {
          this.loading = false
          if(res.code === 0) {
            this.tableData = res.data && res.data.length ? res.data : []
            this.tableData.forEach(item => {
              item.storeNo = item.storeNo || item.erpStoreNo
              item.storeName = item.storeName || item.erpStoreName
              item.tempStoreName = `${item.storeNo}_${item.storeName}`
              item.saleTypeName = item.saleTypeName || item.saleTypeStr
            })
            this.defaultSelectFun()
            return
          }
          throw new Error(res.msg)
        }).catch(e => {
          this.loading = false
          this.$notify.error({
            title: '',
            message: e.message
          })
        })
      },
      /**
       * [handleSelect     ]
       * @return {[type]} [description]
       */
      handleSelect(checkedRows, row) {
        if(checkedRows.includes(row)) {
          this.checkedList.push(row)
        }else {
          const index = this.checkedList.findIndex(item => {
            return item.storeNo === row.storeNo
          })
          this.checkedList.splice(index, 1)
        }
      },
      /**
       * [handleSelectAll     ]
       * @return {[type]} [description]
       */
      handleSelectAll(val) {
        this.checkedList = [...val]
      },
      /**
       * [deleteChooseItem ]
       * @return {[type]} [description]
       */
      deleteChooseItem(row) {
        const index = this.checkedList.findIndex(item => {
          return item.storeNo === row.storeNo
        })
        this.checkedList.splice(index, 1)
        // 
        this.defaultSelectFun()
      },
      /**
       * [clearChooseData ]
       * @return {[type]} [description]
       */
      clearChooseData() {
        this.checkedList = []
        this.handleSelectAll(this.checkedList)
        // 
        this.defaultSelectFun()
      },
      /**
       * [defaultSelectFun ]
       * @param    {[type]}                 row [description]
       * @return   {[type]}                     [description]
       */
       defaultSelectFun() {
        let _this = this
        let rows = []
        for(let i of this.tableData) {
          for(let j of this.checkedList) {
            if(i.storeNo == j.storeNo) {
              rows.push(i)
            }
          }
        }
        if (rows.length > 0) {
          if(_this.$refs.multiple) _this.$refs.multiple.clearSelection()
          setTimeout(function() {
            rows.forEach(rowItem => {
              _this.$refs.multiple.toggleRowSelection(rowItem)
            });
          }, 100)
        } else {
          if(_this.$refs.multiple) _this.$refs.multiple.clearSelection()
        }
      },
      /**
       * @description: 
       * @param {type} 
       * @return: 
       */
      emitMsg(params) {
        this.$emit("setCheckedData", params)
      }

    }
  }
</script>


