
<style scoped>
@import "../../../assets/choose_store_dialog.css";
</style>
<template>
  <div>
    <el-dialog 
      :title="title" 
      :visible.sync="isShow" 
      :close-on-click-modal = "false"
      :close-on-press-escape = "false"
      width='940px'>
      <el-form ref="form" @submit.native.prevent>
        <el-form-item class="form-item-wrap">
          <el-row class="store-container">
            <el-col :span="14">
              <div class="l-table">
                <div class="l-t-top">
                  <el-select 
                    class="search-input-small-width" 
                    v-model="cityId" 
                    filterable 
                    placeholder="" 
                    clearable 
                    size="mini"
                    @change="doSearchHandel">
                    <el-option
                      v-for="item in cityOptions"
                      :key="item.cityId"
                      :label="item.cityName"
                      :value="item.cityId">
                    </el-option>
                  </el-select>
                  <el-cascader
                    ref="cascaderCategory"
                    placeholder=""
                    class="search-input-small-width"
                    :options="storeGroupTypeNoOptions"
                    size="mini"
                    filterable
                    clearable
                    :props="defaultProps"
                    @change="doSearchHandel"
                    v-model="storeGroupTypeNoList">
                  </el-cascader>
                  <el-select 
                    class="search-input-small-width" 
                    v-model="saleType" 
                    filterable 
                    placeholder="" 
                    clearable 
                    size="mini"
                    @change="doSearchHandel">
                    <el-option
                      v-for="item in saleTypeOptions"
                      :key="item.value"
                      :label="item.label"
                      :value="item.value">
                    </el-option>
                  </el-select>
                  <el-input
                    placeholder="ERP/"
                    size="mini"
                    clearable
                    v-model="searchVal"
                    class="search-input-small-width"
                    @keyup.enter.native="doSearchHandel">
                  </el-input>
                </div>
                <div class="l-t-bottom">
                  <el-table
                    class="fixed-height"
                    ref="multiple"
                    v-loading="loading"
                    :data="tableData"
                    @select="handleSelect"
                    @select-all="handleSelectAll"
                    empty-text=""
                    height="372"
                    size="small"
                    :header-cell-style="headerStyle">
                    <el-table-column prop="tempStoreName" label=""></el-table-column>
                    <el-table-column width="100" prop="cityName" label=""></el-table-column>
                    <el-table-column width="100" prop="saleTypeName" label=""></el-table-column>
                    <el-table-column
                      type="selection"
                      align="center">
                    </el-table-column>
                  </el-table>
                </div>
              </div>
            </el-col>
            <el-col :span="10">
              <div class="r-choose">
                <div class="r-c-top">
                  <el-row>
                    <el-col :span="16">&nbsp;<b>{{checkedList.length}}</b>&nbsp;</el-col>
                    <el-col :span="8" style="text-align:right;"><el-button type="text" size="mini" @click="clearChooseData"></el-button></el-col>
                  </el-row>
                </div>
                <div class="r-c-bottom" v-loading="loading">
                  <ul>
                    <li v-for="(item, idx) of checkedList" :key="idx">
                      <span>{{item.storeNo}}_{{item.storeName}}_{{item.cityName}}_{{item.saleTypeName}}</span>
                      <span @click="deleteChooseItem(item)"><i class="el-icon-close"></i></span>
                    </li>
                  </ul>
                </div>  
              </div>
            </el-col>
          </el-row>
        </el-form-item>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button @click="isShow = false" size="mini"> </el-button>
        <el-button type="primary" @click="confirmClose" size="mini" :disabled="checkedList.length === 0"> </el-button>
      </div>
    </el-dialog>
  </div>
</template>

<script>
  import * as utils from '@/utils/utils'
  const requestUrl = {
    // 
    getStoreGroupType: '/backstage/storeGroup/storeGroupTypeList',
    // 
    getCityList: '/foundation/store/storeCityList/',
    getStoreListUrl: '/plateform/a/baseCommon/store/selectPlatfromStore'
  }
  export default {
    data() {
      return {
        headerStyle: {
          "padding": '0 !important'
        },
        isShow: false,
        title: '', // title
        platformId: '', // id
        regionCode: '', // id
        storeGroupTypeNoList: [],
        storeGroupTypeNoOptions: [],
        loading: false,
        defaultProps: {
          children: 'storeGroupList',
          label: 'labelName',
          value: 'valueName'
        },
        searchVal: '',
        cityId: '', // id
        cityOptions: [], // 
        saleType: '',
        saleTypeOptions: [
          {
            label: '',
            value: 1
          },
          {
            label: '',
            value: 2
          },
          {
            label: '',
            value: 3
          },
          {
            label: '',
            value: 4
          },
          {
            label: '',
            value: 5
          },
          {
            label: '',
            value: 6
          }
        ],
        // 
        checkedList: [],
        otherCheckedList: [],
        groupIndex: 0,
        tableData: []
      }
    },
    methods: {
      /**
       * [setDefault ]
       * @param {[type]} val [description]
       */
      setDefault(val) {
        if(val) {
          this.platformId = val.platformId
          this.regionCode = val.regionCode
          this.title = val.title
          this.checkedList = JSON.parse(JSON.stringify(val.checkedList)) 
          if(val.otherCheckedList && val.otherCheckedList.length) {
            this.otherCheckedList = [...val.otherCheckedList]
          }
          this.groupIndex = val.groupIndex || 0

          if(this.checkedList && this.checkedList.length) {
            this.checkedList.forEach(item => {
              item.storeNo = item.storeNo || item.erpStoreNo
              item.storeName = item.storeName || item.erpStoreName
              item.tempStoreName = `${item.storeNo}_${item.storeName}`
            })
          }
          if(this.otherCheckedList && this.otherCheckedList.length) {
            this.otherCheckedList.forEach(item => {
              item.storeNo = item.storeNo || item.erpStoreNo
              item.storeName = item.storeName || item.erpStoreName
              item.tempStoreName = `${item.storeNo}_${item.storeName}`
            })
          }
        }

        this.isShow = true
        this.getCityList(this.regionCode)
        this.getStoreGroupType()
        this.doSearchHandel()
      },
      /**
       * @description: 
       * @param {type} 
       * @return: 
       */
       getCityList(regionCode) {
        this.$http.post(`${requestUrl.getCityList}${regionCode}`, {DIFFCITY: true}).then(res => {
          if(res.code === 0) {
            this.cityOptions = res.data && res.data.length ? res.data : []
            return 
          }
          throw new Error(res.msg)
        }).catch(e => {
          console.log("" + e.message)
        })
      },
      /**
       * @description: 
       * @param {type} 
       * @return: 
       */
       getStoreGroupType() {
        this.$http.get(requestUrl.getStoreGroupType).then(res => {
          if(res.code === 0) {
            let dataList = res.data && res.data.length ? res.data: []
            this.modifyStoreGroupeTree(dataList)
            this.storeGroupTypeNoOptions = dataList
            return 
          }
          throw new Error(res.msg)
        }).catch(e => {
          console.log("" + e.message)
        })
      },
      modifyStoreGroupeTree(storeTree, typeNo) {
        storeTree.forEach(v => {
          if(v.storeGroupList && v.storeGroupTypeNo) {
            // 
            v.valueName = v.storeGroupTypeNo
            v.labelName = v.storeGroupTypeName ? v.storeGroupTypeNo + '.' + v.storeGroupTypeName : ''
            if(v.storeGroupList.length) {
              this.modifyStoreGroupeTree(v.storeGroupList, v.storeGroupTypeNo)
            }
          }else if(v.storeGroupNo) {
            // 
            v.valueName = v.storeGroupNo
            v.labelName = v.storeGroupName ? v.storeGroupNo + '.' + v.storeGroupName : ''
            if(v.childrenList && v.childrenList.length) {
              v.storeGroupList = JSON.parse(JSON.stringify(v.childrenList))
              this.modifyStoreGroupeTree(v.storeGroupList)
            }
          }
        })
      },
      /**
       * [doSearchHandel ]
       * @return {[type]} [description]
       */
      doSearchHandel() {
        this.loading = true
        let params = {
          cityId: this.cityId || '',
          searchValue: this.searchVal || '',
          platformId: this.platformId || '',
          platform: this.platformId || '',
          regionCode: this.regionCode || '',
          saleType: this.saleType || '',
          storeGroupTypeNo: this.storeGroupTypeNoList && this.storeGroupTypeNoList[0] ? this.storeGroupTypeNoList[0] : '',
          storeGroupNo: this.storeGroupTypeNoList && this.storeGroupTypeNoList[1] ? this.storeGroupTypeNoList[1] : '',
          DIFFCITY: true
        }
        this.$http.post(requestUrl.getStoreListUrl, {...params}).then(res => {
          this.loading = false
          if(res.code === 0) {
            let tableDataTemp = res.data && res.data.length ? res.data : []
            tableDataTemp.forEach(item => {
              item.storeNo = item.storeNo || item.erpStoreNo
              item.storeName = item.storeName || item.erpStoreName
              item.tempStoreName = `${item.storeNo}_${item.storeName}`
            })
            this.tableData = utils.getSubtract(tableDataTemp, this.otherCheckedList, 'storeNo')
            this.defaultSelectFun()
            return
          }
          throw new Error(res.msg)
        }).catch(e => {
          this.loading = false
          this.$notify.error({
            title: '',
            message: e.message
          })
        })
      },
      /**
       * [handleSelect     ]
       * @return {[type]} [description]
       */
      handleSelect(checkedRows, row) {
        if(checkedRows.includes(row)) {
          this.checkedList.push(row)
        }else {
          const index = this.checkedList.findIndex(item => {
            return item.storeNo === row.storeNo
          })
          this.checkedList.splice(index, 1)
        }
      },
      /**
       * [handleSelectAll     ]
       * @return {[type]} [description]
       */
      handleSelectAll(val) {
        this.checkedList = [...val]
      },
      /**
       * [deleteChooseItem ]
       * @return {[type]} [description]
       */
      deleteChooseItem(row) {
        const index = this.checkedList.findIndex(item => {
          return item.storeNo === row.storeNo
        })
        this.checkedList.splice(index, 1)
        // 
        this.defaultSelectFun()
      },
      /**
       * [clearChooseData ]
       * @return {[type]} [description]
       */
      clearChooseData() {
        this.checkedList = []
        // 
        this.defaultSelectFun()
      },
      /**
       * [defaultSelectFun ]
       * @param    {[type]}                 row [description]
       * @return   {[type]}                     [description]
       */
       defaultSelectFun() {
        let _this = this
        let rows = []
        for(let i of this.tableData) {
          for(let j of this.checkedList) {
            if(i.storeNo == j.storeNo) {
              rows.push(i)
            }
          }
        }
        if (rows.length > 0) {
          if(_this.$refs.multiple) _this.$refs.multiple.clearSelection()
          setTimeout(function() {
            rows.forEach(rowItem => {
              _this.$refs.multiple.toggleRowSelection(rowItem)
            });
          }, 100)
        } else {
          if(_this.$refs.multiple) _this.$refs.multiple.clearSelection()
        }
      },
      /**
       * [confirmClose ]
       * @return {[type]} [description]
       */
      confirmClose() {
        let _params = {
          checkedList: this.checkedList,
          groupIndex: this.groupIndex
        }
        this.$emit("confirmChooseStore", _params)
        this.isShow = false
      },

      /**
       * @description: 
       * @param {type} 
       * @return: 
       */
      setCheckedData(checkedList) {
        this.checkedList = checkedList || []
      }

    }
  }
</script>



