<style>
  .show-choose{
    color: #409EFF;
    cursor: pointer;
  }
  .upload{
    display: inline-block;
    line-height: 1;
    white-space: nowrap;
    cursor: pointer;
    border: 1px solid #dcdfe6;
    color: #fff;
    -webkit-appearance: none;
    text-align: center;
    box-sizing: border-box;
    outline: 0;
    margin: 0;
    padding: 10px 15px;
    font-weight: 500;
    border-radius: 3px;
    background: #2077FF;
  }
</style>
<template>
  <div>
    <!--  -->
    <div>
      <el-form-item label="">
        <el-select size="small" class="add-input" v-model.trim="addOnline.chooseOnlineType" placeholder="" @change='clearType'>
          <el-option :label="item.label" :value="item.value" :key="item.value" v-for="item in chooseOnlineTypeList"></el-option>
        </el-select>
      </el-form-item>

      <el-form-item label="" v-if="addOnline.chooseOnlineType === 1">
        <el-button size="small" @click="chooseOnlineGoodsOpen" icon="el-icon-plus"></el-button>
        <span class="show-choose" v-if="addOnline.onlineGoodsCheckedList.length && addOnline.chooseOnlineGoodsTOF" @click="showOnlineGoodsChecked"></span>
        <span class="show-choose" v-else-if="addOnline.onlineGoodsCheckedList.length && !addOnline.chooseOnlineGoodsTOF" @click="showOnlineGoodsChecked"></span>
      </el-form-item>

      <el-form-item label="" v-if="addOnline.chooseOnlineType === 2">
        <el-button size="small" @click="chooseCateOpen" icon="el-icon-plus"></el-button>
        <span class="show-choose" v-if="addOnline.onlineCategoryCheckedList.length && addOnline.chooseNCateTOF" @click="showOnlineCategoryChecked"></span>
        <span class="show-choose" v-else-if="addOnline.onlineCategoryCheckedList.length && !addOnline.chooseNCateTOF" @click="showOnlineCategoryChecked"></span>
      </el-form-item>

      <el-form-item label="" v-if="addOnline.chooseOnlineType === 3">
        <el-button size="small" type="info" @click="downloadModel"></el-button>
        <label for="excelFileN">
          <span class="upload"></span>
          <input type="file" id="excelFileN" hidden @change="handleFileSelectN" @click="chooseFileN" v-if="fileN.isShow"/>
          <p style="display: inline-block;">{{ addOnline.fileName }}</p>
        </label>
        <span v-if="addOnline.uploadNGoodsNo.length" class="show-choose" @click="showOnlineGoodsUpload"></span>
      </el-form-item>
      
      <el-form-item label="">
        <el-checkbox-group v-model="addOnline.onlineActivityTypeList" @change='changeOnline'>
          <el-checkbox label="21"></el-checkbox>
          <el-checkbox label="11"></el-checkbox>
          <el-checkbox label="31"></el-checkbox>
          <el-checkbox label="41"></el-checkbox>
          <el-checkbox label="51"></el-checkbox>
          <el-checkbox label="61"></el-checkbox>
        </el-checkbox-group>
        <span></span>
      </el-form-item>
    </div>
    
    <!--  -->
    <div>
      <choose-online-goods ref="chooseOnlineGoods" @confirmChooseOnlineGoods="confirmChooseOnlineGoods"></choose-online-goods>
    </div>
    <!--  -->
    <div>
      <choose-category ref="chooseCate" @confirmChooseCate="confirmChooseCate"></choose-category>
    </div>
  </div>
</template>
<script>
  import * as utils from '@/utils/utils'
  import ChooseOnlineGoods from "../Dialog/ChooseOnlineGoods"
  import ChooseCategory from "../Dialog/ChooseCategory"
  import exportSheet from '@/utils/exportSheet'
  const reqUrl = {
    // 
    productList: '/foundation/goods/info/query',
    // 
    selectPageGooods: '/plateform/a/baseCommon/goods/selectPageGooods'
  }
  export default {
    data() {
      return {
        // 
        chooseOnlineTypeList: [
          {
            value: 1,
            label: ''
          },
          {
            value: 2,
            label: ''
          },
          {
            value: 3,
            label: ''
          }
        ],
        addOnline: {
          promType: '',
          chooseOnlineType: 1, //  --
          chooseOnlineGoodsTOF: true, // 
          onlineGoodsCheckedList: [], // 
          chooseNCateTOF: true, // 
          onlineCategoryCheckedList: [], // 
          uploadNGoodsNo: [], // 
          onlineActivityTypeList: [], // 
          fileName: ''
        },
        fileN: {name: '', isShow: true}, // 
        tempConfigN: {
          GoodsNO: 'ID',
          GoodsName: ''
        },
        initOnline: {
          promType: '',
          chooseOnlineType: 1, //  --
          chooseOnlineGoodsTOF: true, // 
          onlineGoodsCheckedList: [], // 
          chooseNCateTOF: true, // 
          onlineCategoryCheckedList: [], // 
          uploadNGoodsNo: [], // 
          onlineActivityTypeList: [] // 
        }
      }
    },
    created() {

    },
    mounted() {
      
    },
    components: {
      "choose-online-goods": ChooseOnlineGoods,
      "choose-category": ChooseCategory
    },
    methods: {
      promTypeChange(promType, promReqs) {
        if(promType === '2') {
          this.chooseOnlineTypeList = [
            {
              value: 1,
              label: ''
            },
            {
              value: 2,
              label: ''
            },
            {
              value: 3,
              label: ''
            }
          ]
        }else if(promType === '5') {
          this.chooseOnlineTypeList = [
            {
              value: 1,
              label: ''
            },
            {
              value: 3,
              label: ''
            }
          ]
        }
        this.addOnline = {...this.initOnline, ...promReqs}
      },

      /**
       * [chooseOnlineGoodsOpen ]
       * @return {[type]} [description]
       */
      chooseOnlineGoodsOpen() {
        // 
        if(this.addOnline.promType === '5') {
          let _params = {
            title: '',
            checkedList: this.addOnline.onlineGoodsCheckedList
          }
          this.$refs.chooseOnlineGoods.setDefault(_params, true, 'noExclude')
        }else{
          let _params = {
            title: '',
            chooseOnlineGoodsTOF: this.addOnline.chooseOnlineGoodsTOF,
            checkedList: this.addOnline.onlineGoodsCheckedList
          }
          this.$refs.chooseOnlineGoods.setDefault(_params, true)
        }
      },
      /**
       * [chooseCateOpen ]
       * @return {[type]} [description]
       */
      chooseCateOpen(_val) {
        // 
        let _params = {
          title: '',
          chooseNCateTOF: this.addOnline.chooseNCateTOF,
          checkedList: this.addOnline.onlineCategoryCheckedList
        }
        this.$refs.chooseCate.setDefault(_params, true)
      },
      /**
       * [confirmChooseOnlineGoods ]
       * @param  {[type]} _value [description]
       * @return {[type]}        [description]
       */
      confirmChooseOnlineGoods(_value) {
        if(_value.chooseOnlineGoodsTOF !== undefined) {
          this.addOnline.chooseOnlineGoodsTOF = _value.chooseOnlineGoodsTOF
        }
        if(_value.checkedList && _value.checkedList.length) {
          this.addOnline.onlineGoodsCheckedList = _value.checkedList
        }else {
          this.addOnline.onlineGoodsCheckedList = []
        }
        this.changeOnline()
      },
      /**
       * [confirmChooseCate ]
       * @param  {[type]} _value [description]
       * @return {[type]}        [description]
       */
      confirmChooseCate(_value) {
        if(_value.checkedOnlineList && _value.checkedOnlineList.length) {
          this.addOnline.chooseNCateTOF = _value.chooseNCateTOF
          this.addOnline.onlineCategoryCheckedList = _value.checkedOnlineList
        }else {
          this.addOnline.onlineCategoryCheckedList = []
        }
        this.changeOnline()
      },
      /**
       * [showOnlineGoodsChecked ]
       */
      showOnlineGoodsChecked() {
        let title = ''
        if(this.addOnline.chooseOnlineGoodsTOF) {
          title = ''
        }else{
          title = ''
        }
        let _params = {
          title: title,
          checkedList: this.addOnline.onlineGoodsCheckedList
        }
        this.$refs.chooseOnlineGoods.setDefault(_params, 'show')
      },
      /**
       * [showOnlineCategoryChecked ]
       */
      showOnlineCategoryChecked() {
        let title = ''
        if(this.addOnline.chooseNCateTOF) {
          title = ''
        }else{
          title = ''
        }
        let _params = {
          title: title,
          checkedList: this.addOnline.onlineCategoryCheckedList
        }
        this.$refs.chooseCate.setDefault(_params, 'show')
      },
      /**
       * [showOnlineGoodsUpload ]
       */
      showOnlineGoodsUpload() {
        let _params = {
          title: '',
          checkedList: this.addOnline.uploadNGoodsNo
        }
        this.$refs.chooseOnlineGoods.setDefault(_params, 'show')
      },
      /**
       * [downloadModel ]
       * @return {[type]} [description]
       */
      downloadModel(_params) {
        try {
          exportSheet.exportFile([{ GoodsNO: '', GoodsName: '' }], '.xlsx', this.tempConfigN)
          this.$notify.success({
            title: '',
            message: ''
          })
        } catch (e) {
          this.$notify.error({
            title: '',
            message: ''
          })
        }
      },
      // 
      chooseFileN() {
        this.fileN = {
          name: '',
          isShow: false
        }
        this.addOnline.fileName = ''
        this.addOnline.uploadNGoodsNo = []
        setTimeout(() => {
          this.$set(this.fileN, 'isShow', true)
        }, 100)
        this.changeOnline()
      },

      /**
       * [handleFileSelectN --]
       * @param  {[type]} e [description]
       * @return {[type]}   [description]
       */
      handleFileSelectN(e) {
        // 
        if(!e.target.files.length) {
          return
        }
        this.fileN = e.target.files[0] || {name: ''}
        this.addOnline.fileName = this.fileN.name
        this.$set(this.fileN, 'isShow', true)
        let idx = this.fileN.name.lastIndexOf(".")
        let strLength = this.fileN.name.length
        let postf = this.fileN.name.substring(idx, strLength) // 

        if (!this.fileN.name || postf != '.xlsx') {
          this.$notify.warning({
            title: '',
            message: 'excel'
          })
          return
        }

        let items = []
        let parseConfig = {}
        for (let key in this.tempConfigN) {
          parseConfig[this.tempConfigN[key]] = key
        }
        items = exportSheet.parseToJSON(this.fileN, parseConfig)
        items.then((result) => {
          // 
          let resultVal = result.filter(item => !!item.GoodsNO)

          if(resultVal.length <= 0) {
            this.$notify.warning({
              title: '',
              message: ''
            })
            return
          }
          let goodsNoArr = []
          let goodsNameArr = []
          // 
          for(let i of resultVal) {
            goodsNoArr.push(i.GoodsNO)
            goodsNameArr.push(i.GoodsName)
          }
          //
          this.$http.post(reqUrl.selectPageGooods, {goodsIds: goodsNoArr, pageNum: 1, pageSize: goodsNoArr.length, byCityId: true}).then(res => {
            if(res.code === 0) {
              if(res.data && res.data.result && res.data.result.length) {
                this.addOnline.uploadNGoodsNo = res.data.result
              }else {
                this.addOnline.uploadNGoodsNo = []
                this.$notify.error({
                  title: '',
                  message: ''
                })
              }
              this.changeOnline()
              return
            }
            throw new Error(res.msg)
          }).catch(e => {
            this.$notify.error({
              title: '',
              message: e.message
            })
          })
        })
      },
      changeOnline() {
        // 
        this.$emit("changeOnline", this.addOnline)
      },
      clearType(val) {
        if(val === 1) {
          // 
          this.addOnline.chooseNCateTOF = true
          this.addOnline.onlineCategoryCheckedList = []
          this.addOnline.uploadNGoodsNo = []
          this.fileN = {name: '', isShow: true}
          this.addOnline.fileName = ''
        }else if(val === 2) {
          // 
          this.addOnline.chooseOnlineGoodsTOF = true
          this.addOnline.onlineGoodsCheckedList = []
          this.addOnline.uploadNGoodsNo = []
          this.fileN = {name: '', isShow: true}
          this.addOnline.fileName = ''
        }else if(val === 3) {
          // 
          this.addOnline.chooseOnlineGoodsTOF = true
          this.addOnline.onlineGoodsCheckedList = []
          this.addOnline.chooseNCateTOF = true
          this.addOnline.onlineCategoryCheckedList = []
        }
        this.changeOnline()
      }
    }
  }

</script>


